var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var EventListener = require("../../Code/EventListener");

var ImageFace = function(vatomView,face)
{
    Face.call(this, vatomView,face);


    if(!vatomView||!face)return;

    //console.log(this.face.properties.resources);
    if(this.face.properties.resources.length==0)
    {
        console.log("no resources");
        this.face.properties.resources.push("ActivatedImage");
    }
    this.image = this.vatom.resources[this.face.properties.resources[0]];
    console.log("created image face");
    this.onResourceChanged = function (resource) {

        if(resource==this.image)
        {
            this.onLoad();
        }
            }.bind(this);
    Events.registerEvent("vatom.resource.changed", this.onResourceChanged);
    this.isFolder = this.vatom.properties.root_type=="vAtomic::v1::vAtom::vAtomType::DefinedFolderContainerType"||this.vatom.properties.root_type=="vAtomic::v1::vAtom::vAtomType::FolderContainerType";


};
// Subclass
ImageFace.prototype = Object.create(Face.prototype);
ImageFace.prototype.constructor = ImageFace;

ImageFace.prototype.onLoad = function()
{

    if(this.isFolder) {
        this.folderCombineOverlay = document.createElement("div");
        this.folderCombineOverlay.style.cssText = 'position: absolute; top: 20px; left: 20px; width: calc(100% - 40px); height: calc(100% - 40px); z-index: 1; border: 4px dashed #AAA; background-color: rgba(0, 0, 0, 0.1); border-radius: 8px; display: none; justify-content: center; align-items: center; font-family: Helvetica, Arial; font-size: 30px; color: #AAA; ';
        this.folderCombineOverlay.innerHTML = '+';
        this.folderListener = new EventListener();
        this.folderListener.registerEvent("vatom.combine-possible", this.combinePossible.bind(this));
        this.folderListener.registerEvent("vatom.combine-cancel", this.combineCancel.bind(this));
        this.folderListener.registerEvent("vatom.combine", this.combine.bind(this));
        Events.addListener(this.folderListener);
    }

    if(this.image) {

        if(this.image.state==VatomResource.states.Loaded)
        {

            var div = document.createElement("div");
            div.style.backgroundSize = this.vatomView.supportsViewMode("card") ? "cover" : "contain";
            div.style.backgroundPosition = "center";
            div.style.backgroundRepeat = "no-repeat";
            div.style.backgroundImage = "url("+this.image.data+")";
            div.style.width = "100%";
            div.style.height = "100%";
            Events.unregisterEvent("vatom.resource.changed",this.onResourceChanged);
            this.show(div);
            this.onLoadComplete();

        }
        else
        if(this.image.state==VatomResource.states.Error)
        {
            var div = document.createElement("div");
            div.style.backgroundSize = "contain";
            div.style.backgroundPosition = "50% 50%";
            div.style.backgroundRepeat = "no-repeat";
            div.style.backgroundImage = "url("+this.image.URL+")";
            div.style.width = "100%";
            div.style.height = "100%";
            Events.unregisterEvent("vatom.resource.changed",this.onResourceChanged);
            this.show(div);
            this.onLoadComplete();
        }
        else
        {
            Events.registerEvent("vatom.resource.changed",this.onResourceChanged);
            this.showLoading();
        }

    }
    else
    {
        this.showError();
        this.onLoadError();
    }

}
ImageFace.prototype.onUnload = function()
{
    if(this.isFolder&&this.folderListener) {
        this.folderListener.unregisterEvent("vatom.combine-possible", this.combinePossible.bind(this));
        this.folderListener.unregisterEvent("vatom.combine-cancel", this.combineCancel.bind(this));
        this.folderListener.unregisterEvent("vatom.combine", this.combine.bind(this));
        Events.removeListener(this.folderListener);
    }
}
//folder stuff...
ImageFace.prototype.combinePossible = function (data) {

    console.log("combine possible?")
    if (data.vid == this.vatom.identifier) {
        this.folderCombineOverlay.style.display = 'flex';
        this.show(this.folderCombineOverlay,true)
    }

};
ImageFace.prototype.combineCancel = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.folderCombineOverlay.style.display = 'none';
    }
};
ImageFace.prototype.combine = function (data) {
    var onsuccess = function(message)
    {
        console.log(message);

        Events.callEvent("vatom.combine-success", data.vatom);
    };
    var onfail = function()
    {
        Events.callEvent("vatom.combine-failed", data.vatom);
    };
    if (data.vid == this.vatom.identifier) {
        this.folderCombineOverlay.style.display = 'none';
        data.vatom.patch({
            parent_id: this.vatom.identifier || ".",
            visibility: {
                type: "owner",
                value: "*"
            }
        },onsuccess.bind(this),onfail.bind(this));
        Events.callEvent("vatom.combine", data.vatom);

    }

};





module.exports = ImageFace;



