//
// Manages messages in a thread

import EventEmitter from '../Utils/EventEmitter'
import Request from '../Request'
import Message from './Message'

export default class MessageThread extends EventEmitter {

    constructor(id, threadManager) {
        super();

        // Properties
        this.id = id;
        this.threadManager = threadManager;
        this.messages = [];
        this.latestDate = new Date(0);

        // Refresh
        this.refresh();

    }

    get isUpdating() {
        return !!this._fetchPromise;
    }

    /** Fetch updates from the server */
    refresh() {

        // Only do one request at a time
        if (this._fetchPromise)
            return this._fetchPromise;

        // Create payload
        var payload = {
            name: this.id,
            count: 50
        }

        // Do request
        this._fetchPromise = Request.post("/analytics/mythreadmessages", payload).then(data => {

            // Process data
            for (var msgInfo of data && data.messages || []) {

                // Add thread info
                this.addMessage(msgInfo.message);

            }

            // Done
            this._fetchPromise = null;
            this.emit("change");
            this.threadManager && this.threadManager.emit("change");
            return this;

        }).catch(err => {

            // Re-throw errors
            this._fetchPromise = null;
            this.emit("change");
            this.threadManager && this.threadManager.emit("change");
            throw err;

        });

        // Notify changed
        this.emit("change");
        this.threadManager && this.threadManager.emit("change");
        return this._fetchPromise;

    }

    /** @private Add message data */
    addMessage(data) {

        // Check if message exists
        var msg = this.messages.find(t => t.id == data.msg_id);
        if (msg) {

            // Process updated data
            msg.processData(data);

        } else {

            // Message doesn't exist, create it
            msg = new Message(data, this);

            // Add it to the list
            this.messages.push(msg);

            // Sort messages
            this.messages.sort((a, b) => b.date.getTime() - a.date.getTime());

        }

    }

}



