var addCSSRule = function (selector, rules, index) {

    var sheet ;
    if(document.styleSheets.length>0)
    {
        sheet = document.styleSheets[0];
    }
    else
    {
        var style = document.createElement("style");
        // WebKit hack :(
        style.appendChild(document.createTextNode(""));

        // Add the <style> element to the page
        document.head.appendChild(style);

        sheet = style.sheet;
    }


    if(!selectorExists(sheet,selector)) {
        if ("insertRule" in sheet) {
            sheet.insertRule(selector + "{" + rules + "}", index||0);
        }
        else if ("addRule" in sheet) {
            sheet.addRule(selector, rules, index||0);
        }
    }
};

var getSelectors =function(sheet) {
    var ret = [];

        var rules = sheet.rules || sheet.cssRules;
        for(var x in rules) {
            if(typeof rules[x].selectorText == 'string') ret.push(rules[x].selectorText);
        }

    return ret;
};

var selectorExists=function(sheet,selector) {

    var selectors = getSelectors(sheet);
    for(var i = 0; i < selectors.length; i++) {
        if(selectors[i] == selector) return true;
    }
    return false;
};


module.exports =addCSSRule;


