var View = require('./View');

var VideoPlayer = function()
{
    View.call(this);

    this.div = document.createElement("DIV");//$("<div class = 'vatom-view vatom-audio-player' ></div>");//use div instead of iframe directly so you can still add children views
    this.videoElement = document.createElement("VIDEO");
    this.videoElement.controls = true;
    this.videoElement.style.width='100%';
    this.videoElement.style.height='auto';
    this.videoElement.style.marginTop='auto';
    this.videoElement.style.marginBottom='auto';
    this.div.appendChild(this.videoElement);
    //this.videoElement =$("<video controls class='vatom-view' style='width:100%;height:100%'></video>");
   // this.div.append(this.videoElement);

    this.src = '';
    var _this = this;
    // Init view
    View.prototype.init.call(this, this.div);
    this.div.style.display='flex';
    //Sets webview src
    Object.defineProperty(this, 'video', {
        get: function() { return _this.src; },
        set: function(newValue) {
            //console.log(newValue);
            if(newValue) {
                _this.src = newValue.URL;
                _this.videoElement.src= _this.src;
            }
        },
        enumerable: true,
        configurable: true
    });
}



// Subclass
VideoPlayer.prototype = Object.create(View.prototype);
VideoPlayer.prototype.constructor = VideoPlayer;

module.exports=VideoPlayer;


