function LocalVatom(vatom)
{


    this.vatom =  vatom;
    var _this = this;
    Object.defineProperty(this, 'containerView', {
        get: function() {

            return _this.iconView;
        }
    });

    Object.defineProperty(this, 'identifier', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.identifier;
        }
    });
    Object.defineProperty(this, 'properties', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.properties;
        }
    });
    Object.defineProperty(this, 'resources', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.resources;
        }
    });
    Object.defineProperty(this, 'states', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.states;
        }
    });
    Object.defineProperty(this, 'subvatoms', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.subvatoms;
        }
    });

    Object.defineProperty(this, 'template', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.template;
        }
    });
    Object.defineProperty(this, 'templateVariation', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.templateVariation;
        }
    });
    
    Object.defineProperty(this, 'isOwned', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.isOwned;
        }
    });
    Object.defineProperty(this, 'isVariationOwned', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.isVariationOwned;
        }
    });
    Object.defineProperty(this, 'state', {
        get: function() {

            if(!_this.vatom)return null;

            return _this.vatom.state;
        }
    });
}

LocalVatom.prototype.retrieveChildVatom=function(vatomID)
{
    if(this.vatom)
        return this.vatom.retrieveChildVatom(vatomID);
};
LocalVatom.prototype.transferToUser=function (user, onSuccess, onFail)
{
    if(this.vatom)
        return this.vatom.transferToUser(user, onSuccess, onFail);
};

LocalVatom.prototype.getProperty=function (property)
{
   return this.vatom.properties[property];
};

LocalVatom.prototype.setAcquireable=function(val)
{
    console.log(val);
};

module.exports = LocalVatom;


