var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var Store = require("../../Code/VatomicStore");
window.THREE = require("three");
window.Hammer = require("hammerjs");
window.V3DLoader = require("@vatomic/threejs-v3d-loader");
var CAMERA_DISTANCE_MULTIPLIER = 1.5
var V3DFace = function (vatomView, face) {
    Face.call(this, vatomView, face);
    this.snap = null;
    // Vars
    this.canvas = document.createElement("CANVAS");
    this.canvas.style.width = "100%";
    this.canvas.style.height = "100%";

    this.cameraRotationTarget = new THREE.Vector3();

    // Prepare 3D
    this.renderer = new THREE.WebGLRenderer({ canvas: this.canvas, alpha: true, antialias: true });
    this.renderer.setClearColor(0, 0);
    this.renderer.setPixelRatio(window.devicePixelRatio || 1);

    // Setup camera
    this.camera = new THREE.PerspectiveCamera(45, this.element.clientWidth / this.element.clientHeight, 0.0001, 20000);
    this.cameraContainer = new THREE.Object3D();
    this.cameraContainer.add(this.camera);

    // Add list to camera
    this.cameraLight = new THREE.PointLight();
    this.camera.add(this.cameraLight);

    // Add rotate gesture recognizer
    this.hammertime = new Hammer(this.canvas);
    this.hammertime.on("pan", function (ev) {

        // Only do if gestures are enabled
        if (!this.gesturesEnabled)
            return;

        if(!this.properties['gestures-disabled']) {
            // Set target rotation
            this.cameraRotationTarget.y -= ev.velocityX * 0.4;
            this.cameraRotationTarget.x -= ev.velocityY * 0.4;
            this.cameraRotationTarget.x = Math.min(Math.max(this.cameraRotationTarget.x, -Math.PI / 2), Math.PI / 2);

            this.saveState({
                x: this.cameraRotationTarget.x,
                y: this.cameraRotationTarget.y
            });


        }

    }.bind(this));

    this.onResourceChanged = function (resource) {

        if (resource == this.vatom.resources['Scene']) {
            this.onLoad();
        }
    }.bind(this);
    //Events.registerEvent("vatom.resource.changed", this.onResourceChanged);

};
// Subclass
V3DFace.prototype = Object.create(Face.prototype);
V3DFace.prototype.constructor = V3DFace;

V3DFace.prototype.onLoad = function () {
    //  this.showLoading();
    this.alive = true;
    //console.log(this.vatom.resources['Scene']);


    if (this.vatom.resources['Scene']) {

        this.showLoading();
        if (!this.vatom.resources['Scene'].state == VatomResource.states.Loading && !this.vatom.resources['Scene'].data) {
            Events.registerEvent("vatom.resource.changed", this.onResourceChanged);
            this.vatom.resources['Scene'].priority = this.vatom.resources['Scene'].priority  + 1;
        }
        else if (this.vatom.resources['Scene'].state == VatomResource.states.Loaded) {
            // Add resize listener

            // Start render loop
            this.render();


            //console.error(this.vatom.resources["Scene"]);
            // Load asset
            this.openFile(this.vatom.resources["Scene"].data);

            Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
            this.show(this.canvas);
            this.onResize();
            this.onLoadComplete();
        }
        else if (this.vatom.resources['Scene'].state == VatomResource.states.Error) {
            Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
            this.showError();
            this.onLoadError();
        }
        else {
            Events.registerEvent("vatom.resource.changed", this.onResourceChanged);
            this.showLoading();
        }
    }
    else {
        this.showError();
    }
};

V3DFace.prototype.onUnload = function () {
    this.closeFile();
};
V3DFace.prototype.showLoading = function () {
    if (this.snap) {
        //console.log(this.snap);
        var div = document.createElement("div");

        div.style.backgroundPosition = "center";
        div.style.backgroundRepeat = "no-repeat";
        div.style.backgroundImage = "url('" + this.snap + "')";
        div.style.position = "absolute";
        div.style.top = "0px";
        div.style.left = "0px";
        div.style.width = "100%";
        div.style.height = "100%";
        this.show(div);
        this.loader = div;
    }
    else {
        Face.prototype.showLoading.call(this);
    }
}
// Called when the view is resized
V3DFace.prototype.onResize = function () {

    // //console.error("on resize ("+ this.element.clientWidth+"x"+ this.element.clientHeight+")");

    // Get size of canvas
    // var rect = this.canvas.getBoundingClientRect();

    // Update renderer size
    this.renderer.setSize(this.element.clientWidth, this.element.clientHeight, false);
    this.camera.aspect = this.element.clientWidth / this.element.clientHeight;
    this.camera.updateProjectionMatrix();
    this.camera.position.z = this.sceneBSRadius * CAMERA_DISTANCE_MULTIPLIER//* Math.max(this.element.clientWidth / this.element.clientHeight, this.element.clientHeight / this.element.clientWidth);


};

// Render scene
V3DFace.prototype.render = function () {

    if (!this.alive) return;
    // Do again soon
    requestAnimationFrame(this.render.bind(this));

    ////console.log("Test");
    // Rotate camera
    if (this.cameraContainer) {
        this.cameraContainer.rotation.x += (this.cameraRotationTarget.x - this.cameraContainer.rotation.x) * 0.5;
        this.cameraContainer.rotation.y += (this.cameraRotationTarget.y - this.cameraContainer.rotation.y) * 0.5;
        this.cameraContainer.rotation.z += (this.cameraRotationTarget.z - this.cameraContainer.rotation.z) * 0.5;
    }

    // Do render
    if (this.scene && this.camera)
        this.renderer.render(this.scene, this.camera);

};


// Close the currently open file
V3DFace.prototype.closeFile = function () {

    this.alive = false;
    // Remove scene items
    while (this.scene && this.scene.children.length > 0)
        this.scene.remove(this.scene.children[0]);

};

// Open the file
V3DFace.prototype.openFile = function (file) {

    // Load file
    V3DLoader.load(file).then(function (scene1) {


        // Store scene
        this.scene = scene1;


        this.createParent();

        // Add camera
        this.cameraContainer.parent && this.cameraContainer.parent.remove(this.cameraContainer);
        this.scene.add(this.cameraContainer);

        // Move camera to optimal distance to view the entire scene
        this.moveCamera();

        // Remove rotation
        this.cameraRotationTarget.set(0, 0, 0);
        this.cameraContainer.rotation.set(0, 0, 0);

        // Enable or disable camera light
        this.cameraLight.parent && this.cameraLight.parent.remove(this.cameraLight);
        if (!this.scene.userData.fileMetadata["preview.cameraLight.disabled"])
            this.camera.add(this.cameraLight);

        // If preview option is set, remove all lights
        if (!this.scene.userData.fileMetadata["preview.lights.disabled"]) {
            var itemsToRemove = [];
            this.scene.traverse(function (obj) {
                if (obj != this.cameraLight && obj instanceof THREE.Light) itemsToRemove.push(obj);
            }.bind(this));
            for (var i = 0; i < itemsToRemove.length; i++) {
                itemsToRemove[i].parent && itemsToRemove[i].parent.remove(itemsToRemove[i]);
            }
        }

        this.loadState(Store._states[this.vatom.identifier+"-"+this.face.id]);
    }.bind(this)).catch(function (e) {

        // Error
        //console.warn(e.stack);
        console.warn(e);

    });

};

V3DFace.prototype.loadState = function (state) {
    try {
      //  var state = JSON.parse(Store._states["face-state-" + this.vatom.identifier]);
        if (state) {
            //console.log("load state", state);
            this.cameraRotationTarget.x = state.x;
            this.cameraRotationTarget.y = state.y;
            this.cameraContainer.rotation.x = this.cameraRotationTarget.x ;
            this.cameraContainer.rotation.y = this.cameraRotationTarget.y ;
            //this.cameraContainer.rotation.z = this.cameraRotationTarget.z ;

        }
    }
    catch(e)
    {}
};



V3DFace.prototype.createParent = function () {
    // Stop if no scene
    if (!this.scene)
        return;
    var parent = new THREE.Object3D();

    var _this = this;
    for (var i = 0; i < this.scene.children.length; i++) {
        var object = _this.scene.children[i];
        //_this.scene.remove(object);
        parent.add(object);
        i--;
    }
    this.scene.add(parent);
    this.parent = parent;
};

// Moves the camera to the best distance to view the whole scene
V3DFace.prototype.moveCamera = function () {

    // Stop if no scene
    if (!this.scene || !this.camera)
        return;

    // Calculate the center and radius of the scene (code from https://github.com/mrdoob/three.js/issues/1493)
    var sceneBSCenter = new THREE.Vector3();
    var sceneBSRadius = 0;


    var _this = this;

    // //console.error(sceneBSCenter);

    var box = new THREE.Box3().setFromObject(_this.parent);
    box.center(_this.parent.position); // this re-sets the mesh position
    _this.parent.position.multiplyScalar(-1);
    this.sceneBSRadius = Math.sqrt(Math.pow((box.max.x - box.min.x), 2) + Math.pow((box.max.z - box.min.z), 2) + Math.pow((box.max.y - box.min.y), 2));


    // Move camera
    //console.log(this.element.clientWidth / this.element.clientHeight);

    //console.log();
    this.camera.position.z = this.sceneBSRadius * CAMERA_DISTANCE_MULTIPLIER//* Math.max(this.element.clientWidth / this.element.clientHeight, this.element.clientHeight / this.element.clientWidth);
    //console.log(this.camera.position.z)

    // Set camera's near and far values, to prevent artefacts
    this.camera.near = this.sceneBSRadius * 0.5
    this.camera.far = this.sceneBSRadius * 4
    this.camera.updateProjectionMatrix()
    console.log("V3DFace: Camera near is " + this.camera.near + " and far is " + this.camera.far)

}

/** Controls whether gestures are allowed to be received by the view */
Object.defineProperty(V3DFace.prototype, "gesturesEnabled", {
    get: function() {
        return !this._disableGestures;
    },
    set: function(val) {

        // Store value
        this._disableGestures = !val;

    }
});

V3DFace.prototype.free = function () {
    Face.prototype.free.call(this);

    try {
        this.hammertime && this.hammertime.off('tap');
        this.closeFile();
        this.renderer.forceContextLoss();
        this.renderer.context = null;
        this.renderer.domElement = null;
        this.renderer = null;
    }catch(e)
    {
        console.log(e);
    }

};
module.exports = V3DFace;



