var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");

var Events = require("../../Code/Events");

var ProgressImageFace = function(vatomView,face)
{
    Face.call(this, vatomView,face);

    if(!vatomView||!face)return;


    this.baseImage = this.vatom.resources.BaseImage;
    this.fillImage = this.vatom.resources.ActivatedImage;

    this.onResourceChanged = function (resource) {

        ////console.error("Registered resource changed called",this.vatom.identifier);
        if(resource==this.baseImage||resource==this.fillImage)
        {

            this.onLoad();
        }
    }.bind(this);


    this.onLoad=this.onLoad.bind(this);
    Events.registerEvent("vatom.resource.changed",this.onResourceChanged);
   // Events.registerEvent("vatom.update-complete", this.onLoad);

        ////console.error("Registered resource changed",this.vatom.identifier);

};
// Subclass
ProgressImageFace.prototype = Object.create(Face.prototype);
ProgressImageFace.prototype.constructor = ProgressImageFace;

ProgressImageFace.prototype.onVatomUpdated= function()
{
    this.onLoad();
};
ProgressImageFace.prototype.onLoad = function()
{

    if(this.baseImage&&this.fillImage) {


        if(this.baseImage.data&&this.fillImage.data)
        {

            // Get info
            var score =Math.floor( Math.min(1, Math.max(0, parseFloat(this.vatom.properties.cloning_score) || 0))* 100);
            var padding_start = parseFloat(this.vatom.properties.padding_start) || 0;
            var padding_end = parseFloat(this.vatom.properties.padding_end) || 0;
            var direction = (this.vatom.properties.direction || "").toLowerCase();

            var div = document.createElement("div");
            div.style.width = "100%";
            div.style.height = "100%";

            var baseContainer = document.createElement("div");
            baseContainer.style.width = "100%";
            baseContainer.style.height = "100%";
            baseContainer.style.position="absolute";
            baseContainer.style.top="0px";
            baseContainer.style.left="0px";
            baseContainer.style.overflow="hidden";

            var base = document.createElement("div");
            base.style.backgroundSize = "contain";
            base.style.backgroundPosition = "50% 50%";
            base.style.backgroundRepeat = "no-repeat";
            base.style.backgroundImage = "url("+this.baseImage.data+")";
            base.style.width = "100%";
            base.style.height = "100%";

            var fillContainer = document.createElement("div");
            fillContainer.style.width = "100%";
            fillContainer.style.height = "100%";
            fillContainer.style.position="absolute";
            fillContainer.style.overflow="hidden";
            fillContainer.className="fillContainer";

            var fill = document.createElement("div");
            fill.style.backgroundSize = "contain";
            fill.style.backgroundPosition = "50% 50%";
            fill.style.backgroundRepeat = "no-repeat";
            fill.style.backgroundImage = "url("+this.fillImage.data+")";
            fill.style.width = "100%";
            fill.style.height = "100%";
            fill.style.position='absolute';
            fill.className="fill";


            var percentDiv =  document.createElement("div");
            percentDiv.style.display="block";
            percentDiv.style.position="absolute";
            percentDiv.style.top="10px";
            percentDiv.style.right="10px";
            percentDiv.style.fontFamily="Helvetica, Arial";
            percentDiv.style.fontSize="13px";
            percentDiv.style.color="rgb(170, 170, 170)";
            percentDiv.innerHTML=score+"%";

            baseContainer.appendChild(base);
            fillContainer.appendChild(fill);
            div.appendChild(baseContainer);
            div.appendChild(fillContainer);
            div.appendChild(percentDiv);

            var percent = (100-score);
            if (direction == "down")
            {
                fillContainer.style.top=(-1*percent)+"%";
                fill.style.top=(percent)+"%";


            }
            else if (direction == "up") {

                fillContainer.style.top=percent+"%";
                fill.style.top=(-1*percent)+"%";
            }
            else if (direction == "left") {

                fillContainer.style.left=(-1*percent)+"%";
                fill.style.left=(percent)+"%";
            }
            else
            {
                fillContainer.style.left=percent+"%";
                fill.style.left=(-1*percent)+"%";

            }

            // Check if level is reached
            if (score >= 100) {

               if( this.vatom.properties&& this.vatom.properties.MyCommerce&&this.vatom.properties.MyCommerce.redeemable)
               {
                   this.vatom.transferActions=[{action:"Redeem"},{action:"Transfer"}]
               }
                else
                   this.vatom.transferActions=[{action:"Transfer"}]


                // Set transfer action to Clone


            } else {

                this.vatom.transferActions=[{action:"Clone"}];
                // Normal transfer action

            }


            Events.unregisterEvent("vatom.resource.changed",this.onResourceChanged);
            this.show(div);
            this.onLoadComplete();

        }
        else
        if(this.baseImage.state==VatomResource.states.Error||this.fillImage.state==VatomResource.states.Error)
        {
            Events.unregisterEvent("vatom.resource.changed",this.onResourceChanged);

            this.showError();
            this.onLoadError();
        }
        else
        {


            this.showLoading();




        }

    }
    else
    {
        this.showError();
        this.onLoadError();
    }

};
ProgressImageFace.prototype.onVatomStateChanged = function(data)
{
    if (data&&data.id==this.vatom.identifier&&data.new_object) {
        this.vatom.update(data.new_object);

    }

};
ProgressImageFace.prototype.onUnload = function()
{
   // Events.unregisterEvent("vatom.update-complete", this.onLoad);
    Events.unregisterEvent("vatom.resource.changed",this.onResourceChanged);

};



module.exports = ProgressImageFace;



