var EventListener = function () {
    this.events = {};
    this.messageQueue = [];
    this.isPaused = false;
};


/** Registers an event listener callback */
EventListener.prototype.registerEvent = function (name, callback) {

    // Check for valid values
    if (!name || !callback)
        return;

    // Add event to event array
    this.events[name] = this.events[name] || [];


    if(this.events[name].indexOf(callback)!=-1)
    {
        ////console.log("Event already registered");
        return;
    }

    this.events[name].push(callback);

    
    return true;

};
/** Registers an event listener callback that will only be fired once */
EventListener.prototype.registerSingleCallEvent = function (name, callback) {

    var call = function (data) {

        if (callback(data)) {
            ////console.log("unregistering event")
            this.unregisterEvent(name, call);
        }
    }.bind(this);

    this.registerEvent(name,call);

    return true;

};

/** Triggers an event, and sends the event data to all registered listeners for this event */
EventListener.prototype.callEvent = function (name, data) {
    // Check for values
    if (!name || !this.events[name])
        return;

    if (this.isPaused) {
        this.messageQueue.push({name: name, data: data});
        return;
    }

    // Go through event listeners
    this.events[name].slice(0).forEach(function (callback) {

        // Catch errors
        try {
            // Send event
            callback(data);

        } catch (e) {
            //console.error(e);
        }

    });

};

/** Remove all listeners for a specified event */
EventListener.prototype.clearEvent = function (name) {

    if (this.isPaused) {
        //console.warn("You unregistering an event while listener is paused, messages of that type in queue won't be sent");
    }
    // Remove it
    if (name && this.events[name])
        this.events[name] = [];

};

/** Removes an event listener */
EventListener.prototype.unregisterEvent = function (name, callback) {

    if (this.isPaused) {
        //console.warn("You unregistering an event while listener is paused, messages of that type in queue won't be sent");
    }
    // Check values
    if (!name || !this.events[name])
        return false;

    // Remove item from array
    var index = this.events[name].indexOf(callback);
    if (index > -1) {
        
        this.events[name].splice(index, 1);
        return true;
    }

    // Item not found
    return false;

};

EventListener.prototype.pause = function () {
    this.isPaused = true;
};
EventListener.prototype.resume = function () {
    this.isPaused = false;
    while (this.messageQueue.length > 0) {
        var message = this.messageQueue[0];
        this.callEvent(message.name, message.data);
        this.messageQueue.splice(0, 1);
    }

};
EventListener.prototype.free = function()
{
    this.events = {};
    this.messageQueue = [];
};

module.exports = EventListener;


