var View = require('./View');
var AudioPlayer = function()
{
    View.call(this);

    this.div = document.createElement("DIV");//$("<div class = 'vatom-view vatom-audio-player' ></div>");//use div instead of iframe directly so you can still add children views
    this.audioElement = document.createElement("AUDIO");
    this.audioElement.controls = true;
    this.audioElement.style.width='100%';
    this.audioElement.style.height='100%';
    this.div.appendChild(this.audioElement);
    this.src = '';
    var _this = this;
    // Init view
    View.prototype.init.call(this, this.div);
    //Sets webview src
    Object.defineProperty(this, 'audio', {
        get: function() { return _this.src; },
        set: function(newValue) {
                _this.src = newValue.URL;
                _this.audioElement.src= _this.src;
        },
        enumerable: true,
        configurable: true
    });
}



// Subclass
AudioPlayer.prototype = Object.create(View.prototype);
AudioPlayer.prototype.constructor = AudioPlayer;

module.exports = AudioPlayer;


