var Events = require('../Events');



/** @class The VatomResource class represents a single resource file attached to a vatom */
class VatomResource {

    constructor(url) {

        // Vars
        this.URL = url;
        this._state = VatomResource.states.None;

        /** Resource download priority */
        this.priority = 0;

    }

        // var _this = this;

    // Object.defineProperty(this, 'priority', {
    //     get: function () {
    //         return _this._priority;
    //     },
    //     set: function (newValue) {
    //         _this._priority = newValue;
    //         Events.callEvent("vatom.resource.priority.changed", _this);
    //     }
    //     ,
    //
    //     enumerable: true,
    //     configurable: true
    // });

    /** Gets the data for this resource, or null if not loaded. */
    get data() {

        // Check if we have the data in our memory cache
        if (this.memoryCachedData)
            return this.memoryCachedData;

        return null;

    }

    /** Checks the resource's current state, ie. downloading, error, etc. */
    get state() {
        return this._state
    }

    set state(newValue) {

        // Check for change
        if (this._state == newValue)
            return;

        // Update state
        this._state = newValue;

        // Notify change
        Events.callEvent("vatom.resource.changed", this);

    }

    // TODO: Implement this. This should return the resource data as a Blob, and return a Promise.
    getBlob(callback) {
        throw new Error("Not implemented!");
        // if (!callback && !this.state == VatomResource.states.Loaded)return false;
        //
        // callback(new Blob([this.URL], {type: "text"}));
        //
        // return true;
    }

    /** @private Called to start the loading */
    load() {

        // Update state
        if (this.state == VatomResource.states.Loading) return;
        this.state = VatomResource.states.Loading;

        // Add to download queue
        VatomResourceManager.downloadResource(this);

        console.warning("WARNING: VatomResource.load() not implemented!");

    }

    /** @private Called when the data has been loaded. Can be called multiple times if there is cached data that gets updated. Process the data here and update the state. */
    onLoadedData() {

        // Notify change
        this.state = VatomResource.states.Loaded;

    }

    /** @private Called when the data load fails */
    onLoadFailed(err) {

        // Check if not complete
        if (this.state != VatomResource.states.Loading)
            return;

        // Notify change
        console.log("Vatomic SDK: Vatom resource load failed: " + err);
        this.state = VatomResource.states.Error;

    }

    /** Prefetch this resource by performing an XHR request to fetch it, hopefully adding it to the browser's cache.
     *  TODO: This should be removed and the data should be cached via the load() function.
     *  */
    _prefetch() {
        console.log("Prefetching vatom resource: " + this.URL);
        return new Promise((onSuccess, onFail) => {

            // Create XHR
            var xhr = new XMLHttpRequest();
            xhr.responseType = "arraybuffer";
            xhr.open("GET", this.URL);
            xhr.send();

            // Listen for response
            xhr.onload = onSuccess;
            xhr.onerror = onFail;

        })
    }

}

/** Possible resource states */
VatomResource.states = {
    None: 0,
    Loading: 1,
    Error: 2,
    Loaded: 3
};

module.exports = VatomResource;



