var Request = require('./Request');

// TODO: Remove the need for this class!
//
var RequestHelper = {

    // Do guest login, using the device's Guest ID
    loginGuest: function (guestID, onSuccess, onFail) {
        console.log("logging in guest");
        // Create payload
        var payload = {
            guest_id: guestID
        };

        Request.send("POST", "/user/vatomic/login", null,payload, onSuccess, onFail);

    },
    loginEmail: function (email, password, onSuccess, onFail) {
        // Create payload
        var payload = {
            email: email
        };
        if(password)
        {
            payload['password']=password;
        }
        // Send request
        Request.send("POST", "/user/vatomic/login",null, payload, onSuccess, onFail);
    },
    loginPhone: function (phone, password, onSuccess, onFail) {
        // Create payload
        var payload = {
            phone_number: phone
        };
        if(password)
        {
            payload['password']=password;
        }
        // Send request
        Request.send("POST", "/user/vatomic/login", null,payload, onSuccess, onFail);
    },
    // Do login via username and password. The username can be an email or a phone number.
    login: function (username, password, onSuccess, onFail) {
        RequestHelper.loginEmail(username, password, onSuccess, function (errorCode, errorText) {

            //console.log("logging in");
            // If failed because user didn't exist, try login with phone number instead
            if (errorCode != 'ACCOUNT_NOT_FOUND') {
                if (onFail) {
                    onFail(errorCode, errorText);
                }
                return;
            }

            RequestHelper.loginPhone(username, password, onSuccess, onFail);
        });
    },
    // Do login via OAuth token from a service
    loginOauth: function (service, oauthToken, onSuccess, onFail) {
        // Create payload
        var payload = {
            oauth_provider: service,
            token: oauthToken
        };
        // Send request
        Request.send("POST", "/user/oauth/login",null, payload, onSuccess, onFail);
    },
    logout: function (onSuccess, onFail) {
        // send logout request
        Request.send("GET", "/currentuser/logout",null,null, onSuccess, onFail);

    },
    // Do register
    register: function (info, onSuccess, onFail) {
        // Send request
        Request.send("POST", "/user/register", null,info, onSuccess, onFail);

    },

    // Verify user account
    verifyPhone: function (phone, token, onSuccess, onFail) {

        // Create payload
        var payload = {
            phone_number: phone,
            token: token
        };

        // Send request
        Request.send("POST", "/user/verify",null, payload, onSuccess, onFail);

    },

    // Verify user account
    verifyEmail: function (email, token, onSuccess, onFail) {

        // Create payload
        var payload = {
            email: email,
            token: token
        };

        // Send request
        Request.send("POST", "/user/verify",null, payload, onSuccess, onFail);

    },
    resetPasswordUID:function(onSuccess, onFail)
    {

        Request.send("GET","/api/v1/user/resetpwd/id")
    },
    getVatomInventory: function (id, pageToken, pageAmount, onSuccess, onFail) {

        //console.log("get vatom inventory : "+id);
        // Fetch inventory
        var payload = {
            parent_id: id,
            page_token: pageToken,
            page_amount: pageAmount
        };
        Request.send("POST", "/currentuser/inventory",null, payload, function (data) {


            //console.log(data);
            if (onSuccess && data) {
                var inventory = data.inventory;
                var faces = data.faces;

                RequestHelper.joinVatomsWithFaces(inventory,faces);

                onSuccess(inventory, data.page_token);

            }

        }, onFail);
    },
    joinVatomsWithFaces:function (vatoms,faces) {

        if(!vatoms||!faces||vatoms.length==0||faces.length==0) return;

        for (var i = 0; i < vatoms.length; i++) {
            try {
                var temp_id = vatoms[i]["vAtomic::v1::vAtom::vAtomType"].template;
                if (!temp_id)continue;

                vatoms[i].faces = [];

                for (var o = 0; o < faces.length; o++) {

                    if (faces[o].template == temp_id) {

                        vatoms[i].faces.push(faces[o]);
                    }

                }
            } catch (e) {

                //console.error(e);
            }
        }


    },
    getRootInventory: function (pageToken, pageAmount, onSuccess, onFail) {
        RequestHelper.getVatomInventory('', pageToken, pageAmount, onSuccess, onFail);
    },
    getInventory: function (id,token,pageAmount, onSuccess, onFail) {

       // //console.log("get inventory is being callled?");
        if (!onSuccess) return;

        var pageToken = token;
        var vatomInventory = [];
        var page_amount = pageAmount;

        var success = function (inventory, token) {
            pageToken = token;
            vatomInventory = vatomInventory.concat(inventory);
            //console.log(inventory);

            if (inventory&&inventory.length == page_amount) {
              RequestHelper.getVatomInventory(id||'', pageToken, page_amount, success, onFail)
            }
           else {

                onSuccess(vatomInventory, pageToken);
            }

        };


        RequestHelper.getVatomInventory(id, pageToken, page_amount, success, onFail)
    },
    discoverQuery: function (query, onSuccess, onFail) {
        // Send request
        Request.send("POST", "/currentuser/vatom/discover",null, query, onSuccess, onFail);
    },
    getVatomActions: function (vatom, onSucess, onFail) {
        Request.send("GET", "/currentuser/actions/" + vatom.template, null,null, onSucess, onFail);
    },
    getVatom: function (vatomid, onSuccess, onFail) {

        if(!vatomid||!onSuccess)return;

        //console.log("get Vatom");
        Request.send("POST","/currentuser/vatom/get",null,{ids:[vatomid]},function(data){

            if (onSuccess && data) {
                var inventory = data.objects;
                var faces = data.faces;

                //console.log(data);

                RequestHelper.joinVatomsWithFaces(inventory,faces);
            //    if(inventory[0].faces>0)
               // {
                    onSuccess(inventory[0]);
                //}
                //else
                //{
                  //  onFail();
                //}

            }
        },onFail);
    },
    resetPassword:function(username,onSuccess,onFail)
    {
        var payload = (username.indexOf('@') > -1) ? {email: username} : {phone_number: username};
        Request.send("POST","/user/resetpwd",null,payload,onSuccess,onFail);
    },
    transferVatom:function(id,to,onSuccess,onFail)
    {

        if(!id||!to)
        {
            onFail();
            return;
        }
        var payload = {
            'this.id': id
        };
        if(to.email){
            payload['new.owner.email'] = to.email;
        } else if(to.phone){
            payload['new.owner.phone_number'] = to.phone;
        } else if(to.uid){
            payload['new.owner.id'] = to.uid;
        }
        Request.send('POST',"/currentuser/action/Transfer",null,payload,onSuccess,onFail);

    },
    transferVatomActions:function(id,to,actions,onSuccess,onFail)
    {

        if(!id||!to||!actions)
        {
            onFail("invalid_parameters", "Incorrect parameters passed to RequestHelper.transferVatomActions.");
            return;
        }

        var count = 0;
        var lastError = null;
        var lastErrorText = null;
        var doAction = function(){
            if(count>=actions.length)
            {
                onFail(lastError, lastErrorText);
                return;
            }
            var action = actions[count];
            count++;
            var payload = action.payload||{};
            payload['this.id']=id;
            if(to.email){
                payload['new.owner.email'] = to.email;
            } else if(to.phone){
                payload['new.owner.phone_number'] = to.phone;
            }
            else
            if (to.uid){
                payload["new.owner.id"] = to.uid;
            }

            RequestHelper.currentUserAction(action.action,payload,onSuccess,function(error, text){
                lastError = error;
                lastErrorText = text;
                doAction();
            })
        };
        doAction();
    },
    currentUserAction:function(action,payload,onSuccess,onFail)
    {
        Request.send('POST',"/currentuser/action/"+action,null,payload,function(data){

            if(data) {
               if(data["main"]["Output"]&&data["main"]["Output"].constructor === Array)
               {
                   onSuccess({items:data["main"]["Output"]});
               }
                else
                   onSuccess(data["main"]["Output"]);
            }
            else
            onSuccess(data)
        },onFail);
    },
    send:function(method, endpoint, contentType, payload, onSuccess, onFail)
    {
        Request.send(method,endpoint,contentType,payload,onSuccess,onFail);
    },

    updateCurrentUser: function(payload, onSuccess, onFail) {

        // Send request
        Request.send("PATCH", "/currentuser", null, payload, onSuccess, onFail);
    },
    updateUser: function(payload, onSuccess, onFail) {

        // Send request
        Request.send("PATCH", "/user", null, payload, onSuccess, onFail);
    },
    patchVatom:function(payload,onSuccess, onFail)
    {
        Request.send("PATCH", "/currentuser/vatom", null, payload,function(message){
            console.log(message);
            if(message&&message.num_errors&&message.num_errors>0)
            {
                onFail&&onFail();
            }
            else
                onSuccess&&onSuccess();

        } , onFail);
    },
    getUsername:function(data,onSuccess,onFail)
    {
        if(!data)return;
        var key = data.id?"id":(data.email?"email":(data.phone_number?"phone_number":null));
        if(!key) return;
        var encoded = encodeURI(data[key]).replace('+','%2B');
        Request.send("GET","/user/name?key="+key+"&val="+encoded,null,null,onSuccess,onFail);

    },
    getAvatar:function(data,onSuccess,onFail)
    {
        if(!data)return;
        var key = data.uid?"uid":(data.email?"email":(data.phone_number?"phone_number":null));
        if(!key) return;
        var encoded = encodeURI(data[key]).replace('+','%2B');
        Request.send("GET","/user/avatar?key="+key+"&val="+encoded,null,null,onSuccess,onFail);

    },

    /** Get user ID from email or phone */
    getUserID:function(data,onSuccess,onFail)
    {
        if(!data)return;
        var key = data.uid?"uid":(data.email?"email":(data.phone_number?"phone_number":null));
        if(!key) return;
        var encoded = encodeURI(data[key]).replace('+','%2B');
        Request.send("GET","/user/id?key="+key+"&val="+encoded,null,null,onSuccess,onFail);

    },
    resendVerifyPhone:function(phone,onSuccess,onFail)
    {
        var payload = {phone_number:phone};
        Request.send("POST","/user/verify/resend",null,payload,onSuccess,onFail);

    }
    ,
    resendVerifyEmail:function(email,onSuccess,onFail)
    {
        Request.send("POST","/user/verify/resend",null,{email:email},onSuccess,onFail);

    }





};


module.exports = RequestHelper;



