var VatomicWebSocketListener = require("./VatomicWebSocketListener");
var Inventory = require("./Inventory");
var VatomicStore = require("./VatomicStore");
var Events = require("./Events");

var SocketInventory = function (fetchVatoms) {
    Inventory.call(this, fetchVatoms);

    Events.registerEvent("websocket.open", this.refresh.bind(this));
    this.socketListener = new VatomicWebSocketListener();
    this.socketListener.onInventoryChanged = this.onInventoryChanged.bind(this);
    this.socketListener.onVatomStateChanged = this.onVatomStateChanged.bind(this);
    this.socketListener.start();

};

SocketInventory.prototype = Object.create(Inventory.prototype);
SocketInventory.prototype.constructor = SocketInventory;

SocketInventory.prototype.onInventoryChanged = function (data) {
    ////console.info("socket",data);
    ////console.info("socket vatomic user",VatomicStore['user']);
    if (VatomicStore['user'] && data && data.id && data.new_owner) {
        if (VatomicStore['user'].userID == data.new_owner) {
            //refresh

            this.refresh();
        }
        else {
            /*
             {"msg_type":"inventory","payload":{"event_id":"inv_0fa11860-6be6-487a-9f32-b46f234bd36b:2016-09-14T20:09:50Z","op":"update","id":
             "0fa11860-6be6-487a-9f32-b46f234bd36b","new_owner":"0fc98aa5-7433-4a50-b868-250d45a60aad","old_owner":"91c16b4b-0f20-4722-b646-3ccf402011e7","template_variation":"vatomic.prototyping::Coin::2Dollar","parent_id":"."}}
             */
            //console.log("removing vatom")
            this.removeVatom(data.id);
            this.refresh();
        }
        //var index = this.indexOf(data.id);
    }

};

SocketInventory.prototype.onVatomStateChanged = function (data) {

    var idx = this.indexOf(data.id);

    if (data.new_object && data['new_object']['vAtomic::v1::vAtom::vAtomType'] && data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id) {
        if (idx > -1 &&data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id!=this.vatoms[idx].properties.parent_id) {
            this.removeVatom(data.id);
            this.refresh();
        }
        else {
            this.refresh();
        }
    }
    
};


SocketInventory.prototype.refresh = function () {
    this.socketListener.pause();
    Inventory.prototype.refresh.call(this);
    this.socketListener.resume();

};

SocketInventory.prototype.free = function () {
    this.socketListener.stop();
};

module.exports = SocketInventory;
