var Store = require("./VatomicStore");
var Config = {};
var merge = require("lodash/merge");
Object.defineProperty(Config, 'defaults', {
    get:function()
    {
        return Store.DEFAULTS;
    },
    set:function(value)
    {
        Store.DEFAULTS = merge({},Store.DEFAULTS,value);
    }
});
module.exports = Config;


