var VatomicWebSocketListener = require("../Code/VatomicWebSocketListener");
var ImageFace = require("../UI/NativeFaces/ImageFace");
var CoinFace = require("../UI/NativeFaces/CoinFace");
var ProgressImageFace = require("../UI/NativeFaces/ProgressImageFace");
var HTMLFace = require("../UI/NativeFaces/HTMLFace");
var VatomResourceManager = require('./Resources/VatomResourceManager');
var Face = require("../UI/NativeFaces/Face");
var LayeredImageFace = require('../UI/NativeFaces/LayeredImageFace');
var FolderFace = require('../UI/NativeFaces/FolderFace');
var SpriteSheetFace = require("../UI/NativeFaces/SpriteSheetFace");
//var V3DFace = require("../UI/NativeFaces/V3DFace");
//var V3DFolderFace = require("../UI/NativeFaces/V3DFolderFace");
//var ScriptFace = require("../UI/NativeFaces/ScriptFace");
var VatomicFaceManager = {
    socketListener: new VatomicWebSocketListener(),
    vatoms: {},
    getFace: function (vatom, face, miscProp, callback) {
        if (!callback) return;
        if (!vatom || !face) {
            callback(null);
            return;
        }
        var createNewFace = function (callback) {
            //get a new face
            var download = true;

            if (face.properties && face.properties.display_url) {
                if (face.properties.display_url.indexOf("native://image") == 0) {
                    callback(new ImageFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://coin-renderer") == 0) {
                    callback(new CoinFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://progress-image-overlay") == 0) {
                    callback(new ProgressImageFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://layered-image") == 0) {
                    callback(new LayeredImageFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://folder") == 0) {
                    callback(new FolderFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://generic-3d-folder") == 0) {
                    require(["../UI/NativeFaces/V3DFolderFace"], function (V3DFolderFace) {
                    callback(new V3DFolderFace(vatom, face, miscProp));
                    }.bind(this));
                }
                else if (face.properties.display_url.indexOf("native://generic-3d") == 0) {

                   /* console.log("3d face",face);
                    if(face.properties.constraints.view_mode=="icon")
                    {
                        //console.log("should be an image",vatom);
                        face.properties.resources&&(face.properties.resources[0]=("ActivatedImage"));
                        callback(new ImageFace(vatom, face, miscProp));
                    }
                    else {*/
                        require(["../UI/NativeFaces/V3DFace"], function (V3DFace) {
                            callback(new V3DFace(vatom, face, miscProp));
                        }.bind(this));
                    //}
                }
                else if (face.properties.display_url.indexOf("native://sprite-sheet") == 0) {
                    callback(new SpriteSheetFace(vatom, face, miscProp));
                }
                else if (face.properties.display_url.indexOf("native://script-renderer") == 0) {

                    require(["../UI/NativeFaces/ScriptFace"],function (ScriptFace) {
                        callback(new ScriptFace(vatom, face, miscProp));
                    }.bind(this));

                }
                else if (face.properties.display_url.indexOf("native:") == 0) {
                    callback(null);
                }
                else {
                    download = false;
                    callback(new HTMLFace(vatom, face, miscProp));
                }
            }
            ///start resources downloading that face might need
            if (download) {
               // console.log(face);
                if (face.properties.resources) {
                    for (var i = 0; i < face.properties.resources.length; i++) {
                        VatomResourceManager.downloadResource(vatom.resources[face.properties.resources[i]]);
                    }
                }

            }

        };

        if (VatomicFaceManager.vatoms[vatom.identifier] && VatomicFaceManager.vatoms[vatom.identifier].length > 0)//look for face not in use
        {
            var view;
            //check if the required face view already exists and is not in use
            for (var i = 0; i < VatomicFaceManager.vatoms[vatom.identifier].length; i++) {
                view = VatomicFaceManager.vatoms[vatom.identifier][i];

                if (view.face.id == face.id && !view.isAttached()) {
                    callback(view);
                    return;
                }
            }

        }
        else {
            VatomicFaceManager.vatoms[vatom.identifier] = [];
        }


        //create new face view
        createNewFace(function (face) {
            VatomicFaceManager.vatoms[vatom.identifier].push(face);
            callback(face);
        });


    },

    clean: function () {

      //  console.log("cleaning", VatomicFaceManager.vatoms)

        for (var id in VatomicFaceManager.vatoms) {

            var faces = VatomicFaceManager.vatoms[id];
            //console.log(faces);

            for (var o = 0; o < faces.length; o++) {
                if (!faces[o].isAttached()) {
                    faces[o].free();
                    faces.splice(o, 1);
                    o--;
                  //  console.log("removing un used face");
                }
            }
        }


    }
    ,

    hasParent: function (element) {
        if (element) {
            while (element.parentElement) {
                if (element.parentElement.nodeName == "HTML")
                    return true;
                element = element.parentElement;
            }
        }
        return false;

    }
    ,
    init: function () {
        setInterval(VatomicFaceManager.clean, 15000);

        VatomicFaceManager.socketListener.pause();
        VatomicFaceManager.socketListener.onVatomStateChanged = function (data) {
            console.log(data);
            if (data && data.id && data.new_object) {
                var views = VatomicFaceManager.vatoms[data.id];
                if (views && views.length > 0) {
                    for (var i = 0; i < views.length; i++) {
                        views[i].vatom.update(data.new_object);
                    }
                }
            }
        };
        VatomicFaceManager.socketListener.start();
        VatomicFaceManager.socketListener.resume();
    }


};
VatomicFaceManager.init();
module.exports = VatomicFaceManager;


