var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");

var CoinFace = function(vatomView,face)
{
    Face.call(this, vatomView,face);
};
// Subclass
CoinFace.prototype = Object.create(Face.prototype);
CoinFace.prototype.constructor = CoinFace;
CoinFace.prototype.onResize =function()
{

    if(this.outer&&this.div) {
        this.outer.style.width = Math.min(200,this.div.clientWidth)+ "px";
        this.outer.style.height = this.outer.style.width;
        this.text.style.fontSize=(Math.floor((parseInt(this.outer.style.width.replace("px"))/25)*10))+"px";
    }
};

CoinFace.prototype.onLoad = function()
{
    Face.prototype.onLoad.call(this);
    if(!this.vatom||!this.face)
    {
        this.showError();
        return;
    }
    this.div = document.createElement("div");
    var div = this.div;
    div.style.width = "100%";
    div.style.height = "100%";

    this.outer = document.createElement("div");
    var outer = this.outer;
    outer.style.margin = '0';
    outer.style.position='absolute';
    outer.style.top='50%';
    outer.style.left=' 50%';
    outer.style.transform='translate(-50%, -50%)';
    outer.style.borderRadius = "50%";
    outer.style.backgroundColor="#F7A820";
    outer.style.maxWidth = "250px";
    outer.style.maxHeight = "250px";


    var mid = document.createElement("div");
    mid.style.margin = '0';
    mid.style.position='absolute';
    mid.style.top='50%';
    mid.style.left=' 50%';
    mid.style.transform='translate(-50%, -50%)';
    mid.style.borderRadius = "50%";
    mid.style.backgroundColor="#E48100";
    //inner.style.border = "10% solid #F28700";
    mid.style.width = "80%";
    mid.style.height = "80%";

    var inner = document.createElement("div");
    inner.style.margin = '0';
    inner.style.position='absolute';
    inner.style.top='50%';
    inner.style.left=' 50%';
    inner.style.transform='translate(-50%, -50%)';
    inner.style.borderRadius = "50%";
    inner.style.backgroundColor="#F08700";
    inner.style.width = "95%";
    inner.style.height = "95%";

    this.text =  document.createElement("div");
    var text= this.text;
    text.style.color='#FAA639';


    text.style.position='absolute';
    text.style.top='50%';
    text.style.left=' 50%';
    text.style.transform='translate(-50%, -50%)';
    text.innerHTML = this.vatom.properties.MyCommerce.pricing.value.price;


    inner.appendChild(text);
    mid.appendChild(inner);
    outer.appendChild(mid);
    div.appendChild(outer);
    this.show(div);
};

module.exports = CoinFace;



