var Request = require("../Request");
var Events =require("../Events");

var CachedUsers = {};

/** @class Refers to a Vatom user. This could be a registered user on Vatomic, or a guest accout, etc. */
function VatomUser() {

	// Setup vars
	this.userID					= null;
	this.firstName				= null;
	this.lastName				= null;
	this.email					= null;
	this.emailVerified			= false;
	this.phoneNumber			= null;
	this.phoneNumberVerified	= false;
	this.isCurrentUser 			= false;
	this.avatarURL				= null;
	this.accountActivated		= true;
	this.isFacebookLinked		= false;

}

/** @static Create a user with the specified ID */
VatomUser.withID = function(id) {

	// Check if in cache
	if (CachedUsers[id])
		return CachedUsers[id];

	// Create user object
	var user = new VatomUser();
	user.userID = id;

	// Store it
	CachedUsers[id] = user;

	// Return it
	return user;

}

/** Gets the most human-friendly display name for this user. */
Object.defineProperty(VatomUser.prototype, "displayName", {
	get: function() {

		// Get full name if possible
		if (this.firstName || this.lastName)
			return ((this.firstName || "") + " " + (this.lastName || "")).trim();

		// Check for email
		if (this.email)
			return this.email;

		// Check for phone number
		if (this.phoneNumber)
			return this.phoneNumber;

		// Unknown user
		return "...";

	}
});

/** Checks if this is a guest account */
Object.defineProperty(VatomUser.prototype, "isGuest", {
	get: function() {

		// Check if we have an email
		if (this.email)
			return false;

		// Check if we have a phone number
		if (this.phoneNumber)
			return false;

		// Check if we have a linked Facebook account
		if (this.isFacebookLinked)
			return false;

		//  No other entry point, we are in guest mode
		return true;

	}
});

/** Fetch the profile information for this user. @returns Promise. */
VatomUser.prototype.fetchProfile = function() {

	// Check if we either have a userID or if we are the current user
	if (!this.userID && !this.isCurrentUser)
		return Promise.reject({ code: "NO_USER_DETAILS", message: "There is no user ID in this VatomUser." });

	// Send request
	var endpoint = (this.isCurrentUser ? "/currentuser" : "/user/" + this.userID);
	return Request.get(endpoint).then(data => {

		// Success, get new properties
		var props 					= data.properties || {};
		var systemProps 			= data.system_properties || {};

		// Update user vars
		this.userID 				= data.id || this.userID;
		this.email					= props.email || this.email;
		this.firstName				= props.first_name || this.firstName;
		this.lastName				= props.last_name || this.lastName;
		this.phoneNumber			= props.phone_number || this.phoneNumber;

		// Get avatar URI
		this.avatarURL				= props.avatar_uri || this.avatarURL;

		// Get system properties
		this.accountActivated		= systemProps.activated;
		this.emailVerified			= systemProps.email_confirmed;
		this.phoneNumberVerified	= systemProps.phone_confirmed;

		////console.error("UserUpdated",this)
		this.onUpdated();

		// Return this uVatomUser as the result of the promise
		return this

	})

};

/** @private Called when the user profile is updated */
VatomUser.prototype.onUpdated = function() {

	// Notify listeners
	Events.callEvent("user.updated",this);

}

module.exports = VatomUser;



