//
// RootView - This class represents the root of a View hierarchy
var View = require("./View");
var ResizeSensor = require("../../../Lib/ResizeSensor");
/** @class This class represents the root of a View heirarchy. This class exposes a DIV, and manages resizing etc automatically. */
function RootView() {

	this.devName = "";
	// Create container div
	this.element = document.createElement("div");
	this.element.className = "vatom-root-view";
	this.element.style.cssText = "display: block; position: relative; ";

	this.resizeSensor =new ResizeSensor(this.element,this.onResize.bind(this));
	

	// Create root view
	this.view = new View();
	this.element.appendChild(this.view.element.get && this.view.element.get(0) || this.view.element);
	
	// Register resize listeners
	var _this = this;

	setTimeout(this.onResize.bind(this), 0);
	this.onResize();
	//detect element size changes
	//window.addEventListener("resize", this.onResize.bind(this));

	/*this.element.onscroll = new function(){
		//console.log("resize on scroll");
		_this.onResize();//.bind(_this);
	};*/
	
}

/** @private Called when the DIV element changes size */
RootView.prototype.onResize = function() {

	// Check for changes
	var newWidth = this.element.offsetWidth || 0;
	var newHeight = this.element.offsetHeight || 0;
	//if (newWidth == this._lastWidth && newHeight == this._lastHeight)
	//	return;
	//console.log("resizing root "+this.devName+" "+newWidth+"x"+newHeight);
	this.view.setSize(newWidth, newHeight);
	this.view.setSize(newWidth, newHeight);
	////console.log("Vatomic SDK: HERE");
	
}
module.exports=RootView;


