//
// ExchangeInventory - This class queries the exchange

var Inventory = require("./Inventory.js");
var VatomDiscoverFilter = require("./VatomDiscoverFilter.js");
var RequestHelper = require("./RequestHelper.js");

function ExchangeInventory(exchangeFilter) {
    Inventory.call(this, this.doFetch.bind(this));

    // Store properties
    this.exchangeFilter = exchangeFilter || new VatomDiscoverFilter();
    this.exchangeUserID = null;

}

// Subclass
ExchangeInventory.prototype = Object.create(Inventory.prototype);
ExchangeInventory.prototype.constructor = ExchangeInventory;

/** @private Called to refresh the contents of the inventory */
ExchangeInventory.prototype.doFetch = function(onSuccess, onFail, pageToken, pageCount) {

    // Check if got exchange user
    if (!this.exchangeUserID) {

        // Only once at a time
        if (this.busy) return;
        this.busy = true;

        // Fetch user ID
        RequestHelper.getUserID({ email: "exchange@vatomic.io" }, function(data) {

            // Done, store user ID
            this.busy = false;
            this.exchangeUserID = data.id;
            onFail();

            // Do another fetch
            if (this.exchangeUserID)
                this.refresh();

        }.bind(this), function(errorCode, errorText) {

            // Failed to get user ID
            this.busy = false;
            onFail(errorCode, errorText);

        }.bind(this));
        return;

    }

    // Create copy of discover filter with info added
    var filter = this.exchangeFilter.duplicate();
    filter.setScope(VatomDiscoverFilter.FieldOwner, this.exchangeUserID);
    filter.appendFilter(VatomDiscoverFilter.FieldTemplateVariation, "vatomic.exchange::vAtomicExchange::Trade");

    // Call our fetch function
    filter.createInventoryFetchFunction()(onSuccess, onFail, pageToken, pageCount);

}



// Export the class
module.exports = ExchangeInventory;



