var VatomFaceView = require("./VatomFaceView");
var ResizeSensor = require("../Lib/ResizeSensor");
var Store = require("../Code/VatomicStore");
//var VatomicFaceManager = require("../Code/VatomicFaceManager");
var Face = require("./NativeFaces/Face");
var Events = require("../Code/Events");
var VatomResourceManager = require('../Code/Resources/VatomResourceManager');
var Vatom = require("../Code/Vatom");

import Log from '../Code/Log'

// TODO: The constraints are passed in from the host. Rather we should build them ourselves, with an enum for the view mode.

/**
 * View for presenting a vatom's content. This class provides a DIV element you can insert into your DOM to display the vatom.
 * @class
 * @param {Vatom} vatom The vatom to display
 */
module.exports = class VatomView {

    constructor(vatom, defaultConstraints, defaultProp) {

        // Sanity check on vatom object
        if (!(vatom instanceof Vatom))
            throw new TypeError("The first parameter to the VatomView constructor must be a Vatom object.");

        // Vars
        this.vatom = vatom;
        this.constraints = defaultConstraints || {};
        this.constraints.platform = "web";
        this.properties = defaultProp || {};
    	this.viewMode = this.constraints.view_mode || "icon";
    	this.inContract = vatom && vatom.properties && vatom.properties.in_contract;
        this.rejectFaces = this.properties.rejectFaces || [];

        // `rejectFaces` is a function. If the user supplied an array, convert that to a reject function instead.
        if (this.rejectFaces instanceof Array) {
            var arr = this.rejectFaces;
            this.rejectFaces = faceURL => arr.includes(faceURL);
        }

    	// InContract icon
    	this.contract = document.createElement("div");
    	this.contract.style.backgroundSize = "16px 16px";
    	this.contract.style.backgroundPosition = "center";
    	this.contract.style.backgroundRepeat = "no-repeat";
    	this.contract.style.backgroundImage = "url('" + require("./VatomView.InExchange.svg") + "')";
    	this.contract.style.position = "absolute";
    	this.contract.style.top = "10px";
    	this.contract.style.left = "10px";
    	this.contract.style.width = "16px";
    	this.contract.style.height = "16px";
    	this.contract.style.zIndex = "99";
    	this.contract.style.padding = "2px";
    	this.contract.style.backgroundColor = "#ffffff";
    	this.contract.style.borderRadius = "18px";

        // Create icon view
        this.element = document.createElement("div");
        this.element.style.position = "relative";
        this.element.style.width = "100%";
        this.element.style.height = "100%";

    	// Add listeners
        this.resizeSensor = new ResizeSensor(this.element, this.onResize.bind(this), this.onAttach.bind(this));
    	Events.registerEvent("vatom.update-complete", this._onVatomUpdate.bind(this));

    	// Show loading face
        this.face = this.pickBestFace(this.constraints);
        var faceView = new Face();
        faceView.showLoading();
    	this.setFaceView(faceView);

    	// Update UI
    	this._refreshUI();

    }



    /** Called when the vatom's properties are updated */
    _onVatomUpdate(vatom){


    	if(vatom&&vatom.identifier == this.vatom.identifier) {
    		this.vatom = vatom;
    		this.inContract = this.vatom.properties&&this.vatom.properties.in_contract;
    	   // console.log('in contract',this.inContract );
    		//console.log(this.face);
    		this.faceView.onVatomUpdated();
    		this._refreshUI();
    	}

    }

    /** Updates the UI components, such as the in-contract overlay icon etc */
    _refreshUI() {

    	// Check if should show in contract icon
    	if (this.inContract && this.viewMode == "icon") {

    		// Show it
    		if (this.contract.parentElement != this.element)
    			this.element.appendChild(this.contract);

        } else {

    		// Hide it
            if (this.contract.parentElement == this.element)
    			this.element.removeChild(this.contract);

    	}

    }



    onAttach() {
    	this.refreshFace();
    }

    /** Refreshes the currently displayed face, picking a new one if needed */
    refreshFace() {

    	// TODO: Commented out the FaceManager for now, need to change it to pass this VatomView to the faces instead of the individual properties, as well as using the native faces array
        //VatomicFaceManager.getFace(this.vatom, this.face, this.properties, this.setFaceView.bind(this));

    	// Get best face
    	var faceProps = this.pickBestFace(Object.assign({
    		view_mode: this.viewMode,
    		platform: "web"
    	}, this.constraints));

        // Stop if there was no face chosen
        if (!faceProps)
            return Log.warn("VatomView", "No face found for vatom", this.vatom);

    	// Check if there's a change
    	if (this.faceURL == faceProps.properties.display_url)
    		return;

        // Log new face
        Log.debug("VatomView", "Picked face " + faceProps.properties.display_url + " for vatom " + this.vatom.identifier);

    	// Find constructor
    	var handler = VatomView.NativeFaces[faceProps.properties.display_url];

    	// If no constructor and this is an HTTP display URL, use the HTMLFace class
    	if (!handler && faceProps.properties.display_url.indexOf("http") == 0)
    		handler = (vatomView, faceProps, cb) => require(["./NativeFaces/HTMLFace"], Face => cb(new Face(vatomView, faceProps)));

    	// If still no handler, this is a bug
    	if (!handler)
    		return console.error("No handler found to create the face view! This is a bug, or the URL type is unsupported. " + faceProps.properties.display_url);

    	// Create face view
    	handler(this, faceProps, face => {
    		this.face = faceProps;
    		this.setFaceView(face);

    		///start resources downloading that face might need
    		// TODO: Implement a Resource.load() promise function and then make all faces use it.
    		if (faceProps.properties.resources) {
    			for (var i = 0; i < faceProps.properties.resources.length; i++) {
    				VatomResourceManager.downloadResource(this.vatom.resources[faceProps.properties.resources[i]]);
    			}
    		}

    	});

    }

    onLoad() {


    }

    onError() {


    }

    onResize() {

        if (this.faceView) {
            this.faceView.onResize();
        }
    }


    setFaceView(faceView) {

        //console.log(faceView);
        if (faceView && this.faceView != faceView) {
            if (this.faceView) {
                this.element.removeChild(this.faceView.element);
                this.faceView.free();
            }
            this.faceView = faceView;
            this.element.appendChild(this.faceView.element);
            this.faceView.onLoadComplete = this.onLoad;
            this.faceView.onLoadError = this.onError;
            this.faceView.onLoad();

        }
    }


    viewChanged(iconView, containerView, isFullscreen) {
        this.faceView && this.faceView.viewChanged(iconView, containerView, isFullscreen);
    }

    /**
     * Gets the best vatom face for provided constraints
     * @param constraints
     * @returns {*}
     */
    pickBestFace(constraints) {

        // Check values
        if (!this.vatom.faces || this.vatom.faces.length == 0)
            return null;

        // Default face if no faces found
        var bestFace = {
            properties: {
                display_url: "native://image",
                resources: [],
                constraints: {
                    view_mode:"icon"
                }
            }
        };

        // Pick best match
        var match = 0;
        for (var i = 0; i < this.vatom.faces.length; i++) {
            var face = this.vatom.faces[i];
            var checkMatch = 0;

            // Skip face if it's rejected
            if (this.rejectFaces(face.properties.display_url))
                continue;

            //ignore webview icon faces
            if (!Store.DEFAULTS.ALLOW_ICON_WEBVIEW_FACE && constraints && constraints["view_mode"] && constraints["view_mode"] == "icon" && face.properties.display_url.indexOf("native://") == -1) {
                continue;
            }

            /*if (constraints["view_mode"] == "icon" && face.properties.display_url.indexOf("native://generic-3d") > -1&&prop&&prop.NO_3D) {
                continue;
            }*/

            for (var key in constraints) {


                if (face.properties.constraints[key]) {


                    var values = constraints[key].split('|');
                    var value = values.indexOf(face.properties.constraints[key]);

                    if (face.properties.constraints[key] == 'all' || face.properties.constraints[key] == 'generic' || face.properties.constraints[key] == '' || (value > -1)) {

                        checkMatch += values.length - value;
                        if (face.properties.display_url.indexOf("native:") == 0)
                            checkMatch++;

                    }
                    else {
                        //constraint doesn't match can't use face
                        checkMatch = 0;
                        break;
                    }
                }
            }

            if (checkMatch > match) {
                //console.log(face);
                bestFace = face;
                match = checkMatch;
            }
        }
        // if (!Store.DEFAULTS.ALLOW_ICON_WEBVIEW_FACE && constraints && constraints["view_mode"] && constraints["view_mode"] == "icon" && bestFace.properties.display_url.indexOf("native://") == -1) {
        //     return null;
        // }
        return bestFace;

    }

    /** Sets generic face property*/
    setProperty(key, value) {
        // Store value
        this.properties[key] = value;
        // Update face view
        if (this.faceView)
            this.faceView.setProperty(key, value);

    }

    /** Returns property for given key*/
    getProperty(key) {
        return this.properties[key];
    }

    /** You should call this once you are finished with this VatomView. This releases the memory and views used. Once this has been called, you can't use this class any more. */
    free() {

        this.faceView && this.element.removeChild(this.faceView.element) && this.faceView.free();
        this.onLoad = null;
        this.onError = null;
    }

    /** Checks if we can display faces with the specified view mode */
    supportsViewMode(mode) {

    	// Separate by pipe character
    	return this.viewMode.split("|").indexOf(mode) > -1;

    }

}

/** This object stores the map between native:// URLs and the Face constructors handlers to use to create that face. Use Vatomic.registerFace() to add new native faces. */
module.exports.NativeFaces = {};



