var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var EventListener = require("../../Code/EventListener");
var VatomicWebSocketListener = require("../../Code/VatomicWebSocketListener");
var VatomicFaceManager = require("../../Code/VatomicFaceManager");
var RequestHelper = require("../../Code/RequestHelper");
var Request = require("../../Code/Request");
var HTMLFace = function (vatomView, face) {
    Face.call(this, vatomView, face);

    this.socketListener = new VatomicWebSocketListener();
    this.socketListener.pause();
    this.socketListener.onRPC = this.RPC.bind(this);
    this.socketListener.start();

    this.eventListener = new EventListener();
    this.eventListener.pause();
    Events.addListener(this.eventListener);


    this.iframe = document.createElement("iframe");


    this.iframe.style.cssText = "display: block; position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; overflow: hidden; border: none; background: none; outline: none;z-index:0; ";
    // var display_url = (this.face.properties.display_url.indexOf(".html")!=-1&&this.face.properties.display_url)||(this.face.properties.display_url[this.face.properties.display_url.length-1]=='/')?this.face.properties.display_url+"index.html":(this.face.properties.display_url+"/index.html");
    // //console.log(display_url);
    this.iframe.setAttribute('src', this.face.properties.display_url);

    this.iframe.onload = function () {
        this.registerBridge();
        this.socketListener.resume();
        this.eventListener.resume();

        setTimeout(function () {
            this.hideLoading();
        }.bind(this), 5000)

    }.bind(this);

    this.showLoading();

    this.show(this.iframe, true);

    if (this.properties['html-gestures-disabled'] == true) {
        var clickGrabber = document.createElement('img');
        clickGrabber.src = "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAQAAAC1HAwCAAAAC0lEQVR42mNkYAAAAAYAAjCB0C8AAAAASUVORK5CYII=";
        clickGrabber.style.width = "100%";
        clickGrabber.style.height = "100%";
        clickGrabber.style.position = 'absolute';
        clickGrabber.style.left = '0';
        clickGrabber.style.top = '0';
        clickGrabber.style['z-index'] = '1';
        this.show(clickGrabber, true);
    }

};

// Subclass
HTMLFace.prototype = Object.create(Face.prototype);
HTMLFace.prototype.constructor = HTMLFace;

HTMLFace.prototype.combinePossible = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.sendMessage("vatom.combine.possible", HTMLFace.encodeVatom(data.vatom));
    }

};
HTMLFace.prototype.combineCancel = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.sendMessage("vatom.combine.cancelled", HTMLFace.encodeVatom(data.vatom));
    }
};
HTMLFace.prototype.combine = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.sendMessage("vatom.combine", HTMLFace.encodeVatom(data.vatom));
    }
};

HTMLFace.prototype.onLoad = function () {


    //console.log("onload...");
    this.eventListener.registerEvent("vatom.update-complete", this.vatomStateChanged.bind(this));
    this.eventListener.registerEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.registerEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.registerEvent("vatom.combine", this.combine.bind(this));

    if (this.iframe && this.iframe.contentWindow) {
        this.registerBridge();
        this.socketListener.resume();
        this.eventListener.resume();
        this.onLoadComplete();
    }
    else if (this.iframe) {

    }

};
HTMLFace.prototype.onUnload = function () {

    this.socketListener.stop();
    this.eventListener.unregisterEvent("vatom.update-complete", this.vatomStateChanged.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.unregisterEvent("vatom.combine", this.combine.bind(this));
    this.eventListener.free();
    this.iframe.onload = null;
    Events.removeListener(this.eventListener);
    this.unregisterBridge();
};


HTMLFace.prototype.registerBridge = function () {
    this.unregisterBridge();//remove if already registered
    // Add bridge response listener
    window.addEventListener("message", this.onIncomingBridgeMessage.bind(this));
};
HTMLFace.prototype.unregisterBridge = function () {
    window.removeEventListener("message", this.onIncomingBridgeMessage.bind(this));
};
/** @private Called when a message arrives over the bridge */
HTMLFace.prototype.onIncomingBridgeMessage = function (event) {

    // Get payload
    var payload = event.data;

    if (!payload || !this.iframe || event.source != this.iframe.contentWindow) return;
    // Check source is from this face's iframe

    var responseID = null;
    if (payload.responseID) {
        responseID = payload.responseID;
    }

    // Check message type
    if (payload.name == "vatom.init") {

        this.hideLoading();
        // Create response payload
        var resources = {};


        for (var res in this.vatom.resources) {
            resources[res] = this.vatom.resources[res].URL;
        }

        var data = {

            vatomInfo: {
                id: this.vatom.identifier,
                properties: this.vatom.properties,
                resources: resources,
                faceProperties: this.face.properties
            }
        };
        this.sendMessage("input.pan-register", {});
        this.sendMessage("input.tap.double-register", {});
        this.sendMessage(responseID || "vatom.init-complete", data);
    }

    else if (payload.name == "vatom.children.get") {

        // Fetch children
        this.vatom.listChildren(function (vatoms, errorCode, errorText) {

            // Create list of vatom info
            var vatomInfos = [];
            for (var i = 0; i < vatoms.length; i++)
                vatomInfos.push(HTMLFace.encodeVatom(vatoms[i]));

            // Send vatom info back over the bridge
            this.sendMessage(responseID || "vatom.children.get-response", {
                "items": vatomInfos
            });

        }.bind(this));
    }

    else if (payload.name == "vatom.transfer.actions.set" && payload.data && payload.data.actions) {

        this.vatom.transferActions = payload.data.actions;
    }
    else if (payload.name == "vatom.patch") {

        this.vatom.patch(payload.data);
    }
    else if (payload.name == "vatom.combine-complete") {

        Events.callEvent("vatom.combine-complete", payload.data.id);
    }
    else if (payload.name == "vatom.rpc.call") {
        ///sends on payload to all faces
        Events.callEvent("websocket.rpc", payload.data);
    }
    else if (payload.name == "vatom.performAction") {

        RequestHelper.currentUserAction(payload.data.actionName, payload.data.actionData, function (data) {

                //console.log("perform action result",data);
                this.sendMessage(responseID || "vatom.performAction-complete", data);
            }.bind(this),
            function (errorCode, errorText) {
                //console.log("perform action error",errorCode,errorText);
                this.sendMessage(responseID || "vatom.performAction-complete", {
                    errorCode: errorCode,
                    errorText: errorText
                });
            }.bind(this)
        );

    }
    else if (payload.name == "user.profile.fetch") {


        //console.log("fetch profile",payload.data);
        if (payload.data.userID) {
            payload.data.uid = payload.data.userID;
        }

        RequestHelper.getUsername(payload.data, function (data) {

            this.sendMessage(responseID || "user.profile.fetch", {
                firstName: data.first_name,
                lastName: data.last_name
            });

        }.bind(this), function (code, errorText) {
            this.sendMessage(responseID || "user.profile.fetch-complete", {
                errorCode: errorCode,
                errorText: errorText
            });

        }.bind(this));

    }
    else if (payload.name == "user.avatar.fetch") {
        if (payload.data.userID) {
            payload.data.uid = payload.data.userID;
        }

        RequestHelper.getAvatar(payload.data, function (data) {

            this.sendMessage(responseID || "user.avatar.fetch", {avatarURL: data.avatar_uri});

        }.bind(this), function (code, errorText) {
            this.sendMessage(responseID || "user.profile.fetch-complete", {
                errorCode: errorCode,
                errorText: errorText
            });

        }.bind(this));

    }
    else if (payload.name == "vatom.view.presentCard") {
        Events.callEvent("vatom.view.presentCard",this.vatom);
    }
    else if (payload.name == "vatom.view.close") {
        Events.callEvent("vatom.view.close",this.vatom);
    }
    //`vatom.view.presentCard` : If the card is currently in "peek" mode, then bring it up
    //- `vatom.view.close`

};

HTMLFace.prototype.viewChanged = function (iconView, containerView, isFullscreen) {
    var payload = {
        viewObject: iconView,
        container: containerView,
        isFullscreen: isFullscreen
    };
    this.sendMessage("vatom.view.changed", payload)
};

/** @private @static Encode vatom info for sending over the bridge */
HTMLFace.encodeVatom = function (vatom) {

    // Create resource list
    var resources = {};
    for (var res in vatom.resources)
        resources[res] = vatom.resources[res].URL;

    // Create payload
    return {
        "vatomInfo": {
            "id": vatom.identifier,
            "properties": vatom.properties,
            "resources": resources
        }
    }

}

HTMLFace.prototype.sendMessage = function (name, data) {

    if (!this.iframe || !this.iframe.contentWindow) {
        //console.warn("can;t send message via bridge since either iframe is missing or not loaded");
        return;
    }


    var resp = {
        source: "VatomicSDK",
        name: name,
        data: data
    };
    // Send payload
    this.iframe.contentWindow.postMessage(resp, "*");
};
HTMLFace.prototype.RPC = function (data) {

    if (data && data.template_variation && data.rpc && data.template_variation == this.vatom.templateVariation) {
        this.sendMessage("vatom.rpc.incoming", {rpc: data.rpc})
    }
};
HTMLFace.prototype.vatomStateChanged = function (vatom) {
    if (vatom.identifier == this.vatom.identifier && this.face) {
        this.vatom = vatom;
        var resources = {};

        for (var res in this.vatom.resources) {
            resources[res] = this.vatom.resources[res].URL;
        }

        var data = {
            vatomInfo: {
                id: this.vatom.identifier,
                properties: this.vatom.properties,
                resources: resources,
                faceProperties: this.face.properties
            }
        };
        this.sendMessage("vatom.update", data);
    }
};
HTMLFace.prototype.hideLoading = function () {
    Face.prototype.hideLoading.call(this);
    this.iframe && (this.iframe.style.opacity = '1');

};
HTMLFace.prototype.show = function (view, dontClear) {
    Face.prototype.show.call(this, view, dontClear);
    this.iframe && (this.iframe.style.opacity = '0.001');
};
module.exports = HTMLFace;



