function ScriptEvent()
{
    this.cancelled = false;
    this.name = "";
    this.target = null;

}
ScriptEvent.prototype.cancel = function()
{
    this.cancelled = true;
};
ScriptEvent.prototype.preventDefault = function()
{
    this.canceled = true;
};

module.exports=ScriptEvent;



