 var VatomResource = require('./VatomResource');

var VatomTextResource = function VatomTextResource(url)
{
    VatomResource.call(this,url);
    this.text = null;
};

// Subclass
VatomTextResource.prototype = Object.create(VatomResource.prototype);
VatomTextResource.prototype.constructor = VatomTextResource;
/** Starts loading resource */
VatomTextResource.prototype.load = function()
{
    if(this.state==VatomResource.states.Loading)
    {
        return;//already loading
    }
    this.state = VatomResource.states.Loading;

    ResourceManager.download(this);

}

/** @private Load the image resource */
VatomTextResource.prototype.onLoadedData=function() {

    // Got data, convert to text
    if (this.data) {

        // Save text
        this.text = this.data;

        // Notify change
        this.onTextLoaded();

    } else {

        // Failed
        //console.log("Unable to decode text data as UTF8 for "+this.URL);

        // Notify change
        this.state =VatomResource.states.Error;

    }

};

/** @private Called when the text is loaded */
VatomTextResource.prototype.onTextLoaded=function() {

    // Notify change
    this.state = VatomResource.states.Loaded;

};

VatomTextResource.prototype.getBlob=function(callback)
{
    if(!callback&&!this.state==VatomResource.states.Loaded)return false;

    callback(new Blob([this.text],{type:"text"}));

    return true;

};
module.exports = VatomTextResource;



