//
// Manages the list of threads for the current user
//
// Events:
// - `change` : Triggered when the loading state of this manager or any of it's threads changes

import EventEmitter from '../Utils/EventEmitter'
import Request from '../Request'
import MessageThread from './MessageThread'

export default class MessageThreadManager extends EventEmitter {

    constructor() {
        super();

        // Properties
        this.threads = [];

        // Refresh
        this.refresh();

    }

    get isUpdating() {
        return !!this._fetchPromise;
    }

    /** Fetch updates from the server */
    refresh() {

        // Only do one request at a time
        if (this._fetchPromise)
            return this._fetchPromise;

        // Create payload
        var payload = {
            count: 50
        }

        // Do request
        this._fetchPromise = Request.post("/analytics/mythreads", payload).then(data => {

            // Process data
            for (var threadInfo of data && data.threads || []) {

                // Add thread info
                this.addThread(threadInfo);

            }

            // Done
            this._fetchPromise = null;
            this.emit("change");
            return this;

        }).catch(err => {

            // Re-throw errors
            this._fetchPromise = null;
            this.emit("change");
            throw err;

        });

        // Notify changed
        this.emit("change");
        return this._fetchPromise;

    }

    /** @private Add thread data */
    addThread(data) {

        // Check if thread exists
        var thread = this.threads.find(t => t.id == data.name);
        if (thread) {

            // Thread already exists, update it if it's date is newer
            if (thread.latestDate.getTime() < data.when_modified / 1000000) {
                thread.latestDate = new Date(data.when_modified / 1000000);
                thread.refresh();
            }

        } else {

            // Thread doesn't exist, create it
            thread = new MessageThread(data.name, this);
            thread.latestDate = new Date(data.when_modified / 1000000);

            // Add it to the list
            this.threads.push(thread);

            // Sort threads
            this.threads.sort((a, b) => b.latestDate.getTime() - a.latestDate.getTime());

            // Load thread messages
            thread.refresh();

        }

    }

}



