var addCSSRule = require('./CSSLoader');
var ResizeSensor = function (parent, callback, insert) {
    this.callback = callback;
    this.insert = insert;

    addCSSRule("@keyframes nodeInserted", "from { opacity: 0.99; } to { opacity: 1; }");
    addCSSRule("@-webkit-keyframes nodeInserted", "from { opacity: 0.99; } to { opacity: 1; }");

    var style = 'position: absolute; left: 0; top: 0; right: 0; bottom: 0; overflow: hidden; z-index: -1; visibility: hidden; -webkit-animation: nodeInserted 0.001s; animation: nodeInserted 0.001s;'

    var styleChild = 'position: absolute; left: 0; top: 0; transition: 0s;';
    this.element = document.createElement('div');
    this.element.className = "resize-sensor";
    this.element.style.cssText = style;
    this.expandSensor = document.createElement('div');
    this.expandSensor.className = "expand-sensor";
    this.expandSensor.style.cssText = style;
    this.expandChild = document.createElement('div');
    this.expandChild.style.cssText = styleChild;
    this.expandSensor.appendChild(this.expandChild);
    this.shrinkSensor = document.createElement('div');
    this.shrinkSensor.className = "shrink-sensor";
    this.shrinkSensor.style.cssText = style;
    this.shrinkSensor.innerHTML = '<div style="' + styleChild + 'width: 200%; height: 200%"></div>';

    this.element.appendChild(this.expandSensor);
    this.element.appendChild(this.shrinkSensor);
    this.oldWidth = 0;
    this.oldHeight = 0;


    if (this.element.attachEvent) {

        this.element.attachEvent("onwebkitAnimationEnd", this.onInsert.bind(this));
        this.element.attachEvent("onanimationend", this.onInsert.bind(this));

        this.expandSensor.attachEvent('onscroll', this.onResize.bind(this));
        this.shrinkSensor.attachEvent('onscroll', this.onResize.bind(this));
    }
    else {
        this.element.addEventListener("webkitAnimationEnd", this.onInsert.bind(this));
        this.element.addEventListener("animationend", this.onInsert.bind(this));

        this.expandSensor.addEventListener('scroll', this.onResize.bind(this));
        this.shrinkSensor.addEventListener('scroll', this.onResize.bind(this));
    }

    parent.appendChild(this.element);

};
ResizeSensor.prototype.onInsert = function () {
    if (!this.lastInsert || (this.lastInsert - new Date().getTime() )> 1000) {
        this.lastInsert = new Date().getTime();
        if (this.insert) {
            this.insert();
        }
        this.reset();
    }
};

ResizeSensor.prototype.onResize = function () {

    var width = this.element.clientWidth;
    var height = this.element.clientHeight;

    if (width != this.oldWidth || height != this.oldHeight) {
        this.oldHeight = height;
        this.oldWidth = width;

        if (this.callback) {
            //console.log("Resizing");
            this.callback();
        }
        this.reset();
    }

};

ResizeSensor.prototype.reset = function () {
    this.expandChild.style.width = 100000 + 'px';
    this.expandChild.style.height = 100000 + 'px';

    this.expandSensor.scrollLeft = 100000;
    this.expandSensor.scrollTop = 100000;

    this.shrinkSensor.scrollLeft = 100000;
    this.shrinkSensor.scrollTop = 100000;
};

module.exports = ResizeSensor;


