//
// Log output to the console

class Log {

    /** Log level */
    log(module, text) {

        // Create output string
        var out = "[VatomicSDK > " + module + "] " + text;
        var args = [];
        args.push(out);
        for (var i = 2 ; i < arguments.length ; i++)
            args.push(arguments[i]);

        // Output
        console.log.apply(console, args);

    }

    /** Info level */
    info(module, text) {

        // Create output string
        var out = "I: [VatomicSDK > " + module + "] " + text;
        var args = [];
        args.push(out);
        for (var i = 2 ; i < arguments.length ; i++)
            args.push(arguments[i]);

        // Output
        console.info.apply(console, args);

    }

    /** Debug level */
    debug(module, text) {

        // Create output string
        var out = "D: [VatomicSDK > " + module + "] " + text;
        var args = [];
        args.push(out);
        for (var i = 2 ; i < arguments.length ; i++)
            args.push(arguments[i]);

        // Output
        console.debug.apply(console, args);

    }

    /** Warning level */
    warn(module, text) {

        // Create output string
        var out = "W: [VatomicSDK > " + module + "] " + text;
        var args = [];
        args.push(out);
        for (var i = 2 ; i < arguments.length ; i++)
            args.push(arguments[i]);

        // Output
        console.warn.apply(console, args);

    }

    /** Error level */
    error(module, text) {

        // Create output string
        var out = "E: [VatomicSDK > " + module + "] " + text;
        var args = [];
        args.push(out);
        for (var i = 2 ; i < arguments.length ; i++)
            args.push(arguments[i]);

        // Output
        console.error.apply(console, args);

    }

}

export default new Log();



