var SocketInventory = require("./SocketInventory");
var Events = require("./Events");
var RequestHelper = require("./RequestHelper");
var Vatom = require("./Vatom");

// TODO: Improve these Inventory classes! Their interface is not good at the moment. There should only be one.
var VatomicInventory = function (fetchVatoms) {

    // Create default fetch function if none was passed in.
    fetchVatoms = fetchVatoms || function (onSuccess, onFail, token, pageCount) {

        RequestHelper.getInventory('', token, pageCount, function (data) {
            var vatoms = data.map(d => new Vatom(d));
            onSuccess(vatoms);
        }, onFail);

    };


    SocketInventory.call(this, fetchVatoms);
    this.transferringVatoms = [];
    this.tempVatoms = [];
    Events.registerEvent("vatom.combine", this.combine.bind(this));
    Events.registerEvent("vatom.combine-success", this.combineSuccess.bind(this));
    Events.registerEvent("vatom.combine-failed", this.combineFailed.bind(this));
    //Events.registerEvent("vatom.transfer-start",this.vatomTransferStart.bind(this));
    // Events.registerEvent("vatom.transfer-complete",this.vatomTransferComplete.bind(this));
    //Events.registerEvent("vatom.transfer-failed",this.vatomTransferFailed.bind(this));

};
// Subclass
VatomicInventory.prototype = Object.create(SocketInventory.prototype);
VatomicInventory.prototype.constructor = VatomicInventory;

VatomicInventory.prototype.combine = function (vatomid) {

    if (!vatomid) return;

    var idx = this.indexOf(vatomid);

    if (idx > -1) {
        this.tempVatoms.push(this.vatoms[idx]);
        //console.log("combine");
        this.removeVatom(this.vatoms[idx]);
    }
};
VatomicInventory.prototype.combineFailed = function (vatomid) {

    if (!vatomid) return;


    var idx = this.indexOfTemp(vatomid);
    //console.log("combine fail");
    // Find it
    if (idx > -1) {

        var v = this.tempVatoms[idx];
        // Remove from temporary storage
        this.tempVatoms.splice(idx, 1);
        // Add vatom
        this.addVatom(v);
    }
};

VatomicInventory.prototype.combineSuccess = function (vatomid) {
    if (!vatomid) return;
    // Find it
    //console.log("success");
    var idx = this.indexOfTransfer(vatomid);
    if (idx > -1) {
        // Remove from temporary storage
        this.tempVatoms.splice(idx, 1);
    }
};

VatomicInventory.prototype.getCategories = function () {
    var categories = ["Recent", "All vAtoms"];
    for (var i = 0; i < this.vatoms.length; i++) {
        var category = this.vatoms[i].properties.category;

        if (category && category.length > 0 && categories.indexOf(category) == -1) {
            categories.push(category);
        }
    }
    return categories;
};

/** @private When a transfer starts, remove it temporarily from the inventory */

VatomicInventory.prototype.vatomTransferStart = function (vatom) {

    if (!vatom) return;

    var idx = this.indexOf(vatom);

    if (idx > -1) {
        this.transferringVatoms.push(this.vatoms[idx]);
        this.removeVatom(this.vatoms[idx]);
    }

};

/** @private When a transfer fails, add the vatom back into the inventory */
VatomicInventory.prototype.vatomTransferFailed = function (vatom) {

    if (!vatom) return;


    var idx = this.indexOfTransfer(vatom);
    // Find it
    if (idx > -1) {

        var v = this.transferringVatoms[idx];
        // Remove from temporary storage
        this.transferringVatoms.splice(idx, 1);
        // Add vatom
        this.addVatom(v);
    }

};

/** @private On successful send, remove from temporary storage and refresh inventory */
VatomicInventory.prototype.vatomTransferComplete = function (vatom) {

    if (!vatom) return;
    // Find it
    var idx = this.indexOfTransfer(vatom);
    if (idx > -1) {
        // Remove from temporary storage
        this.transferringVatoms.splice(idx, 1);
    }

};

VatomicInventory.prototype.indexOfTransfer = function (vatom) {

    var vid = vatom.identifier || vatom;
    for (var i = 0; i < this.transferringVatoms.length; i++) {
        if (this.transferringVatoms[i].identifier == vid) {
            return i;
        }
    }
    return -1;
};
VatomicInventory.prototype.indexOfTemp = function (vatom) {

    var vid = vatom.identifier || vatom;
    for (var i = 0; i < this.tempVatoms.length; i++) {
        if (this.tempVatoms[i].identifier == vid) {
            return i;
        }
    }
    return -1;
};


VatomicInventory.prototype.getVatoms = function (filterby, filter, sortby, desc) {

    var vatoms = this.vatoms.filter(function(vatom){
        var dropped = vatom.properties.dropped;
        // var coord=vatom.properties.geo_pos.coordinates;
        return (!dropped);
    });
    if (filterby == "name") {
        vatoms = vatoms.filter(function (vatom) {
            return vatom.getName().toLowerCase().indexOf(filter) > -1;
        }.bind(this));
    }
    else if (filterby == "category") {
        if (filter == "recent") {
            vatoms = vatoms.sort(function (vatom1, vatom2) {
                return (vatom1.dateModified < vatom2.dateModified ? -1 : vatom1.dateModified > vatom2.dateModified) * -1;
            }).slice(0, 12);
        }
        else if (filter == "all vatoms") {
           // vatoms = this.vatoms.slice();
        }
        else {
            vatoms = vatoms.filter(function (vatom) {
                return vatom.properties.category && vatom.properties.category.toLowerCase().indexOf(filter) > -1;
            }.bind(this));
        }
    }
    else if (filterby == "recent") {
        vatoms = vatoms.sort(function (vatom1, vatom2) {
            return (vatom1.dateModified < vatom2.dateModified ? -1 : vatom1.dateModified > vatom2.dateModified) * -1;
        }).slice(0, 12);
    }
    else {
        //vatoms = this.vatoms.slice();
    }

    if (sortby == "name") {
        return vatoms.sort(function (vatom1, vatom2) {
            var name1 = vatom1.getName();
            var name2 = vatom2.getName();
            return (name1 < name2 ? -1 : name1 > name2) * (desc ? 1 : -1);
        });
    }
    else if (sortby == "date") {
        return vatoms.sort(function (vatom1, vatom2) {
            return (vatom1.dateModified < vatom2.dateModified ? -1 : vatom1.dateModified > vatom2.dateModified) * (desc ? -1 : 1);
        });
    }

    return vatoms;
};

VatomicInventory.prototype.free = function () {
    SocketInventory.prototype.free.call(this);
    Events.unregisterEvent("vatom.transfer-start", this.vatomTransferStart.bind(this));
    Events.unregisterEvent("vatom.transfer-complete", this.vatomTransferComplete.bind(this));
    Events.unregisterEvent("vatom.transfer-failed", this.vatomTransferFailed.bind(this));
    Events.unregisterEvent("vatom.combine-complete", this.combineComplete.bind(this));
    Events.unregisterEvent("vatom.combine", this.combine.bind(this));
    Events.unregisterEvent("vatom.combine-success", this.combineSuccess.bind(this));
    Events.unregisterEvent("vatom.combine-failed", this.combineFailed.bind(this));
};

module.exports = VatomicInventory;



