var Events = require('./Events');
var Store = require("./VatomicStore");
var Request = require('./Request');
var VatomicStore = require("./VatomicStore");
var LocalStorage = require("./LocalStorage")

import Log from './Log'

var VatomicWebSocket = {
    connection: null,
    timeout:1000,
    open: function () {

        console.log("Socket opened called");
        // do request to see if user logged in
        Request.send("GET", "/currentuser", null,null, function() {


            VatomicWebSocket.connection = new WebSocket(Store.DEFAULTS.WEBSOCKET_ADDRESS);

            VatomicWebSocket.connection.onopen = function () {
                var payload = {
                    action: "login",
                    appID: Store.DEFAULTS.APP_ID,
                    token: LocalStorage.get("vatomic.token")
                };
                VatomicWebSocket.sendMessage(JSON.stringify(payload));
                Events.callEvent("websocket.opened", '');
                console.log("Socket connection opened");
            };

            VatomicWebSocket.connection.onclose = function (e) {
                console.log('Socket is closed. Reconnect will be attempted in 1 second.', e.reason);
                setTimeout(function () {
                    VatomicWebSocket.open();
                }, VatomicWebSocket.timeout)
            };

            VatomicWebSocket.connection.onerror = function (err) {
                console.error('Socket encountered error: ', err.message, 'Closing socket');
                VatomicWebSocket.connection.close()
            };

            VatomicWebSocket.connection.onmessage = function (message) {

                // //console.log('Server: ' + e.data);
                if (!message || !message.data) return;
                var data = JSON.parse(message.data);

                console.info("Socket Message " + "[Time- " + (new Date().toLocaleString()) + "]", data.msg_type, data.payload);
                if (data.msg_type == 'info') {
                    //console.info(data.payload.text);
                }
                else if (data.msg_type == 'system_message') {
                    //console.log(data.payload.text);
                }
                else if (data.msg_type == 'inventory') {
                    Events.callEvent("websocket.inventory.changed", data.payload);
                    VatomicWebSocket.onInventory(data.payload);
                }
                else if (data.msg_type == 'state_update') {
                    Events.callEvent("websocket.vatom.state.changed", data.payload);

                }
                else if (data.msg_type == 'rpc') {
                    if(data.payload&&data.payload.rpc=="request_cmd")
                    {
                        Events.callEvent("websocket.request.cmd", data.payload.data);

                    }
                    Events.callEvent("websocket.rpc", data.payload);
                }
                else if (data.msg_type == "error") {
                }


            };
        },function(error,errorMessage){

            if(error!='UNAUTHORIZED')
            {
                setTimeout(function () {
                    VatomicWebSocket.open();
                }, VatomicWebSocket.timeout)
            }
            else
            {
                Events.callEvent("user.unauthorized");
            }
        });
    },
    close:function()
    {

    },
    sendMessage: function (message) {
        try {
            if (VatomicWebSocket.connection) {
                VatomicWebSocket.connection.send(message)
            }
        } catch (e) {
            Log.warn("WebSocket", "Unable to send WebSocket message. ", e)
        }
    },

    /** Called when an "inventory" websocket message is received */
    onInventory(data) {

        // Check if incoming or outgoing
        if (data.old_owner == VatomicStore.currentUserID && data.new_owner != VatomicStore.currentUserID) {

            // Vatom sent away
            Events.emit("vatom.removed", data.id);

        } else if (data.old_owner != VatomicStore.currentUserID && data.new_owner == VatomicStore.currentUserID) {

            // Vatom received, make sure it's not inside a parent
            if (data.parent_id && data.parent_id != ".")
                return console.info("WebSocket received a new vatom, but we're ignoring it because it's inside a folder.");

            // Notify listeners TODO: Once the `new Vatom(id)` constructor is in place, send a vatom object and not just the ID with the event.
            Events.emit("vatom.received", data.id);

        }

    }

};

module.exports = VatomicWebSocket;



