var VatomResourceManager = require('./Resources/VatomResourceManager');
var RequestHelper = require("./RequestHelper");
var Request = require("./Request");
var Events = require("./Events");
var Store = require("./VatomicStore");

/**
 * This class represents a single vatom.
 * @class
 * @param {Object} descriptor The raw vatom payload JSON.
 */
class Vatom {

    /** @returns Promise<Vatom> The vatom with the specified ID. */
    static withID(vatomid) {

        // Fetch vatom info
        return Request.post("/currentuser/vatom/get", {ids:[vatomid]}).then(data => {

            // Fetch data
            var inventory = data.objects;
            var faces = data.faces;

            // Join with faces
            RequestHelper.joinVatomsWithFaces(inventory,faces);

            // Create vatom with payload
            return new Vatom(inventory[0]);

        });

    }

    constructor(descriptor) {

        // Store descriptor for caching purposes. TODO: We should store it properly, not just store the desriptor!
        this._descriptor = descriptor;
    // Vars
        this.properties = {};
        this.resources = {};
        //  this.commerce = {};
        this.faces = [];
        this.resourceNames = [];
        this.dateCreated = new Date();
        this.dateModified = new Date();

        // Get identifier
        if(!descriptor) return;
        this.identifier = descriptor.id;
        if (!this.identifier)
            throw "Vatom ID not found in descriptor!";
        this.init(descriptor);

        // Create WebSocket listener
        Events.addEventListener("websocket.vatom.state.changed", this.onWebSocketVatomChanged.bind(this));

    }

    init(descriptor) {
        var _this = this;

        // Get date created
        if (descriptor.when_created)
            this.dateCreated = new Date(descriptor.when_created);

        // Get date modified
        if (descriptor.when_modified)
            this.dateModified = new Date(descriptor.when_modified);

        // Go through all "classes"
        for (var key in descriptor) {

            if (descriptor.hasOwnProperty(key)) {

                if (key == 'id') continue;

                var vClass = descriptor[key];


                if (vClass.constructor == Object) {


                    for (var innerKey in vClass) {

                        if (vClass.hasOwnProperty(innerKey)) {

                            if (innerKey.toLowerCase() == "resources")//adds resources together
                            {
                                var innerRes = vClass[innerKey];

                                // Fetch resources
                                for (var i = 0; i < innerRes.length; i++) {

                                    var type = (innerRes[i])["vAtomic::v1::vAtom::ResourceType"] || (innerRes[i])["v1::ResourceType"] || (innerRes[i])["v2::ResourceType"];
                                    // Get resource URL
                                    var url = (innerRes[i]).value.value;

                                    if (!url || url.length == 0) {
                                        continue;
                                    } // no resource to be found

                                    if (this.resources[innerRes[i].name] != null) continue;
                                    var res;
                                    if (type == null || type == 0) {
                                        continue;
                                    }
                                    res = VatomResourceManager.getResource(url, type);
                                    ////console.log(res);
                                    if (res) {
                                        this.resources[innerRes[i].name] = res;
                                        this.resourceNames.push(innerRes[i].name);
                                    }
                                }


                            }
                            else// Get commerce info
                            if (innerKey.toLowerCase() == "MyCommerce") {
                                var comm = vClass[innerKey];
                                // Fetch redeemable
                                this.commerce.redeemable = comm["redeemable"];
                                // Fetch pricing
                                var pricing = comm["pricing"];
                                // Fetch pricing type
                                if (pricing) {
                                    var t = pricing["vAtomic::v1::vAtom::PricingType"] || pricing["v1::PricingType"];
                                    this.commerce.pricingType = t;
                                    // Fetch value
                                    var value = pricing["value"];
                                    if (value) {
                                        // Fetch price
                                        this.commerce.price = value["price"];
                                        this.commerce.currencyID = value["currency"];

                                        // Fetch image
                                        // this.commerce.currencyIcon = VatomResourceManager.getCurrencyIcon(this.commerce.currencyID);
                                    }
                                }

                            }
                            else {
                                this.properties[innerKey] = vClass[innerKey];
                            }


                        }
                    }
                }
                else if (key.toLowerCase() == "faces") {
                    var faces = vClass;

                    //console.log(faces);
                    for (var i = 0; i < faces.length; i++) {
                        var vatomFace = faces[i];


                        // //check constraints and filter out faces that don't match base constraints
                        // if (((!vatomFace.properties.constraints.platform||vatomFace.properties.constraints.platform.length==0)&&Store.DEFAULTS.SUPPORTED_PLATFORMS.none)||Store.DEFAULTS.SUPPORTED_PLATFORMS[vatomFace.properties.constraints.platform.toLowerCase()]) {
                        //
                        //
                        //     //remove webview icon faces
                        //     if(Store.DEFAULTS.ALLOW_ICON_WEBVIEW_FACE|| vatomFace.properties&&vatomFace.properties.constraints&&(vatomFace.properties.constraints["view_mode"]!='icon'||vatomFace.properties.display_url.indexOf("native://")>=0))
                        //     {
                                this.faces.push(vatomFace);
                        //     }
                        //     else
                        //     {
                        //         //console.log(vatomFace);
                        //     }
                        //
                        //
                        //
                        // }
                        // else
                        // {
                        //     //console.log(vatomFace);
                        // }

                    }
                    //console.log(this.faces);


                }
                else {
                    this.properties[key] = vClass;
                }
            }
        }

        // if(this.faces.length==0)
        // {
        //     // add faces from private section
        //     if (descriptor && descriptor.private && descriptor.private.faces) {
        //         for (var o = 0; o < descriptor.private.faces.length; o++) {
        //             var face = descriptor.private.faces[o];
        //
        //             if(face.display_url) {
        //                 var out = {
        //                     properties: {
        //                         constraints: {},
        //                         display_url: ""
        //                     },
        //
        //                     resources: []
        //                 };
        //                 out.properties.constraints = face.constraints;
        //                 out.properties.display_url = face.display_url;
        //
        //                 if(face.display_url) {
        //                     this.faces.push(out);
        //                 }
        //             }
        //         }
        //
        //     }
        // }
        ///remove unsupported faces

        // for (var i = 0; i < this.faces.length; i++) {
        //
        //     if ((this.faces[i].properties.display_url.indexOf("native:") == 0 && !Store.DEFAULTS.NATIVE_FACES[this.faces[i].properties.display_url]))
        //     {
        //         //console.log(this.faces[i]);
        //         this.faces.splice(i, 1);
        //         i--;
        //     }
        //
        // }


    }

    /**
     * Gets the template name, if it exists.
     * @alias template
     * @memberof Vatom
     * @instance
     * @var
     */
    get template() {
        return this.properties["template"] || '';
    }


    /**
     * Gets the template variation, if it exists
     * @alias templateVariation
     * @memberof Vatom
     * @instance
     * @var
     */
    get templateVariation() {
        return this.properties["template_variation"] || '';
    }

    /**
     * Returns true if the current user owns this vatom
     * @alias isOwned
     * @memberof Vatom
     * @instance
     * @var
     */
    get isOwned() {

        if (this.checkIsOwned) {
            return this.checkIsOwned(this);
        }
        else
            //console.error(this);
        // Get owner
        var ownerID = this.properties["owner"];

        // Get current user ID
        if (window.Vatomic && Vatomic.user && Vatomic.user.userID) {

            // Compare
            return (ownerID == Vatomic.user.userID);

        }

        // Unknown
        return false;

    }


    /**
     * Returns true if the current user owns this vatom or another vatom with the same templateVariation
     * @alias isVariationOwned
     * @memberof Vatom
     * @instance
     * @var
     */
    get isVariationOwned() {

        if (this.checkVariationIsOwned) {
            return this.checkVariationIsOwned(this);
        }
        // Check if owned
        if (this.isOwned)
            return true;


        // Go through inventory
        var inv = window.Vatomic && Vatomic.inventory && Vatomic.inventory.vatoms || [];
        for (var i = 0; i < inv.length; i++)
            if (this.templateVariation == inv[i].templateVariation)
                return true;

        // Not owned
        return false;

    }


    /**
     * Gets the vatom's current state
     * @alias state
     * @memberof Vatom
     * @instance
     * @var
     */
    get state() {

        // Check if state found already
        if (this._state)
            return this._state;

        // Go through states
        for (var i = 0; i < this.states.length; i++) {

            // Get state
            var state = this.states[i];

            // Check value
            if (state.value.toLowerCase() != "true")
                continue;

            // Use this state
            this._state = state;
            break;

        }

        // If no state found, use first state
        if (!this._state)
            this._state = this.states[0];

        // Done
        return this._state;

    }

    /** Fetch child vatoms. @returns Promise<Array<Vatom>> */
    listChildren(callback) {
        return new Promise((onSuccess, onFail) => {
            Vatom.listChildren(this.identifier, (vatoms, errorCode, errorText) => {

                if (callback)
                    callback(vatoms, errorCode, errorText);

                // Check success
                if (errorCode) {

                    // Failed
                    var err = new Error(errorText);
                    err.code = errorCode;
                    onFail(err);

                } else {

                    // Success
                    onSuccess(vatoms);

                }

            });
        });
    }

    /** @static Fetch child vatoms of a vatom with the specified ID */
    static listChildren(parentVatomID, callback) {

        // Send request
        RequestHelper.getVatomInventory(parentVatomID, "", 400, function(items) {

            if(callback) {
                // Process vatoms
                var vatoms = [];
               if(items) {
                   for (var i = 0; i < items.length; i++)
                       vatoms.push(new Vatom(items[i]));

               }
              // Return items
              callback(vatoms);
            }
           // else
                //console.warn("No callback..")

        }, function(errorCode, errorText) {

            if(callback) {
                // Failed
                callback([], errorCode, errorText);
            }

        });

    }

    combinePossible(vatom) {
        var data = {vid:this.identifier,vatom:vatom};
        if(vatom.identifier!=this.identifier) {
            Events.callEvent("vatom.combine-possible", data);
        }
    }

    combineCanceled(vatom) {
        var data = {vid:this.identifier,vatom:vatom};
        if(vatom.identifier!=this.identifier) {
            Events.callEvent("vatom.combine-cancel", data);
        }
    }

    combine(vatom) {
        var data = {vid:this.identifier,vatom:vatom};
        if(vatom.identifier!=this.identifier)
        {
            Events.callEvent("vatom.combine",data);
        }

    }

    /**
     * This is called when updated info for this vatom is received. In this case, copy all info from the new vatom object.
     * @private
     */
    update(descriptor) {

        var faces = this.faces;
        this.init(descriptor);
        if(!this.faces||this.faces.length==0)
        this.faces =faces;
        // Notify anyone interested that this vatom's info has changed
        Events.callEvent("vatom.update-complete", this);

    }

    updateVatom(vatom) {

        var faces = this.faces;

        // Vars
        this.properties = vatom.properties;
        this.resources = vatom.resources;
        this.faces = vatom.faces;
        this.resourceNames = vatom.resourceNames;
        this.dateCreated = vatom.dateCreated;
        this.dateModified = vatom.dateModified;


        if(!this.faces||this.faces.length==0)
            this.faces =faces;
        // Notify anyone interested that this vatom's info has changed
        Events.callEvent("vatom.update-complete", this);

    }

    reload() {
        RequestHelper.getVatom(this.identifier,function(data){
            this.update(data);
        }.bind(this))
    }

    static getVatom(id,onSuccess,onFail) {
        RequestHelper.getVatom(id,function(data){
           if(onSuccess)
           {
               onSuccess(new Vatom(data));
           }
        },onFail)
    }

    getName() {
        return this.properties.name||this.properties.title||'';
    }

    get title() {
        return this.properties.title || this.properties.name || 'Untitled Vatom';
    }

    patch(data,onSuccess,onFail) {
        data.ids = [this.identifier];

        RequestHelper.patchVatom(data,onSuccess,onFail);
    }



    /** Perform the specified action on this vatom. @returns Promise with response */
    performAction(actionName, payload) {
        return Request.action(actionName, this.identifier, payload);
    }

    /** Transfer this vAtom to the specified user. @returns Promise */
    transferTo(user) {

        // Check if user is a VatomUser
        var payload = {};
        if (typeof user == "string") {

            // Check if string is email or phone number
            if (user.indexOf("@") !== -1)
                payload["new.owner.email"] = user;
            else if (user.indexOf("+") === 0)
                payload["new.owner.phone_number"] = user;
            else
                payload["new.owner.id"] = user;

        } else {

            // This must be a VatomUser, fetch the identifying property
            if (user.userID)
                payload["new.owner.id"] = user.userID;
            else if (user.phoneNumber)
                payload["new.owner.phone_number"] = user.phoneNumber;
            else if (user.email)
                payload["new.owner.email"] = user.email;
            else
                return Promise.reject({ code: "INVALID_PARAMETER", message: "The user object supplied didn't have any identifying fields. It must have either a userID, an email, or a phoneNumber." });

        }

        // Send request
        return this.performAction("Transfer", payload);

    }

    /** Delete a vAtom. This is implemented by sending it to trash@vatomic.io. @returns Promise */
    delete() {
        return this.transferTo("trash@vatomic.io");
    }

    /** Called when a WebSocket vatom updated event is received */
    onWebSocketVatomChanged(e) {

        // Check if for us
        if (e.id != this.identifier)
            return;

        // Get new properties
        var newProperties = e.new_object;
        this.update(newProperties);

    }

    /** Checks if a specified face URL exists in this vatom */
    containsFace(url) {

        // Find face
        for (var face of this.faces)
            if (face.properties.display_url == url)
                return true;

        // Not found
        return false;

    }



    /** Prefetch vatoms. Pass in an array, a vatom ID string, or a Vatom object. */
    static prefetch(vatoms) {

        // Make sure we have an array
        if (!(vatoms instanceof Array))
            vatoms = [vatoms];

        // Go through vatoms
        let prefetchPromises = [];
        for (let vatom of vatoms) {

            // Check if we need to load the vatom from an ID.
            let vatomPromise = null;
            if (typeof vatom == "string")
                vatomPromise = Vatom.withID(vatom);
            else if (vatom instanceof Vatom)
                vatomPromise = Promise.resolve(vatom);
            else
                vatomPromise = Promise.reject(new Error("Unknown type specified to the prefetch function. Please pass an array, a vatom ID string, or a Vatom object. " + vatom));

            // Wait until loaded
            vatomPromise = vatomPromise.then(vatom => vatom.prefetch());

            // Add to promise list
            prefetchPromises.push(vatomPromise);

        }

        // Wait until done
        return Promise.all(prefetchPromises);

    }

    /** Prefetch assets for this vatom */
    prefetch() {

        // Fetch each resource
        let promises = [];
        for (let name in this.resources)
            promises.push(this.resources[name]._prefetch());

        // List children
        promises.push(this.listChildren().then(children => {
            return Promise.all(children.map(child => child.prefetch()));
        }))

        // Wait until done
        return Promise.all(promises);

    }

}



module.exports = Vatom;



