//
// Gesture recognizer base class



/** The GestureRecognizer class is the base class for gesture recognizers. It takes a View object. */
function GestureRecognizer() {
	
	// Save vars
	this.view						= null;
	this.state						= 0;
	this.enabled					= true;
	this.onGestureStateChange		= null;
	this._tempPoint					= {x: 0, y: 0}
	this.offsetX					= 0;
	this.offsetY					= 0;
	this.velocityX					= 0;
	this.velocityY					= 0;
	this.lastPointerEventTime		= 0;
	
}

GestureRecognizer.complete = false;
/** @private Called by subclasses to change the gesture recognizer's state */
GestureRecognizer.prototype.setState = function(s) {
	
	// Check if the state is the same and ignore (except for the Moved state which can be triggered multiple times)
	if (s == this.state && this.state != GestureRecognizer.GestureRecognizerStates.Moved)
		return;

	// Store new state
	if(GestureRecognizer.complete)
	{
		this.state = GestureRecognizer.GestureRecognizerStates.Cancelled;
	}
	else
	this.state = s;
	
	// Notify listener
	if (this.onGestureStateChange&&!GestureRecognizer.complete&&this.onGestureStateChange(this))
	{
		GestureRecognizer.complete = true;
		setTimeout(function(){ GestureRecognizer.complete = false }, 0);
	}
	
	// Update pointer event time
	this.lastPointerEventTime = Date.now();
	
}

/** Set the current offset position */
GestureRecognizer.prototype.setOffset = function(x, y) {
	this.offsetX = x;
	this.offsetY = y;
}

/** Cancels an in-progress gesture */
GestureRecognizer.prototype.cancel = function() {
	
	// Update state
	if (this.state == GestureRecognizer.GestureRecognizerStates.None || this.state == GestureRecognizer.GestureRecognizerStates.Completed) {
		
		// No change
		
	} else {

		// Cancel it
		this.setState(GestureRecognizer.GestureRecognizerStates.Cancelled);
		
	}
	
}

/** Gets the touch's main point inside the view */
GestureRecognizer.prototype.getPoint = function() {
	
	// By default, just convert the pointer object. Check if it exists first.
	if (!this.pointer || !this.view) {
		this._tempPoint.x = 0;
		this._tempPoint.y = 0;
		return this._tempPoint;
	}
	
	// Set point
	var offset = $(this.view.element).offset();
	this._tempPoint.x = this.pointer.x - offset.left;
	this._tempPoint.y = this.pointer.y - offset.top;
	return this._tempPoint;
	
}

/** Gesture recognizer states */
GestureRecognizer.GestureRecognizerStates = {
	None: 0,							// Recognizer hasn't run yet
	Possible: 1,						// Detected a possible gesture
	Started: 2,							// Long-running gesture has started (such as a pan or zoom gesture)
	Moved: 3,							// Long-running gesture is changing
	Completed: 4,						// Competely detected a finished gesture
	Cancelled: 5						// Gesture was cancelled before successfully being completed. Return state to before the gesture was recognized.
};
module.exports=GestureRecognizer;


