//
// View object

/** View objects, contains an HTML element and a list of child Views. */
var TapGestureRecognizer = require("./TapGestureRecognizer");
var GestureRecognizer= require("./GestureRecognizer");

function View(element) {

	// Autoresize options
	this.FlexibleTopMargin		= 1 << 0;
	this.FlexibleLeftMargin		= 1 << 1;
	this.FlexibleBottomMargin	= 1 << 2;
	this.FlexibleRightMargin	= 1 << 3;
	this.FlexibleWidth			= 1 << 4;
	this.FlexibleHeight			= 1 << 5;

	// Setup vars
	this.element 					= null;
	this.subviews					= [];
	this.gestureRecognizers			= [];
	this.activePointers				= [];
	this.positionX					= 0;
	this.positionY					= 0;
	this.cornerRad 					= 0;
	this.viewWidth					= 0;
	this.viewHeight					= 0;
	this.oldWidth					= 0;
	this.oldHeight					= 0;
	this.scaleVal					= 1;
	this._autoresize 				= -1;

	var _this = this;
	Object.defineProperty(this, 'cornerRadius', {
		get: function() { return _this.cornerRad; },
		set: function(newValue) {
			_this.cornerRad = newValue;
			_this.element.style["border-radius"]=newValue+"px";
		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'x', {
		get: function() { return _this.positionX; },
		set: function(newValue) { _this.positionX = newValue;
			_this.updateCSS();
		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'y', {
		get: function() { return _this.positionY; },
		set: function(newValue) { _this.positionY = newValue;
			_this.updateCSS();
		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'width', {
		get: function() { return _this.viewWidth; },
		set: function(newValue) {

			////console.log("set width "+newValue);
			_this.oldWidth = _this.viewWidth;
			_this.viewWidth = newValue;
			_this.updateCSS();

		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'height', {
		get: function() { return _this.viewHeight; },
		set: function(newValue) {
			////console.log("set height "+newValue);
			_this.oldHeight = _this.viewHeight;
			_this.viewHeight = newValue;
			_this.updateCSS();

		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'scale', {
		get: function() {
			return _this.scaleVal;
		},
		set: function(newValue) {
			_this.scaleVal = newValue;
			_this.updateCSS();
		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'hidden', {
		get: function() { return !(!!( _this.element.offsetWidth || _this.element.offsetHeight || _this.element.getClientRects().length ));

		},
		set: function(newValue) {
			if(newValue)
			{
				_this.displayType = _this.element.style.display;
				_this.element.style.display='none';
			}
			else
			{
				_this.element.style=_this.displayType||"block";
			}
		},
		enumerable: true,
		configurable: true
	});
	Object.defineProperty(this, 'opacity', {
		get: function() {
			return _this.element.style.opacity;
		},
		set: function(newValue) {
			_this.element.style.opacity=newValue;
		},
		enumerable: true,
		configurable: true
	});

	Object.defineProperty(this, 'autoresize', {
		get: function() {
			return _this._autoresize;
		},
		set: function(newValue) {

			_this._autoresize=newValue;
		},
		enumerable: true,
		configurable: true
	});


	//setup view
	//this.init(this.element);

	//default tap gesture for all views
	this.tapGesture = new TapGestureRecognizer();
	this.addGestureRecognizer(this.tapGesture);
	this.tapGesture.onGestureStateChange = function(recognizer)
	{
		// Check state
		if (recognizer.state != GestureRecognizer.GestureRecognizerStates.Completed)
			return;

		// if view has OnClick method, run it
		if(_this.onClick)
		{
			////console.log("onclick");
			return _this.onClick();
		}

		return false;
	};

	this.init(element);
}

// Static vars
View.lastZIndex = 0;

/** Initialize with a specified HTML element */
View.prototype.init = function(element) {
	
	// Remove old element
	if (this.element)
		this.element.remove();

	if (!element) //if no defined element, create default vatom view
		element = document.createElement("DIV");//$("<div class='vatom-view'></div>");
	
	// Store element
	this.element = element;

	this.element.style['display']='inline-block';
	this.element.style['position']='absolute';
	this.element.style['transform']='translate(0, 0)';
	this.element.style['overflow']='hidden';
	this.element.style['left']='0';
	
	// Setup listeners
	var _this = this;
	this.element.addEventListener("mousedown", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Check if on a video element
		if (e.target.tagName.toLowerCase() == "video")
			return;
		
		// Send mouse pointer down event
		var preventDefault = _this.onPointerDown("mouse", e.pageX, e.pageY);
		if (preventDefault)
			e.preventDefault();
		return true;
		
	});

	this.element.addEventListener("mousemove", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Send mouse move event
		var preventDefault = _this.onPointerMove("mouse", e.pageX, e.pageY);
		if (preventDefault)
			e.preventDefault();
		return true;
		
	});

	this.element.addEventListener("mouseup", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Send mouse up event
		var preventDefault = _this.onPointerUp("mouse", e.pageX, e.pageY);
		if (preventDefault)
			e.preventDefault();
		return true;
		
	});

	this.element.addEventListener("touchstart", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Check if on a video element
		if (e.target.tagName.toLowerCase() == "video")
			return;
		
		// Send all pointer changed events
		var preventDefault = false;
		var touches = e.originalEvent.changedTouches;
		for (var i = 0 ; i < touches.length ; i++)
			preventDefault = _this.onPointerDown(touches[i].identifier, touches[i].pageX, touches[i].pageY) || preventDefault;
		
		// Do fake click
		e.target.click();
		//console.log(e);
			
		// Prevent default browser action if needed
		if (preventDefault) {
			e.preventDefault();
			return true;
		}
		
	});

	this.element.addEventListener("touchmove", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Send all pointer changed events
		var preventDefault = false;
		var touches = e.originalEvent.changedTouches;
		for (var i = 0 ; i < touches.length ; i++)
			preventDefault = _this.onPointerMove(touches[i].identifier, touches[i].pageX, touches[i].pageY) || preventDefault;
			
		// Prevent default browser action if needed
		if (preventDefault) {
			e.preventDefault();
			return true;
		}

	});

	this.element.addEventListener("touchend", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Send all pointer changed events
		var preventDefault = false;
		var touches = e.originalEvent.changedTouches;
		for (var i = 0 ; i < touches.length ; i++)
			preventDefault = _this.onPointerUp(touches[i].identifier, touches[i].pageX, touches[i].pageY) || preventDefault;
			
		// Prevent default browser action if needed
		if (preventDefault) {
			e.preventDefault();
			return true;
		}

	});

	this.element.addEventListener("touchcancel", function(e) {
		
		// Check if on an input element
		if (e.target.tagName.toLowerCase() == "input")
			return;
		
		// Send all pointer changed events
		var preventDefault = false;
		var touches = e.originalEvent.changedTouches;
		for (var i = 0 ; i < touches.length ; i++)
			preventDefault = _this.onPointerCancel(touches[i].identifier, touches[i].pageX, touches[i].pageY) || preventDefault;
			
		// Prevent default browser action if needed
		if (preventDefault) {
			e.preventDefault();
			return true;
		}

	});
	
};

/** @private Called when a pointer goes down. Returns true if handled in code. */
View.prototype.onPointerDown = function(id, x, y) {

	////console.log("pointer down");
	// Skip if pointer event exists already
	for (var i = 0 ; i < this.activePointers.length ; i++)
	{
		if(this.activePointers[i].id == id)
		{
			//console.log("pointer already exists");
			return;
		}
	}
		
	// Start monitoring event
	var event = {
		id: id,
		x: x,
		y: y,
		forwardedTo: null
	}

	// Add to active pointer list
	this.activePointers.push(event);


	// Pass to gesture recognizers
	var consumed = false;
	for (var i = 0 ; i < this.gestureRecognizers.length ; i++)
		if (this.gestureRecognizers[i].enabled)
			consumed = this.gestureRecognizers[i].onPointerDown(event) || consumed;

	// Return consumed status
	return false;
	
}

/** @private Called when a pointer moves. Returns true if handled in code. */
View.prototype.onPointerMove = function(id, x, y) {
	
	// Get pointer event
	var event = null;
	for (var i = 0 ; i < this.activePointers.length ; i++)
		if (this.activePointers[i].id == id)
			event = this.activePointers[i];
		
	// Skip if not found
	if (!event)
		return;
	
	// Forward if necessary
	if (event.forwardedTo)
		return event.forwardedTo.onPointerMove(id, x, y);
	
	// Update details
	event.x = x;
	event.y = y;
	
	// Pass to gesture recognizers
	var consumed = false;
	for (var i = 0 ; i < this.gestureRecognizers.length ; i++)
		if (this.gestureRecognizers[i].enabled)
			consumed = this.gestureRecognizers[i].onPointerMove(event) || consumed;

	// Return consumed status
	return false;
	
}

/** @private Called when a pointer goes up. Returns true if handled in code. */
View.prototype.onPointerUp = function(id, x, y) {

	////console.log("pointer up");
	// Get pointer event
	var event = null;
	for (var i = 0 ; i < this.activePointers.length ; i++) {
		if (this.activePointers[i].id == id) {
			
			// Found pointer
			event = this.activePointers[i];
			
			// Remove it
			this.activePointers.splice(i, 1);
			i--;
			
		}
	}
		
	// Skip if not found
	if (!event)
		return;
	
	// Forward if necessary
	//if (event.forwardedTo)
		//return event.forwardedTo.onPointerUp(id, x, y);
	
	// Pass to gesture recognizers
	var consumed = false;
	for (var i = 0 ; i < this.gestureRecognizers.length ; i++)
		if (this.gestureRecognizers[i].enabled)
			consumed = this.gestureRecognizers[i].onPointerUp(event) || consumed;
	
	// Return consumed status
	return false;
	
};

/** @private Called when a pointer event gets cancelled. Returns true if handled in code. */
View.prototype.onPointerCancel = function(id, x, y) {
	
	// Get pointer event
	var event = null;
	for (var i = 0 ; i < this.activePointers.length ; i++) {
		if (this.activePointers[i].id == id) {
			
			// Found pointer
			event = this.activePointers[i];
			
			// Remove it
			this.activePointers.splice(i, 1);
			i--;
			
		}
	}
		
	// Skip if not found
	if (!event)
		return;
	
	// Forward if necessary
	if (event.forwardedTo)
		return event.forwardedTo.onPointerCancel(id, x, y);
	
	// Pass to gesture recognizers
	var consumed = false;
	for (var i = 0 ; i < this.gestureRecognizers.length ; i++)
		if (this.gestureRecognizers[i].enabled)
			consumed = this.gestureRecognizers[i].onPointerCancel(event) || consumed;
	
	// Return consumed status
	return false;
	
}

/** Adds a gesture recognizer */
View.prototype.addGestureRecognizer = function(recognizer) {
	
	// Link view to gesture recognizer
	recognizer.view = this;
	
	// Add to recognizer list
	this.gestureRecognizers.push(recognizer);
	
}

/** Removes a gesture recognizer */
View.prototype.removeGestureRecognizer = function(recognizer) {
	
	// Remove link if needed
	if (recognizer.view == this)
		recognizer.view = null;
	
	// Remove from array
	for (var i = 0 ; i < this.gestureRecognizers.length ; i++) {
		if (this.gestureRecognizers[i] == recognizer) {
			this.gestureRecognizers.splice(i, 1);
			i--;
		}
	}
	
}

/** Adds sub view to this view */
View.prototype.addSubview = function(subview)
{
	this.element.appendChild(subview.element);

	this.subviews.push(subview);
	subview.parent = this;
};

/** Remove a subview */
View.prototype.removeView = function(subview) {
	
	// Remove from list
	for (var i = 0 ; i < this.subviews.length ; i++)
		if (this.subviews[i] == subview)
			this.subviews.splice(i, 1);
		
	if (subview.parent == this)
		subview.parent = null;
	
}

/** Brings a subview to the front (only works if children are absolutely positioned) */
View.prototype.bringToFront = function() {
	View.bringToFront(this.element);
}

/** Gets the view's width */
View.prototype.getWidth = function() {
	return this.element.innerWidth;
}

/** Gets the view's height */
View.prototype.getHeight = function() {
	return this.element.innerHeight;
}

/** Find hit test window in heirarchy */
View.prototype.findHitTestView = function(x, y) {
	
	// Check our children
	for (var i = 0 ; i < this.subviews.length ; i++) {
		
		// Check if within bounds
		var pos = this.subviews[i].element.offset();
		if (x < pos.left || x > pos.left + this.subviews[i].getWidth() || y < pos.top || y > pos.top + this.subviews[i].getHeight())
			continue;
		
		// Check if hit test
		var hitView = this.subviews[i].findHitTestView(x, y);
		if (hitView)
			return hitView;
		
	}
	
	// We are the highest hit target
	return this;
	
}

/** Moves the vatom to the specified position on screen */
View.prototype.moveTo = function(x, y) {
	
	// Check for NaN
	if (!x) x = 0;
	if (!y) y = 0;
	
	// Set values
	this.x = x;
	this.y = y;
	this.updateCSS();
	
}

/** Sets the scale of the vatom */
View.prototype.setScale = function(s) {
	
	// Set values
	this.scale = (s ? s : 1);
	this.updateCSS();
	
}

/** Called to update the CSS after a move or scale etc. */
View.prototype.updateCSS = function() {

	// Check if got element
	if (!this.element)
		return;
	// Find new values
	var x = this.x;
	var y = this.y;
	var scale = this.scale;
	var width = this.width;
	var height = this.height;


	//Resize view
	if (width == 0) {
		this.element.style["width"]= "0";
	}
	else
		this.element.style["width"]= width + "px";

	if (height == 0) {
		this.element.style["height"]= "0";
	}
	else
		this.element.style["height"]= height + "px";


	// Set CSS
	this.element.style["transform"]= "translate(" + x + "px, " + y + "px) scale(" + scale + ", " + scale + ")";

	//notify view re sized
	if (this.onResize) {
		this.onResize(width,height);
	}

	this.didResize(this.oldWidth,this.oldHeight,this.width,this.height);

	this.oldWidth=this.width;
	this.oldHeight=this.height;
};


/** Sets view background color **/
View.prototype.setBackgroundColor = function(r,g,b,a)
{
	this.element.style["background-color"]="rgba("+(r*255)+","+(g*255)+","+(b*255)+","+a+")";
};
/** Sets the vatom's position on screen **/
View.prototype.setPosition = function(x, y)
{
// Set values
	this.x = x;
	this.y = y;
};
/** Sets the size of the vatom **/
View.prototype.setSize = function(width, height)
{
	if(isNaN(width)||isNaN(height))return;
	//this.width = width;
	//this.height = height;

	//console.log("set size "+width+" "+height);
	this.oldHeight = this.viewHeight;
	this.viewHeight = height;
	this.oldWidth = this.viewWidth;
	this.viewWidth = width;
	this.updateCSS();
};



/** Brings an element to the front */
View.bringToFront = function(element) {
	
	// Increase last z-index
	//View.lastZIndex++;
	
	// Get element
	var elem = null;
	if (element instanceof View)
		elem = element.element;
	else
		elem = element;
	
	// Set z-index
	//elem.css("z-index", View.lastZIndex + "");
	elem.style['z-index']= '2';


	var n = elem.parentNode.firstChild;
	for ( ; n; n = n.nextSibling ){
		if ( n.nodeType == 1 && n != elem &&n.nodeName == "DIV")
		{
			n.style['z-index']=1;
		}
	}


};


/** @private Called when a view's width and/or height property changes */
View.prototype.didResize = function(oldWidth, oldHeight, newWidth, newHeight) {


	// Pass on to child views
	this.subviews.forEach(function(view) {

		//run if autoresize assigned
		if(view.autoresize>-1) {
			view.performAutoresize(oldWidth, oldHeight, newWidth, newHeight);
		}

	});

};

/** @private Called when the parent view is resized, and we should perform our autoresize logic */
View.prototype.performAutoresize = function(oldWidth, oldHeight, newWidth, newHeight) {

	// For X axis, perform autoresize
	if (this.autoresize & this.FlexibleLeftMargin && this.autoresize & this.FlexibleWidth && this.autoresize & this.FlexibleRightMargin) {

		// Flexible left margin, width and right margin
		this.positionX += (newWidth - oldWidth) / 3;
		this.oldWidth=this.viewWidth;
		this.viewWidth += (newWidth - oldWidth) / 3;

	} else if (this.autoresize & this.FlexibleLeftMargin && this.autoresize & this.FlexibleRightMargin) {

		// Flexible left and right margin, so center point will change
		this.positionX += (newWidth - oldWidth) / 2;

	} else if (this.autoresize & this.FlexibleWidth) {

		// Flexible width only, so width will change
		this.oldWidth=this.viewWidth;
		this.viewWidth += (newWidth - oldWidth);

	} else if (this.autoresize & this.FlexibleLeftMargin) {

		// Flexible left margin, so left point will change
		this.positionX += (newWidth - oldWidth);

	} else if (this.autoresize & this.FlexibleRightMargin) {
		// Flexible right margin, so right point will change (ie nothing happens)
		this.positionX += 0;
	}

	// For X axis, perform autoresize
	if (this.autoresize & this.FlexibleTopMargin && this.autoresize & this.FlexibleHeight && this.autoresize & this.FlexibleBottomMargin) {

		//console.log("resizing top margin and bottom margin and height!");
		// Flexible left margin, width and right margin
		this.positionY += (newHeight - oldHeight) / 3;
		this.oldHeight=this.viewHeight;
		this.viewHeight += (newHeight - oldHeight) / 3;

	} else if (this.autoresize & this.FlexibleTopMargin && this.autoresize & this.FlexibleBottomMargin) {

		//console.log("resizing top margin and bottom margin!");
		// Flexible left and right margin, so center point will change
		this.positionY += (newHeight - oldHeight) / 2;

	} else if (this.autoresize & this.FlexibleHeight) {

		//console.log("resizing top height!");
		// Flexible width only, so width will change
		this.oldHeight=this.viewHeight;
		this.viewHeight += (newHeight - oldHeight);

	} else if (this.autoresize & this.FlexibleTopMargin) {
		//console.log("resizing top top margin! "+(newHeight - oldHeight));

		// Flexible left margin, so left point will change
		this.positionY += (newHeight - oldHeight);

	} else if (this.autoresize & this.FlexibleBottomMargin) {

		// Flexible right margin, so right point will change (ie nothing happens)
		this.positionY += 0;

	}
	this.updateCSS();

};
module.exports = View;


