var View = require('./View');
var VatomCanvasContext = require("./CanvasContext");
var CanvasView = function () {

    View.call(this);

    //vars
    this.div = document.createElement("DIV");//$("<div class = 'vatom-view vatom-canvas'></div>");
    this.canvas = document.createElement("CANVAS");
    this.canvas.style.position='absolute';//$("<canvas style='position:absolute'></canvas>");
    this.canvas.style.left="0";
    this.div.appendChild(this.canvas);
    this.rate = 60;
    this.now;
    this.then = Date.now();
    this.interval = 1000/this.rate;
    this.delta;

    this.ctx = new VatomCanvasContext(this.canvas);

    var _this = this;
    Object.defineProperty(this, 'frameRate', {
        set: function (newValue) {
            _this.rate = newValue;
            _this.interval = 1000/_this.rate;
            _this.draw();
        },
        get: function () {
            return _this.rate;
        },
        enumerable: true,
        configurable: true
    });
    // Init view
    View.prototype.init.call(this, this.div);
    this.draw();

};


// Subclass
CanvasView.prototype = Object.create(View.prototype);
CanvasView.prototype.constructor = CanvasView;

CanvasView.prototype.updateCSS = function()
{
    this.canvas.width=this.width;
    this.canvas.height=this.height;

    View.prototype.updateCSS.call(this);
};
CanvasView.prototype.draw= function() {

    if(this.rate!=0) {
        requestAnimationFrame(this.draw.bind(this));
    }

    this.now = Date.now();
    this.delta = this.now - this.then;

    if (this.delta > this.interval) {

        if(this.onRender)
        {
            //try {
                this.onRender(this.ctx);
            /*}
                catch(e)
                {
                //console.error(e);
                }*/

           // //console.log(this.ctx);
        }
        this.then = this.now - (this.delta % this.interval);
    }
};

module.exports=CanvasView;





