//
// A server message

import VatomUser from '../Users/VatomUser'

 export default class Message {

     constructor(data, thread) {

         // Properties
         this.id = "";
         this.thread = thread;
         this.user = null;
         this.triggeredBy = null;
         this.message = "";
         this.actionName = "";
         this.date = new Date(0);

         // Process data
         if (data)
            this.processData(data);

     }

     processData(data) {

         // Read data
         this.id = data.msg_id;
         this.date = new Date(data.when_created);
         this.user = VatomUser.withID(data.user_id);
         this.triggeredBy = VatomUser.withID(data.triggered_by);
         this.message = data.msg;
         this.actionName = data.action_name;

         // Read vatoms TODO: We need `new Vatom(id)` and `Vatom.withID(id)` for this.
        //  this.vatoms = [];
        //  for (var vatomID of data.vatoms || []) {
        //      this.vatoms.push(Vatom.withID(vatomID));
        //  }

     }

 }



