function VatomCanvasContext(canvas)
{
    this.ctx = canvas.getContext("2d");

    this.savedContexts = 0;
    var _this = this;
    Object.defineProperty(this, 'width', {

        get: function () {

           // //console.log(_this.ctx.canvas.width);
            return _this.ctx.canvas.width;
        },
        enumerable: true,
        configurable: true
    });
    Object.defineProperty(this, 'height', {

        get: function () {

           // //console.log(_this.ctx.canvas.height);
            return _this.ctx.canvas.height;
        },
        enumerable: true,
        configurable: true
    });
    /** The line thickness when stroking/drawing shapes */
    Object.defineProperty(this, 'lineWidth', {

        get: function () {
            return _this.ctx.lineWidth;
        },
        set: function (newValue) {
            return _this.ctx.lineWidth=newValue;
        },
        enumerable: true,
        configurable: true
    });

};


/** Reset context to default scale, clipping etc */
VatomCanvasContext.prototype.resetContext = function() {

    // Stop if no context
    if (this.ctx == null) {
        return
    }

    // Pop saved stacks
    while (this.savedContexts > 0) {
        this.ctx.restore();
        this.savedContexts -= 1;
    }

    // Save this "default" state
    this.ctx.save();
    this.savedContexts += 1;

    // Reset transformation matrix
    //let ctm = CGContextGetCTM(ctx)
    //if !CGAffineTransformIsIdentity(ctm) {
    //	CGContextConcatCTM(ctx, CGAffineTransformInvert(ctm))
    //}

    // Set default scale based on screen pixel ratio
   // var ratio = round(self.size.width / (self.canvasView?.bounds.size.width ?? 1))
    //if ratio <= 0 { ratio = 1 }
    //CGContextScaleCTM(ctx, ratio, ratio)

};

/** Clears all pixels */
VatomCanvasContext.prototype.clear = function() {
    if (this.ctx == null) { return; }
    this.ctx.clearRect(0, 0, this.width, this.height);
  // //console.log("clearing");
}

/** Clears the pixels in the specified rect */
VatomCanvasContext.prototype.clearRect = function(x,y,width,height) {
    if (this.ctx == null) { return; }
    this.ctx.clearRect(x, y, width, height);
    ////console.log("clearRect");
}

/** Sets the current fill color */
VatomCanvasContext.prototype.setFillColor = function(red,green,blue,alpha) {
    if (this.ctx == null) { return; }

   // throw "error";
    this.ctx.fillStyle = "rgba("+Math.round(red*255)+","+Math.round(green*255)+","+Math.round(blue*255)+","+alpha+")";

}

/** Sets the current stroke color */
VatomCanvasContext.prototype.setStrokeColor = function(red,green,blue,alpha) {
    if (this.ctx == null) { return; }
    this.ctx.strokeStyle = "rgba("+Math.round(red*255)+","+Math.round(green*255)+","+Math.round(blue*255)+","+alpha+")";
  //  //console.log("stroke color");
    //CGContextSetRGBStrokeColor(ctx, red, green, blue, alpha)
}

/** Draws a rect */
VatomCanvasContext.prototype.drawRect = function(x,y ,width,height) {
    if (this.ctx == null||width<=0||height<=0) { return; }
    this.ctx.strokeRect(x,y,width,height);

}

/** Fills a rect */
VatomCanvasContext.prototype.fillRect = function(x,y,width,height) {
    if (this.ctx == null||width<=0||height<=0) { return; }
   this.ctx.fillRect(x,y,width,height);

}

/** Draws a rounded rect */
VatomCanvasContext.prototype.drawRoundedRect = function(x,y,width,height,cornerRadius) {
    if (this.ctx == null||width<=0||height<=0) { return; }
    if (width < 2 * cornerRadius) cornerRadius = width / 2;
    if (hight < 2 * cornerRadius) cornerRadius = hight / 2;
    this.ctx.beginPath();
    this.ctx.moveTo(x+cornerRadius, y);
    this.ctx.arcTo(x+width, y,   x+width, y+hight, cornerRadius);
    this.ctx.arcTo(x+width, y+hight, x,   y+hight, cornerRadius);
    this.ctx.arcTo(x,   y+hight, x,   y,   cornerRadius);
    this.ctx.arcTo(x,   y,   x+width, y,   cornerRadius);
    this.ctx.closePath();
    this.ctx.stroke();
    ////console.log("round rect");
}


/** Fills a rounded rect */
VatomCanvasContext.prototype.fillRoundedRect = function(x,y,width,height,cornerRadius) {
    if (this.ctx == null||width<=0||height<=0) { return; }
    if (width < 2 * cornerRadius) cornerRadius = width / 2;
    if (hight < 2 * cornerRadius) cornerRadius = hight / 2;
    this.ctx.beginPath();
    this.ctx.moveTo(x+cornerRadius, y);
    this.ctx.arcTo(x+width, y,   x+width, y+hight, cornerRadius);
    this.ctx.arcTo(x+width, y+hight, x,   y+hight, cornerRadius);
    this.ctx.arcTo(x,   y+hight, x,   y,   cornerRadius);
    this.ctx.arcTo(x,   y,   x+width, y,   cornerRadius);
    this.ctx.closePath();
    this.ctx.fill();
    ////console.log("fill round rect");
}

/** Draws an ellipse inside the specified rect */
VatomCanvasContext.prototype.drawEllipse = function(x,y,width,height) {
    if (this.ctx == null||width<=0||height<=0) { return; }

    this.ctx.save();
    this.ctx.beginPath();
    var r =  width>height?width/2:height/2;//Math.sqrt(Math.pow(width/2,2)+Math.pow(height/2,2));
    if(width>height)
    {
        this.ctx.scale(height/width,1);
    }
    else
        this.ctx.scale(1,width/height);

    this.ctx.translate(x+width/2,y+height/2);
    this.ctx.arc(0, 0,r, 0, 2 * Math.PI, false);
    this.ctx.closePath();
    this.ctx.stroke();
    this.ctx.restore();

}
/** Fills an ellipse inside the specified rect */
VatomCanvasContext.prototype.fillEllipse = function(x,y,width,height) {

    if (this.ctx == null||width<=0||height<=0) { return; }

    this.ctx.save();
    this.ctx.beginPath();
    var r =  width>height?width/2:height/2;//Math.sqrt(Math.pow(width/2,2)+Math.pow(height/2,2));
    if(width>height)
    {
        this.ctx.scale(height/width,1);
    }
    else
        this.ctx.scale(1,width/height);


    this.ctx.translate(x+width/2,y+height/2);
    ////console.log(r);
    this.ctx.arc(0, 0,r, 0, 2 * Math.PI, false);
    this.ctx.closePath();
    this.ctx.fill();
    this.ctx.restore();


}


/** Draws a line between two points */
VatomCanvasContext.prototype.drawLine = function(x1,y1,x2,y2) {
    if (this.ctx == null) { return; }
    this.ctx.beginPath();
    this.ctx.moveTo(x1,y1);
    this.ctx.lineTo(x2,y2);
    this.ctx.stroke();
}



/** Draws an image resource to the canvas.If 0 or nothing is provided for width and height, then the image width/height will be used.*/
VatomCanvasContext.prototype.drawImage = function(resource,x,y,width,height) {
    if (this.ctx == null) { return; }
    if(resource&&resource.image)
    {
        this.ctx.drawImage(resource.image,x,y,width,height);
    }
}

/** Saves the current context state by pushing it onto the stack */
VatomCanvasContext.prototype.saveState = function() {
    if (this.ctx == null) { return; }

    this.ctx.save();

    // Increase context count
    this.savedContexts += 1;

}

/** Restores the previous context state by popping it off the stack */
VatomCanvasContext.prototype.restoreState = function() {
    if (this.ctx == null) { return; }

    // Pop last context off stack and use it
    this.ctx.restore();

    // Decrease context count
    this.savedContexts -= 1;
    if (this.savedContexts < 0 ){
        this.savedContexts = 0;
    }

}

/** Starts a new path.Any old path data is discarded.*/
VatomCanvasContext.prototype.beginPath = function() {
    if (this.ctx == null) { return; }
    this.ctx.beginPath();
}

/** Moves the path's current position, or cursor, to the specified coordinates */
VatomCanvasContext.prototype.moveTo = function(x,y) {
    if (this.ctx == null) { return; }
    this.ctx.moveTo(x,y);
}

/** Adds a line to the current path, from the path's current position to the new specified position */
VatomCanvasContext.prototype.lineTo = function(x,y) {
    if (this.ctx == null) { return; }
    this.ctx.lineTo(x,y);
}

/** Adds an arc to the current path */
VatomCanvasContext.prototype.arc = function(x,y,radius,startAngle,endAngle,clockwise) {
    if (this.ctx == null) { return; }
    this.ctx.arc(x,y,radius,startAngle,endAngle,!clockwise);
}

/** Adds an arc to the current path, between the specified points */
VatomCanvasContext.prototype.arcTo = function(x1,y1,x2,y2,radius) {
    if (this.ctx == null) { return; }
    this.ctx.arcTo(x,y,x2,y2,radius);
}

/** Closes the current path (adds a line between the path's first point and last point) */
VatomCanvasContext.prototype.closePath = function() {
    if (this.ctx == null) { return; }
    this.ctx.closePath();
}

/** Strokes the current path using the current stroke color and line width */
VatomCanvasContext.prototype.drawPath = function() {
    if (this.ctx == null) { return; }
    this.ctx.stroke();
}

/** Fills the current path using the current fill color */
VatomCanvasContext.prototype.fillPath = function() {
    if (this.ctx == null) { return; }
    this.ctx.fill();
}

/** Clips the canvas to the current path.Any drawing operations outside of the path will be hidden.*/
VatomCanvasContext.prototype.clip = function() {
    if (this.ctx == null) { return; }
    this.ctx.clip();
}
module.exports=VatomCanvasContext;


