var Request = require("./Request");
var Vatom =require("./Vatom");
/** @class This class defines a discovery query to be used to discover vatoms. */
var VatomDiscoverFilter = function(customPayload) {

	// Setup default vars
	this.overridePayload 	= customPayload;
    this.filters 			= [];
    this.groupBy 			= [VatomDiscoverFilter.FieldTemplateVariation];
	this.scope				= null;

	// The default scope returns vatoms that the user owns only
	this.setScopeToOwnedVatomsOnly();

	/** The number of samples to return. This is based on the groupBy property. 0 will return all vatoms without sampling them. */
	this.samples			= 0;

}

/** Filter operations */
VatomDiscoverFilter.FilterOperation = {
	Equal: "Eq",
	GreaterThan: "Gt",
	GreaterOrEqual: "Ge",
	LessThan: "Lt",
	LessOrEqual: "Le",
	NotEqual: "Ne",
	Match: "Match"
};

/** Filter combine operations */
VatomDiscoverFilter.CombineOperation = {
	And: "And",
	Or: "Or"
}

/** Predefined fields */
VatomDiscoverFilter.FieldAcquireable = "vAtomic::v1::vAtom::vAtomType.acquireable"
VatomDiscoverFilter.FieldPrivateName = "private.name"
VatomDiscoverFilter.FieldID = "id"
VatomDiscoverFilter.FieldPublisherFqdn = "vAtomic::v1::vAtom::vAtomType.publisher_fqdn"
VatomDiscoverFilter.FieldTemplate = "vAtomic::v1::vAtom::vAtomType.template"
VatomDiscoverFilter.FieldTemplateVariation = "vAtomic::v1::vAtom::vAtomType.template_variation"
VatomDiscoverFilter.FieldVisibilityType = "vAtomic::v1::vAtom::vAtomType.visibility.type"
VatomDiscoverFilter.FieldOwner = "vAtomic::v1::vAtom::vAtomType.owner"
VatomDiscoverFilter.FieldParentID = "vAtomic::v1::vAtom::vAtomType.parent_id"

/** Appends a filter to the filters list. */
VatomDiscoverFilter.prototype.appendFilter = function(field, value, compareOperation, combineOperation) {

	// Append filter
	this.filters.push({
		field: field,
		filter_op: compareOperation || VatomDiscoverFilter.FilterOperation.Equal,
		value: value,
		bool_op: combineOperation || VatomDiscoverFilter.CombineOperation.And
	});

}

/** Set the scope to only return vatoms with the specified publisher FQDN */
VatomDiscoverFilter.prototype.setScopeToPublisherFQDN = function(fqdn) {

	// Set scope
	this.scope = {
		key: VatomDiscoverFilter.FieldPublisherFqdn,
		value: fqdn
	};

}

/** Set the scope to only return owned vatoms */
VatomDiscoverFilter.prototype.setScopeToOwnedVatomsOnly = function() {

	// Set scope
	this.scope = {
		key: VatomDiscoverFilter.FieldOwner,
		value: "$currentuser"
	};

}

/** Set the scope to specified key and value */
VatomDiscoverFilter.prototype.setScope = function(key, value) {

	// Set scope
	this.scope = {
		key: key,
		value: value
	};

}

/** @private Get the request payload to send to the Discovery API call */
VatomDiscoverFilter.prototype.getPayload = function() {

	// Check for override
    if (this.overridePayload)
        return this.overridePayload;

	// Check scope
	if (!this.scope)
		return //console.warn("Vatomic SDK: No scope set for discover filter!");

	// Create payload
    var payload = {};
	payload.scope = this.scope;

	// Set sampling
	if (this.samples > 0 && this.groupBy.length > 0) {
		payload.sample = this.samples;
		payload.group_by = this.groupBy;
	}

    // Set filters
	if (this.filters.length > 0) {
		payload.filters = [{
			filter_elems: this.filters
		}];
	}

    // Set return info
    payload.return = {
        "type": "*",
        "fields": []
    }

    // Done
    return payload;

}

/** Execute the discover query, returning an array of Vatoms */
VatomDiscoverFilter.prototype.execute = function(callback) {

	// Send request
	Request.send("POST", "/currentuser/vatom/discover", null,this.getPayload(), function(data) {

		//console.log(data);
		// Create list of vatoms
		var vatoms = [];
		data.results.forEach(function(vatomInfo) {


			// Add all vatoms
			var v = new Vatom(vatomInfo);
			if (v)
				vatoms.push(v);

		});

		// Done
		callback(vatoms);

	}, function(errorCode, errorText) {

		// Failed
		callback([], errorCode, errorText);

	});

}

/** Creates a function which can be used as an inventory's fetch function, based on this discover filter */
VatomDiscoverFilter.prototype.createInventoryFetchFunction = function() {

	// Create function
	return function(onSuccess, onFail, pageToken, pageCount) {

		// Fetch all vatoms
		this.execute(function(vatoms, errorCode, errorText) {

			// Check for error
			if (!vatoms && errorCode) {

				// Failed, pass error to inventory
				onFail(errorCode, errorText);

			} else {

				// Success, pass vatoms to inventory
				onSuccess(vatoms);

			}

		});

	}.bind(this);

}

/** Make a copy of this discover filter */
VatomDiscoverFilter.prototype.duplicate = function() {

	// Make a copy
	var filter = new VatomDiscoverFilter();
	filter.overridePayload = this.overridePayload;
    filter.filters = this.filters;
    filter.groupBy = this.groupBy;
	filter.scope = this.scope;
	filter.samples = this.samples;
	return filter;

}

module.exports = VatomDiscoverFilter;



