var UUID = require("../../Lib/UUID");
var Store = require("../../Code/VatomicStore");
var Events = require("../../Code/Events");

module.exports = class Face {

	constructor(vatomView, faceProps) {

		// Properties
	    this._uuid = UUID.generate();
		this.vatomView = vatomView;
	    this.vatom = vatomView && vatomView.vatom;
	    this.face = faceProps || {};
	    this.properties = this.face.properties || {};

	    // Create native face element;
	    this.element = document.createElement("div");
	    this.element.style.position = "relative";
	    this.element.style.height = "100%";
	    this.element.style.width = "100%";

		// Stop if data is invalid
		if(!this.vatom || !this.face.id)
			return;

		// Load the state, if it exists
		var lastState = Store._states[this.vatom.identifier+"-"+this.face.id];
	    if (lastState)
	        this.loadState(lastState);

		// Listen for events
		this._bound_onStateChanged = this._onStateChanged.bind(this);
	    Events.registerEvent("face.state.changed",this._bound_onStateChanged);

	}

	/** @abstract Subcass can override this to be notified when the vatom's properties change */
	onVatomUpdated() {

	}

	onLoad() {

	}

	onUnload() {

	}

	onResize() {

	}

	/** @abstract The subclass can override this to receive the state it saved last */
	loadState(state) {

	}

	saveState(state) {

		// console.log("state saved ",state);
	    Store._states[this.vatom.identifier+"-"+this.face.id] = state;
	    Events.callEvent("face.state.changed",{id:(this.vatom.identifier+"-"+this.face.id),senderId:this._uuid,state:state})

	}

	_onStateChanged(event) {

	    if(event.id == (this.vatom.identifier+"-"+this.face.id)&&event.senderId!=this._uuid)
	    {
	        this.loadState(event.state);
	    }


	}

	clear() {
		while (this.element.children.length > 0) {
	        this.element.removeChild(this.element.children[0]);
	    }
	}

	showError() {

	    var div = document.createElement("div");
	    div.style.backgroundSize = "32px 32px";
	    div.style.backgroundPosition = "center";
	    div.style.backgroundRepeat = "no-repeat";
	    div.style.backgroundImage = "url('" + Face.ErrorIcon + "')";
	    div.style.position = "absolute";
	    div.style.top = "0px";
	    div.style.left = "0px";
	    div.style.width = "100%";
	    div.style.height = "100%";
	    this.show(div);

	}

	showLoading() {

	    var div = document.createElement("div");
	    div.style.backgroundSize = "96px 96px";
	    div.style.backgroundPosition = "center";
	    div.style.backgroundRepeat = "no-repeat";
	    div.style.backgroundImage = "url('" + Face.LoadingIcon + "')";
	    div.style.position = "absolute";
	    div.style.top = "0px";
	    div.style.left = "0px";
	    div.style.width = "100%";
	    div.style.height = "100%";
	    this.show(div);
	    this.loader = div;

	}

	hideLoading() {

		try {
	        this.element.removeChild(this.loader);
	    }
	    catch (e) {
	    }

	}

	show(view, dontClear) {

		!dontClear && this.clear();

		this.element.appendChild(view);

	}

	setProperty(key, value) {

		this.properties[key] = value;

	    if (this.view)
	        this.view.setProperty(key, value);

	}

	getProperty(key) {
		return this.properties[key];
	}

	isAttached() {

		var element = this.element;
	    while (element.parentElement) {
	        if (element.parentElement.nodeName == "HTML")
	            return true;
	        element = element.parentElement;
	    }
	    return false;

	}

	free() {

		this.onUnload();
	    this.vatom = null;
	    this.face = null;
	    this.properties = null;
	    this.element = null;
	    this.onLoadComplete= null;
	    this.onLoadError = null;
	    Events.unregisterEvent("face.state.changed", this._bound_onStateChanged);
	    this._onUpdate = null;

	}

	viewChanged(iconView, containerView, isFullscreen) {
	}

}


// Icon assets
module.exports.ErrorIcon = "data:image/png;base64,";
module.exports.LoadingIcon = "data:image/svg+xml;base64,"



