// TODO: Once all functions are using promises, increase the major version number of the SDK and remove old onSuccess and onFail handlers

var Store = require("./VatomicStore.js");
var LocalStorage = require("./LocalStorage")

var Request = {

    // Static vars
    API_PREFIX: "/api/v1"

};

/** Sends a POST request to the server. @returns Promise with payload data. */
Request.post = function(endpoint, payload) {

    // Check payload type
    var type = "application/json";
    if (payload instanceof FormData)
        type = null;

    return Request.send("POST", endpoint, type, payload);
}

/** Sends a GET request to the server. @returns Promise with payload data. */
Request.get = function(endpoint) {
    return Request.send("GET", endpoint);
}

/** Sends a PATCH request to the server. @returns Promise with payload data. */
Request.patch = function(endpoint, payload) {
    return Request.send("PATCH", endpoint, "application/json", payload);
}

/** Sends a Vatom Action request to the server. @returns Promise with payload data. */
Request.action = function(actionName, onVatomID, payload) {

    // Add vatom ID to payload
    payload = payload || {};
    payload["this.id"] = onVatomID;

    // Send request
    return Request.send("POST", "/currentuser/action/" + actionName, "application/json", payload).then(response => {

        // Fetch action output, in it's weird format
        var actionOut = response && response.main && response.main.Output;

        // Check if it's a string
        if (typeof actionOut == "string") {

            // If it's a string, decode as JSON for the Actions that return a JSON string
            try {

                // Decode JSON
                actionOut = JSON.parse(actionOut);

            } catch (e) {

                // It isn't JSON, use as string value
                actionOut = { Output: actionOut };

            }

        }

        // Done
        return actionOut;

    });

}

/** Send a request to the server. @returns Promise with payload data. */
Request.send = function (method, endpoint, contentType, payload, onSuccess, onFail) {

    // Create promise
    return new Promise((promiseSuccess, promiseFail) => {

        // Check endpoint
        if (!endpoint || endpoint.charAt(0) != '/' ) {

            // Fail, call error handler and throw
            var code = "invalid_url";
            var msg = "The request URL provided was invalid.";
            onFail && onFail(code, msg);
            return promiseFail({ code: code, message: msg });

        }

        // Create URL to request to
        var url = Store.DEFAULTS.SERVER_ADDRESS + Request.API_PREFIX + endpoint;

        // Stringify payload if needed
        if (!(payload instanceof FormData) && typeof payload != "string")
            payload = JSON.stringify(payload);

        // Create request
        var req = new XMLHttpRequest();

        // Add on success and on error handler
        req.onload = req.onerror = e => {

            // Catch errors
            try {

                // Convert to JSON
                var response = JSON.parse(req.responseText);

                // Check for error
                if (response.error) {

                    // Failed
                    var err = Request.ERROR_CODES[response.error] || response.error;
                    var errText = Request.ERROR_MESSAGES[err] || response.message;
                    onFail && onFail(err, errText);
                    return promiseFail({ code: err, message: errText });

                }

                // Store authorization token
                try {

                    var auth = req.getResponseHeader("Authorization")
                    if (auth)
                        LocalStorage.set("vatomic.token", auth)

                } catch (e) {
                    console.warn(e)
                }

                // Passed
                onSuccess && onSuccess(response.payload);
                promiseSuccess(response.payload);

            } catch (err) {

                // Throw error based on network response
				console.warn("Vatomic SDK Error: ", err);
                var err = req.status;
                var errText = err.message || Request.ERROR_MESSAGES[err] || req.statusText || "Network error";
                onFail && onFail(err, errText);
                return promiseFail({ code: err, message: errText });

            }

        }

        // Start request
        req.open(method, url);

        // Add app ID header
        var appID = Store.DEFAULTS.APP_ID
        req.setRequestHeader('X-Vatomic-App-Id', appID)
        if (!appID) {
            onFail && onFail("MISING_APP_ID", "Please set your app ID with Vatomic.setAppID(...) first.");
            return promiseFail({ code: "MISING_APP_ID", message: "Please set your app ID with Vatomic.setAppID(...) first." });
        }

        // Add auth token header if we have it
        var auth = LocalStorage.get("vatomic.token")
        if (auth)
            req.setRequestHeader("Authorization", auth)

        // Add content type header if needed
        if (contentType)
            req.setRequestHeader("Content-Type", contentType);

        // Create payload if needed
        if (method != "GET" && method != "DELETE")
            req.send(payload);
        else
            req.send();

    });

};

Request.ERROR_MESSAGES = {
    INVALID_PASSWORD:"Wrong password entered.",
    USERNAME_EXISTS: "An account with the specified email already exists.",
    INVALID_APP_KEY: "App profile not found.",
    UNAUTHORIZED: "You have been logged out.",
    INVALID_REQUEST_DATA: "The server couldn't process the data sent from the app.",
    GUESTMODE_UPGRADE_REQUIRES_PASSWORD: "You need to set a password before you can activate your account.",
    INVALID_PHONE_NUMBER: "Please enter a phone number in international format.",
    INVALID_TOKEN: "The token specified was invalid.",
    ALREADY_VERIFIED: "This user has already been verified.",
    INVALID_DATA: "The server could not process the request. There was a problem with the multipart form data.",
    USERNAME_NOT_FOUND: "A user with the specified username was not found.",
    UNVERIFIED: "Please verify your account.",
    ACCOUNT_NOT_FOUND: "The user was not found.",
    USER_EXISTS: "This user already exists.",
    NOT_FOUND: "The specified item was not found.",
    ALREADY_OWNER: "The specified user already owns this vatom.",
    UNKNOWN_ACTION: "Unknown action requested.",
    UNKNOWN_OAUTH_PROVIDER: "Unknown OAuth login provider.",
    REQUEST_DENIED: "The request was denied.",
    INVALID_PARAMETER: "There was a problem with the request.",
    ALREADY_OWNED: "The user already owns this item.",
    NOT_ENOUGH_COINS: "You don't have enough coins to complete this purchase.",
    TRANSER_NOT_ALLOWED: "This vatom is not allowed to be transferred.",
    VATOM_NOT_FOUND: "The specified vatom was not found.",
    USER_NOT_FOUND: "The user was not found on the system.",
    VATOM_UNAUTHORIZED: "You are not allowed to view this vatom.",
    ERROR_REDEEMABLE: "You can't transfer this vAtom, but you can redeem it.",
    ALREADY_OFFERED: "This vAtom has already been offered in a trade.",
    TRANSFER_ERROR_USERID_NOT_FOUND: "The specified user was not found."
};
Request.ERROR_CODES = {
    401: "INVALID_PASSWORD",
    409: "USERNAME_EXISTS",
    513: "INVALID_APP_KEY",
    1001: "UNAUTHORIZED",
    1004: "INVALID_REQUEST_DATA",
    1006: "GUESTMODE_UPGRADE_REQUIRES_PASSWORD",
    1007: "INVALID_PHONE_NUMBER",
    1008: "INVALID_TOKEN",
    1010: "ALREADY_VERIFIED",
    1012: "INVALID_DATA",
    1014: "USERNAME_NOT_FOUND",
    1015: "UNVERIFIED",
    1016: "ACCOUNT_NOT_FOUND",
    1018: "ALREADY_VERIFIED",
    1019: "ALREADY_VERIFIED",
    1025: "USER_EXISTS",
    1026: "INVALID_PASSWORD",
    1029: "NOT_FOUND",
    1034: "ALREADY_OWNER",
    1035: "UNKNOWN_ACTION",
    1036: "UNKNOWN_OAUTH_PROVIDER",
    1062: "ERROR_REDEEMABLE",
    1601: "REQUEST_DENIED",
    1602: "INVALID_PARAMETER",
    1605: "ALREADY_OWNED",
    1608: "NOT_ENOUGH_COINS",
    1610: "TRANSER_NOT_ALLOWED",
    1635: "ALREADY_OFFERED",
    1701: "VATOM_NOT_FOUND",
    1702: "TRANSFER_ERROR_USERID_NOT_FOUND",
    1705: "USER_NOT_FOUND",
    1708: "VATOM_UNAUTHORIZED",
};
Request.simplifyJson = function(json)
{

    if(typeof json != 'object'||typeof json == 'string')
        return json;
    var newJson = {};
    for (var key in json) {

        if(!json[key]||(typeof json[key] === 'object' && json[key].length == 0))
        {
            //delete json[key];
        }
        else
        {

            newJson[key]=Request.simplifyJson(json[key]);
        }

    }
    if(newJson.length==1)
    {
        newJson = newJson[0];
    }
    return newJson;
};
module.exports = Request;



