var View = require('./View');
var Label = function () {

    View.call(this);

    //vars
    this.div = document.createElement("DIV");//$("<div class = 'vatom-view vatom-label'></div>");

    this.label = document.createElement("P");
    this.label.style.width="100%";
    this.label.style.height="100%";
    this.label.style.position="absolute";
    this.label.style.margin=0;
    //$("<p style='position:absolute;width:100%;height:100%'></p>");
    this.textContainer=this.label;

   // this.textContainer.append(this.label);
    this.div.appendChild(this.textContainer);

    // Text stuff
    this.AlignLeft = 1 << 0;
    this.AlignCenter = 1 << 1;
    this.AlignRight = 1 << 2;
    this.AlignTop = 1 << 3;
    this.AlignMiddle= 1 << 4;
    this.AlignBottom= 1 << 5;


    this.numberOfLines = 1;
    this.textAlignVal = 0;
    var _this = this;
    Object.defineProperty(this, 'textAlign', {
        set: function (newValue) {
            _this.setTextAlignment(newValue);

        },
        get: function () {
            return _this.textAlignVal;
        },
        enumerable: true,
        configurable: true
    });


    Object.defineProperty(this, 'text', {
        set: function (newValue) {

            _this.label.innerHTML=newValue&&newValue.replace(/(?:\r\n|\r|\n)/g, '<br />')||'';
        },
        get: function () {
            return _this.label.innerHTML.replace(/<br\s*[\/]?>/gi, '\n');
        },
        enumerable: true,
        configurable: true
    });

    // Init view
    View.prototype.init.call(this, this.div);
    this.setTextAlignment(this.AlignMiddle);

};

// Subclass
Label.prototype = Object.create(View.prototype);
Label.prototype.constructor = Label;


Label.prototype.setTextColor = function (r, g, b, a) {
    this.label.style.color="rgba(" + (r * 255) + "," + (g * 255) + "," + (b * 255) + "," + a + ")";
};

Label.prototype.setFont = function (fontName, size) {
    // Check font name
    if (!fontName) {

        this.label.style["font-family"] = 'Helvetica, Arial';
        this.label.style["font-size"] = size + "px";
    } else {
        this.label.style["font-family"] = fontName + ",Helvetica, Arial";
        this.label.style["font-size"] = size + "px";
    }
};

Label.prototype.setTextAlignment = function (align) {

    this.textAlignVal = align;


    var transX = '';
    var transY = '';
    this.label.style["text-align"] = "left";

    if (this.textAlignVal & this.AlignLeft) {

        this.label.style["text-align"] = "left";
        //transX = '0';
    }
    else
    if (this.textAlignVal & this.AlignRight) {

        this.label.style["text-align"] = "right";
       // transX = '0';
    }
    else
    if (this.textAlignVal & this.AlignCenter) {

        this.label.style["text-align"] = "center";
       // transX='0';
    }
    else
    {
        this.label.style["text-align"]= "left";
        //transX='0';
    }

    if (this.textAlignVal & this.AlignTop) {
        this.textContainer.style.top='0';
        this.textContainer.style.bottom = '';
        //transY='0';
    }
    else
    if (this.textAlignVal & this.AlignMiddle) {
       // transY='-50%';
       // this.textContainer.style.top="50%";
        this.textContainer.style.top='0';
        this.textContainer.style.bottom='0';
    }
    else
    if (this.textAlignVal & this.AlignBottom) {
        this.textContainer.style.top='';
        this.textContainer.style.bottom='0';
        //transY='0';
    }
    else
    {

        this.textContainer.style.top='0';
        this.textContainer.style.bottom='0';
        //this.textContainer.style.bottom='';
        //this.textContainer.style.top='50%';
        //transY='-50%';

    }

   // this.textContainer.style["-webkit-transform"]=" translateY("+transY+")";
    //this.textContainer.style["-ms-transform"]=" translateY("+transY+")";
    //this.textContainer.style["transform"]=" translateY("+transY+")";


};

Label.prototype.sizeToFit = function () {

}
module.exports=Label;


