var V3DFace = require("./V3DFace");
var Events = require("../../Code/Events");
var EventListener = require("../../Code/EventListener");

var V3DFolderFace = function (vatomView, face) {
    V3DFace.call(this, vatomView, face);

    this.combineOverlay = document.createElement("div");
    this.combineOverlay.style.cssText = 'position: absolute; top: 20px; left: 20px; width: calc(100% - 40px); height: calc(100% - 40px); z-index: 1; border: 4px dashed #AAA; background-color: rgba(0, 0, 0, 0.1); border-radius: 8px; display: none; justify-content: center; align-items: center; font-family: Helvetica, Arial; font-size: 30px; color: #AAA; ';
    this.combineOverlay.innerHTML = '+';
    this.eventListener = new EventListener();
    //this.eventListener.pause();
    Events.addListener(this.eventListener);
    this.eventListener.registerEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.registerEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.registerEvent("vatom.combine-failed", this.combineCancel.bind(this));
    this.eventListener.registerEvent("vatom.combine", this.combine.bind(this));
    //console.log("v3d folder face...");
};
// Subclass
V3DFolderFace.prototype = Object.create(V3DFace.prototype);
V3DFolderFace.prototype.constructor = V3DFolderFace;


V3DFolderFace.prototype.onUnload = function () {

    V3DFace.prototype.onUnload.call(this);
    this.eventListener.unregisterEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-failed", this.combineCancel.bind(this));
    this.eventListener.unregisterEvent("vatom.combine", this.combine.bind(this));
    Events.removeListener(this.eventListener);
};
V3DFolderFace.prototype.combinePossible = function (data) {

    //console.log("combine possible?")
    if (data.vid == this.vatom.identifier) {
        this.combineOverlay.style.display = 'flex';
        this.show(this.combineOverlay,true)
    }

};
V3DFolderFace.prototype.combineCancel = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.combineOverlay.style.display = 'none';
    }
};
V3DFolderFace.prototype.combine = function (data) {

    console.log("combine",data,this.vatom.identifier,data.vid==this.vatom.identifier);
    var onsuccess = function(message)
    {
        console.log("combine-success",data);
        Events.callEvent("vatom.combine-success", data.vatom);
    };
    var onfail = function()
    {
        console.log("combine-failed",data);
        Events.callEvent("vatom.combine-failed", data.vatom);
    };
    if (data.vid == this.vatom.identifier) {

        this.combineOverlay.style.display = 'none';
        data.vatom.patch({
            parent_id: this.vatom.identifier || ".",
            visibility: {
                type: "owner",
                value: "*"
            }
        },onsuccess.bind(this),onfail.bind(this));
        Events.callEvent("vatom.combine", data.vatom);

    }

};

module.exports = V3DFolderFace;



