var Events = require("./Events")
;var VatomicWebSocketListener = function () {
    this.messageQueue = [];
    this.isPaused = false;

};

VatomicWebSocketListener.prototype.start = function () {
    //console.log("socket listener started...");
    Events.registerEvent("websocket.inventory.changed", this.inventoryChanged.bind(this));
    Events.registerEvent("websocket.vatom.state.changed", this.vatomStateChanged.bind(this));
    Events.registerEvent("websocket.rpc", this.rpc.bind(this));
    Events.registerEvent("websocket.request.cmd", this.requestCommand.bind(this));
};
VatomicWebSocketListener.prototype.stop = function () {
    //console.log("socket listener closed...");
    Events.unregisterEvent("websocket.inventory.changed", this.inventoryChanged.bind(this));
    Events.unregisterEvent("websocket.vatom.state.changed", this.vatomStateChanged.bind(this));
    Events.unregisterEvent("websocket.rpc", this.rpc.bind(this));
    Events.unregisterEvent("websocket.request.cmd", this.requestCommand.bind(this));
};
VatomicWebSocketListener.prototype.pause = function () {
    this.isPaused = true;
};
VatomicWebSocketListener.prototype.resume = function () {

    this.isPaused = false;
    while (this.messageQueue.length > 0) {
        var message = this.messageQueue[0];
        this.messageQueue.splice(0, 1);
        
        if (message.type == 'inventory.changed') {
            if (this.onInventoryChanged) {
                this.onInventoryChanged(message.payload);
            }
        }
        else if (message.type == 'vatom.state.changed') {
            if (this.onVatomStateChanged) {
                this.onVatomStateChanged(message.payload);
            }
        }
        else if (message.type == 'rpc') {
            if (this.onRPC) {
                this.onRPC(message.payload);
            }
        }
        else if (message.type == 'request.cmd') {
            if (this.onRequestCommand) {
                this.onRequestCommand(message.payload);
            }
        }
    }
};

VatomicWebSocketListener.prototype.inventoryChanged = function (payload) {
    if (this.isPaused) {
        this.messageQueue.push({type: 'inventory.changed', payload: payload});
    }
    else if (this.onInventoryChanged) {
        this.onInventoryChanged(payload);
    }

};
VatomicWebSocketListener.prototype.vatomStateChanged = function (payload) {
    if (this.isPaused) {
        //console.warn("Receiving messages and listener paused...");
        this.messageQueue.push({type: 'vatom.state.changed', payload: payload});
    }
    else if (this.onVatomStateChanged) {
        this.onVatomStateChanged(payload);
    }
};
VatomicWebSocketListener.prototype.rpc = function (payload) {
    if (this.isPaused) {
        this.messageQueue.push({type: 'rpc', payload: payload});
    }
    else if (this.onRPC) {
        this.onRPC(payload);
    }
};
VatomicWebSocketListener.prototype.requestCommand = function (payload) {
    if (this.isPaused) {
        this.messageQueue.push({type: 'request.cmd', payload: payload});
    }
    else if (this.onRequestCommand) {
        this.onRequestCommand(payload);
    }
};

VatomicWebSocketListener.prototype.free = function()
{
    this.stop();
    this.onInventoryChanged = null;
    this.onVatomStateChanged = null;
    this.onRPC = null;
    this.onRequestCommand = null;
};

module.exports = VatomicWebSocketListener;


