class LocalStorage {

    /** Constructor */
    constructor() {

        // Properties
        this.cached = {}

    }

    /** See getItem() */
    get(name) {
        return this.getItem(name)
    }

    /** Gets a JSON-encoded value from localStorage */
    getItem(name) {

        // Look for cached value
        if (this.cached[name])
            return this.cached[name]

        // Fetch string from localStorage
        var str = ""
        try {
            str = localStorage[name]
        } catch (e) {
            console.warn(e)
            return null
        }

        // Check for null
        if (!str)
            return str

        // Convert to JSON
        try {
            return JSON.parse(str)
        } catch (e) {
            console.warn(e)
            return str
        }

    }

    /** See setItem() */
    set(name, value) {
        return this.setItem(name, value)
    }

    /** Stores a value into local storage, JSON encoded */
    setItem(name, value) {

        // Store in cache
        this.cached[name] = value

        // Store into local storage
        try {
            if (!value)  localStorage.removeItem(name)
            else        localStorage[name] = JSON.stringify(value)
        } catch (e) {
            console.warn(e)
        }

    }

    /** See removeItem() */
    remove(name) {
        return this.removeItem(name)
    }

    /** Removes an item from local storage */
    removeItem(name) {
        return this.setItem(name, null)
    }

}

module.exports = new LocalStorage()



