var View = require('./View');
var Events =require("../../../Code/Events");

var ImageView = function()
{
    View.call(this);

// Image modes
    this.ScaleAspectFill = 1;
    this.ScaleAspectFit = 2;
    this.ScaleToFit = 3;
    this.ScaleToFill = 3;
    this.Contain = 4;

    //var
    this.mode = 4;
    this.div = document.createElement("DIV");
    this.div.style.overflow='hidden';
    this.div.style['background-repeat']='no-repeat';
    this.div.style['background-position']='center';
   // $("<div class = 'vatom-view vatom-image' style= 'overflow:hidden;background-repeat: no-repeat;   background-position: center;'></div>");
    // Init view
    View.prototype.init.call(this, this.div);

    this.div.style['box-sizing']='border-box';
    this.div.style['background-origin']='content-box';
    this.div.style.backgroundSize='contain';
    
    this.imageResource = null;
    var _this = this;


    /** Image resource to display */
    Object.defineProperty(this, 'image', {
        set: function(newValue) {
            if(newValue) {
                Events.unregisterEvent("vatom.resource.changed",_this.onResourceLoaded.bind(_this));

                _this.imageResource =newValue;
               if(newValue.data==null)//resource is not loaded yet
               {
                   newValue.priority = newValue.priority+1;

                   Events.registerSingleCallEvent("vatom.resource.changed",_this.onResourceLoaded.bind(_this));//register as single call event so it is cleared automaticly once called

               }
                else
               {
                   _this.div.style["background-image"]="Url(" + newValue.URL + ")";
                   _this.imageMode = _this.mode;
               }



            }

        },
        enumerable: true,
        configurable: true
    });

    /** Image scale mode */
    Object.defineProperty(this, 'imageMode', {
        set: function(newValue) {
            _this.mode = newValue;


            if(!_this.imageResource) return;
            if (newValue == 1) {

                _this.div.style.backgroundSize="cover";
            }
            else if (newValue == 2) {

               _this.div.style.backgroundSize="contain";
            }
            else if (newValue == 3) {
                _this.div.style.backgroundSize="100% 100%";
            }
            else if (newValue == 4) {



                if(_this.imageResource.width<_this.div.offsetWidth&&_this.imageResource.height<_this.div.offsetHeight)
                {
                    _this.div.style.backgroundSize="initial";
                }
                else {
                    _this.div.style.backgroundSize= "contain";

                    ////console.log("background size..");
                }

            }
        },
        enumerable: true,
        configurable: true
    });


};

// Subclass

ImageView.prototype = Object.create(View.prototype);
ImageView.prototype.constructor = ImageView;

ImageView.prototype.setImagePadding = function(top, right, bottom, left)
{
    this.paddingTop = top;
    this.paddingRight = right;
    this.paddingBottom = bottom;
    this.paddingLeft = left;
    this.div.style["padding"]=top+"px "+right+"px "+bottom+"px "+left+"px";
    this.imageMode=this.mode;
};
ImageView.prototype.onResourceLoaded = function(value)
{

        if(value==this.imageResource&&value)
        {
            this.div.style["background-image"]="Url(" + (value.data||value.URL) + ")";
            this.imageMode = this.mode;//recall image mode
            return true;
        }
    return false;

};
ImageView.prototype.didResize = function(oldWidth, oldHeight, newWidth, newHeight)
{
    //console.log("image did resize: old "+oldWidth+"x"+oldHeight+" new "+newWidth+"x"+newHeight);
    View.prototype.didResize.call(this,oldWidth, oldHeight, newWidth, newHeight);
    this.imageMode = this.mode;
};

module.exports=ImageView;


