var VatomResource = require('./VatomResource');
var Events = require("../Events");
/** @class Represents an image resource attached to a vatom */
var VatomV3DResource = function (url) {
    VatomResource.call(this, url);
    
};

// Subclass
VatomV3DResource.prototype = Object.create(VatomResource.prototype);
VatomV3DResource.prototype.constructor = VatomV3DResource;


VatomV3DResource.prototype.getBlob = function (callback) {
    if (!callback && !this.state == VatomResource.states.Loaded)
        return false;

    var xhr = new XMLHttpRequest();
    xhr.open('GET', this.data, true);
    xhr.responseType = 'blob';
    xhr.onload = function (e) {
        if (this.status == 200) {

            callback(this.response);
            // myBlob is now the blob that the object URL pointed to.
        }
    };
    xhr.send();
    return true;


};
module.exports = VatomV3DResource;



