var RootView = require("./ScriptFace/RootView");
var AudioPlayer = require("./ScriptFace/AudioPlayer");
var CanvasView = require("./ScriptFace/CanvasView");
var ScriptEvent = require("./ScriptFace/Event");
var ImageView = require("./ScriptFace/ImageView");
var Label = require("./ScriptFace/Label");
var LocalVatom = require("./ScriptFace/LocalVatom");
var Network = require("./ScriptFace/Network");
var ScrollView = require("./ScriptFace/ScrollView");
var VideoPlayer = require("./ScriptFace/VideoPlayer");
var WebView = require("./ScriptFace/WebView");
var View = require("./ScriptFace/View");
var VatomCanvasContext = require("./ScriptFace/CanvasContext");
var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var EventListener = require("../../Code/EventListener");
var ImageResource = require('../../Code/Resources/VatomImageResource');
var VatomTextResource = require('../../Code/Resources/VatomTextResource');

var ScriptFace = function (vatomView, face) {
    Face.call(this, vatomView, face);

    if (!vatomView || !face)return;

    this.script = (this.face.properties.resources.length > 0 && this.vatom.resources[this.face.properties.resources[0]]) || this.vatom.resources["MainScript"];
    console.log("created script face");

    // Add icon root view
    this.iconView = new RootView();
    this.iconView.devName = "Icon";
    this.iconView.element.style.position = "absolute";
    this.iconView.element.style.top = "0px";
    this.iconView.element.style.left = "0px";
    this.iconView.element.style.width = "100%";
    this.iconView.element.style.height = "100%";
    //this.vatomView.iconView.appendChild(this.iconView.element);

    // Add fullscreen root view
    this.fullscreenView = new RootView();
    this.fullscreenView.element.style.position = "absolute";
    this.fullscreenView.element.style.top = "0px";
    this.fullscreenView.element.style.left = "0px";
    this.fullscreenView.element.style.width = "100%";
    this.fullscreenView.element.style.height = "100%";

    this.onResourceChanged = function (resource) {


        if (resource == this.script) {
            this.onLoad();
        }
        else {
            for (var i = 0; i < this.face.properties.resources.length; i++) {
                if (this.vatom.resources[this.face.properties.resources[i]] == resource) {
                    this.onLoad();
                    return;
                }
            }
        }

    }.bind(this);
    Events.registerEvent("vatom.resource.changed", this.onResourceChanged);

};
// Subclass
ScriptFace.prototype = Object.create(Face.prototype);
ScriptFace.prototype.constructor = ScriptFace;

ScriptFace.prototype.onLoad = function () {


    if (this.script) {

        for (var i = 0; i < this.face.properties.resources.length; i++) {
            if (this.vatom.resources[this.face.properties.resources[i]].state == VatomResource.states.Error) {
                this.showError();
                Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
                return;
            }
            else if (this.vatom.resources[this.face.properties.resources[i]].state == VatomResource.states.Loading) {
                Events.registerEvent("vatom.resource.changed", this.onResourceChanged);
                this.showLoading();
                return;
            }
        }

        if (this.script.state == VatomResource.states.Loaded) {

            try {
                // Expose log function
                var log = function (message) {
                    //console.log(message);
                };

                // Expose local vatom
                var vatom = new LocalVatom(this.vatom);
                vatom.iconView = this.iconView.view;
                vatom.fullscreenView = this.fullscreenView.view;
                this.localVatom = vatom;

                // Expose inventory
                var inventory = null;
                if (inventory == null) {
                    inventory = {
                        vatoms: []
                    };
                }
                var Inventory = {};//VatomInventory;

                var viewer = {
                    properties: {},
                    sendMessage: function (message) {
                        return false;
                    }
                };
                // Execute script
                eval(this.script.text);
                vatom.onLoad&&vatom.onLoad();


            } catch (e) {
                console.error("Vatomic SDK: Error executing Javascript vatom! ", e);
            }

            if (this.face.properties.constraints["view_mode"] == "icon") {
                this.show(this.iconView.element);
            }
            else {
                console.log(this.fullscreenView.element);
                this.show(this.fullscreenView.element);
            }
            Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);

        }

    }
    else {
        this.showError();
    }

};

ScriptFace.prototype.free = function () {
    this.onUnload();
};

module.exports = ScriptFace;



