var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var VatomicWebSocketListener = require("../../Code/VatomicWebSocketListener");
var EventListener = require("../../Code/EventListener");
var Vatom = require("../../Code/Vatom");

var LayeredImageFace = function (vatomView, face) {
    Face.call(this, vatomView, face);

    if (!vatomView || !face)return;

    this.socketListener = new VatomicWebSocketListener();
    this.socketListener.pause();
    this.socketListener.onRPC = this.RPC.bind(this);
    this.socketListener.onVatomStateChanged = this.vatomStateChanged.bind(this);
    this.socketListener.start();
    this.eventListener = new EventListener();
    this.eventListener.pause();
    Events.addListener(this.eventListener);

    this.activatedImage = this.vatom.resources.ActivatedImage;
    this.imageView = document.createElement("div");
    this.imageView.style.cssText = 'position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; background-size: contain; background-position: center; background-repeat: no-repeat; ';
    this.splitButton = document.createElement("div");
    this.splitButton.style.cssText = 'position: absolute; top: 5%; left: 5%; padding: 5px 30px; font-family: Helvetica, Arial; font-size: 17px; color: #FFF; border-radius: 8px; background-color: rgba(200, 200, 200, 0.8); z-index: 1; ';
    this.splitButton.innerHTML = "Split";
    this.combineOverlay = document.createElement("div");
    this.combineOverlay.style.cssText = 'position: absolute; top: 20px; left: 20px; width: calc(100% - 40px); height: calc(100% - 40px); z-index: 1; border: 4px dashed #AAA; background-color: rgba(0, 0, 0, 0.1); border-radius: 8px; display: none; justify-content: center; align-items: center; font-family: Helvetica, Arial; font-size: 30px; color: #AAA; ';
    this.combineOverlay.innerHTML = '+';

    this.splitButton.addEventListener("click", function () {

        if (this.children) {
            //console.log(this.children);
            for (var i = 0; i < this.children.length; i++)
                this.children[i].patch({
                    parent_id: this.vatom.properties.parentID || ".",
                    visibility: {
                        type: "owner",
                        value: "*"
                    }
                });
        }


    }.bind(this));

    this.onResourceChanged = function (resource) {

        if (resource == this.activatedImage) {

            this.onLoad();
        }
    }.bind(this);


    this.onLoad = this.onLoad.bind(this);
    Events.registerEvent("vatom.resource.changed", this.onResourceChanged);


    this.eventListener.registerEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.registerEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.registerEvent("vatom.combine", this.combine.bind(this));


};

// Subclass
LayeredImageFace.prototype = Object.create(Face.prototype);
LayeredImageFace.prototype.constructor = LayeredImageFace;

LayeredImageFace.prototype.onLoad = function () {

    if (this.activatedImage && this.activatedImage.state == VatomResource.states.Loaded || this.activatedImage.state == VatomResource.states.Error) {

        // Set image
        this.imageView.style.backgroundImage = "url('" + (this.activatedImage.data || this.activatedImage.URL) + "')";

        this.show(this.imageView);
        this.show(this.combineOverlay, true);
        this.vatom.listChildren(function (vatoms) {

            this.children = vatoms;
            if (vatoms) {
                if (!this.properties['split-disabled']) {
                    this.show(this.splitButton, true);
                }
                for (var i = 0; i < vatoms.length; i++) {
                    if (vatoms[i].resources["ActivatedImage"].URL)
                        this.addImage(vatoms[i].identifier,vatoms[i].resources["ActivatedImage"].data||vatoms[i].resources["ActivatedImage"].URL);
                }
            }
        }.bind(this));
        this.socketListener.resume();
        this.eventListener.resume();
        this.onLoadComplete();
    }
    else {
        this.showLoading();
    }

};
// Adds a new image overlay
LayeredImageFace.prototype.addImage = function (id,url) {
    this.removeImage(id);
    // Create element
    var lastImg = document.createElement("div");

    lastImg.style.cssText = 'position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; background-size: contain; background-position: center; background-repeat: no-repeat;  opacity: 1; transform: translate(0, 0); transition: transform 0.5s, opacity: 0.5s; ';
    lastImg.style.backgroundImage = "url('" + url + "')";
    lastImg.className = "splittable "+id;
    this.show(lastImg, true);
    return lastImg;

};
LayeredImageFace.prototype.removeImage = function (id) {

    for (var i = 0; i < this.element.childNodes.length; i++) {
        if (this.element.childNodes[i].className.indexOf(id)>-1) {
            this.element.removeChild(this.element.childNodes[i]);
            i--;
        }
    }

};

LayeredImageFace.prototype.onUnload = function () {

    Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
    this.eventListener.unregisterEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.unregisterEvent("vatom.combine", this.combine.bind(this));
    this.socketListener.stop();
};

LayeredImageFace.prototype.RPC = function (data) {

    if (data && data.template_variation && data.rpc && data.template_variation == this.vatom.templateVariation) {

    }
};
LayeredImageFace.prototype.vatomStateChanged = function (data) {


    if (data) {
        if (data.new_object && data['new_object']['vAtomic::v1::vAtom::vAtomType'] && data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id && data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id != this.vatom.identifier) {
            if (this.children && this.children.length > 0) {
                for (var i = 0; i < this.children.length; i++) {
                    if (this.children[i].identifier == data.id) {
                        this.removeImage(data.id);
                        this.children.splice(i, 1);
                        if (this.children.length == 0) {
                            this.element.removeChild(this.splitButton);
                        }
                        return;
                    }
                }
            }
        }

        if (data.new_object && data['new_object']['vAtomic::v1::vAtom::vAtomType'] && data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id && data['new_object']['vAtomic::v1::vAtom::vAtomType'].parent_id == this.vatom.identifier) {

            Vatom.getVatom(data.id, function (vatom) {
                if (!this.children) {
                    this.children = [];
                }
                this.children.push(vatom);
                vatom.element = this.addImage(vatom.identifier,vatom.resources["ActivatedImage"].URL);
                if (!this.properties['split-disabled']) {
                    this.show(this.splitButton, true);
                }
            }.bind(this));


        }

    }
};

LayeredImageFace.prototype.combinePossible = function (data) {
    if (data.vid == this.vatom.identifier) {

        this.combineOverlay.style.display = 'flex';
    }

};
LayeredImageFace.prototype.combineCancel = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.combineOverlay.style.display = 'none';
    }
};
LayeredImageFace.prototype.combine = function (data) {
    var onsuccess = function(message)
    {
        //console.log(message);

        Events.callEvent("vatom.combine-success", data.vatom);
    };
    var onfail = function()
    {
        Events.callEvent("vatom.combine-failed", data.vatom);
    };
    if (data.vid == this.vatom.identifier) {
        this.combineOverlay.style.display = 'none';
        data.vatom.patch({
            parent_id: this.vatom.identifier || ".",
            visibility: {
                type: "owner",
                value: "*"
            }
        },onsuccess.bind(this),onfail.bind(this));
        Events.callEvent("vatom.combine", data.vatom);

    }

};


module.exports = LayeredImageFace;



