var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var EventListener = require("../../Code/EventListener");


var FolderFace = function (vatomView, face) {
    Face.call(this, vatomView, face);

    if (!vatomView || !face)return;


    this.eventListener = new EventListener();
    this.eventListener.pause();
    Events.addListener(this.eventListener);

    this.activatedImage = this.vatom.resources.FolderHeader;
    this.imageView = document.createElement("div");
    this.imageView.style.cssText = 'position: absolute; top: 0px; left: 0px; width: 100%; height: 100%; background-size: contain; background-position: center; background-repeat: no-repeat; ';
    this.combineOverlay = document.createElement("div");
    this.combineOverlay.style.cssText = 'position: absolute; top: 20px; left: 20px; width: calc(100% - 40px); height: calc(100% - 40px); z-index: 1; border: 4px dashed #AAA; background-color: rgba(0, 0, 0, 0.1); border-radius: 8px; display: none; justify-content: center; align-items: center; font-family: Helvetica, Arial; font-size: 30px; color: #AAA; ';
    this.combineOverlay.innerHTML = '+';

    this.onResourceChanged = function (resource) {

        if (resource == this.activatedImage) {

            this.onLoad();
        }
    }.bind(this);


    this.onLoad = this.onLoad.bind(this);
    Events.registerEvent("vatom.resource.changed", this.onResourceChanged);


    this.eventListener.registerEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.registerEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.registerEvent("vatom.combine", this.combine.bind(this));


};

// Subclass
FolderFace.prototype = Object.create(Face.prototype);
FolderFace.prototype.constructor = FolderFace;

FolderFace.prototype.onLoad = function () {

    if (this.activatedImage && this.activatedImage.state == VatomResource.states.Loaded || this.activatedImage.state == VatomResource.states.Error) {

        // Set image
        this.imageView.style.backgroundImage = "url('" + (this.activatedImage.data || this.activatedImage.URL) + "')";
        this.show(this.imageView);
        this.show(this.combineOverlay, true);
        this.eventListener.resume();
        this.onLoadComplete();
    }
    else {
        this.showLoading();
    }

};



FolderFace.prototype.onUnload = function () {

    Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
    this.eventListener.unregisterEvent("vatom.combine-possible", this.combinePossible.bind(this));
    this.eventListener.unregisterEvent("vatom.combine-cancel", this.combineCancel.bind(this));
    this.eventListener.unregisterEvent("vatom.combine", this.combine.bind(this));
};




FolderFace.prototype.combinePossible = function (data) {
    if (data.vid == this.vatom.identifier) {

        this.combineOverlay.style.display = 'flex';
    }

};
FolderFace.prototype.combineCancel = function (data) {
    if (data.vid == this.vatom.identifier) {
        this.combineOverlay.style.display = 'none';
    }
};
FolderFace.prototype.combine = function (data) {
    var onsuccess = function(message)
    {
        //console.log(message);

        Events.callEvent("vatom.combine-success", data.vatom);
    };
    var onfail = function()
    {
        Events.callEvent("vatom.combine-failed", data.vatom);
    };
    if (data.vid == this.vatom.identifier) {
        this.folderCombineOverlay.style.display = 'none';
        data.vatom.patch({
            parent_id: this.vatom.identifier || ".",
            visibility: {
                type: "owner",
                value: "*"
            }
        },onsuccess.bind(this),onfail.bind(this));
        Events.callEvent("vatom.combine", data.vatom);

    }


};


module.exports = FolderFace;



