var VatomicFaceManager = require("../Code/VatomicFaceManager");
var VatomicWebSocketListener = require("../Code/VatomicWebSocketListener");
var Face = require("./NativeFaces/Face");
var VatomFaceView = function (face, vatom, constraints, properites) {
    this.element = document.createElement("div");
    this.element.style.position = "relative";
    this.element.style.width = "100%";
    this.element.style.height = "100%";

    this.vatom = vatom;
    this.constraints = constraints;
    this.properties = properites;
    this.face = face;
    this.view = null;
    this.init();

};

VatomFaceView.prototype.init = function () {

    this.view = new Face();
    this.element.appendChild(this.view.element);
    if (!this.face) {
        this.view.showError();
    }
    else {
        this.view.showLoading();
        this.socketListener = new VatomicWebSocketListener();
        this.socketListener.pause();
        this.socketListener.onVatomStateChanged = this.vatomStateChanged.bind(this);
        this.socketListener.start();
        console.log(this.face);
        VatomicFaceManager.getFace(this.vatom, this.face, this.properties, this.setFace.bind(this));

    }


};
VatomFaceView.prototype.setFace = function (view) {

    if(!view)
    {
        this.view.showError();
    }
    else {
        this.onUnload();
        while (this.element.children.length > 0) {
            this.element.removeChild(this.element.children[0]);
        }
        this.view = view;
        this.element.appendChild(this.view.element);
        this.onLoad();
    }
};
VatomFaceView.prototype.onResize = function () {
    this.view && this.view.onResize && this.view.onResize();
};

VatomFaceView.prototype.onLoad = function () {
    console.log("onload");
    this.view && this.view.onLoad && this.view.onLoad();
    this.socketListener.resume();
    this.onResize();
};

VatomFaceView.prototype.onUnload = function () {
    this.socketListener.stop();
    this.view && this.view.onUnload && this.view.onUnload();
};


VatomFaceView.prototype.free = function () {
    this.onUnload();
    this.view = null;
    this.vatom = null;
    this.constraints = null;
    this.face = null;
    this.element = null;

};
VatomFaceView.prototype.viewChanged = function (iconView, containerView, isFullscreen) {
    this.view && this.view.viewChanged && this.view.viewChanged(iconView, containerView, isFullscreen);
};
VatomFaceView.prototype.vatomStateChanged = function (data) {
    //console.log("vatom state chnaged")
    if (data && data.id == this.vatom.identifier && data.new_object) {
        this.vatom.update(data.new_object);

    }

};

VatomFaceView.prototype.setProperty = function (key, value) {
    this.properties[key] = value;
    if (this.view)
        this.view.setProperty(key, value);
};

VatomFaceView.prototype.getProperty = function (key) {
    return this.properties[key];
};

module.exports = VatomFaceView;



