var Events = require("./Events");
var LocalStorage = require("./LocalStorage");
var Vatom = require("./Vatom.js");
var VatomDiscoverFilter = require("./VatomDiscoverFilter.js");
var EventEmitter = require("./Utils/EventEmitter.js");

import Log from "./Log"


var Inventory = function (fetchVatoms) {
    this.vatoms = [];
    this.pageToken = '';
    this.pageCount = 400;
    this.fetchVatoms = fetchVatoms;
    this.isFetching = false;

    // Check if discover filter was passed in
    if (fetchVatoms instanceof VatomDiscoverFilter) {

        // Create a fetch function that uses this discover filter
        var filter = fetchVatoms;
        this.fetchVatoms = filter.createInventoryFetchFunction();

    }

    // Add event listeners TODO: When WebSockets are stable, we can remove this manual full refresh
    Events.addEventListener("websocket.vatom.state.changed", this.refresh.bind(this));

};

// Add event emitter support
EventEmitter.mixin(Inventory.prototype);

/** Caches the contents of this inventory based on the name used here */
Object.defineProperty(Inventory.prototype, "cacheName", {
    get: function() {

        // Return previous cache name
        return this._cacheName;

    },
    set: function(val) {

        // Store new cache name
        this._cacheName = val;

        // Save items to cache if there are some ...
        if (this.vatoms.length > 0)
            return this.saveCache();

        // ... otherwise load items from the cache
        this.loadCache();

    }
});

/** @private Loads vatoms from the cache */
Inventory.prototype.loadCache = function() {

    // Check if we have a cache name
    if (!this.cacheName)
        return;

    // Remove old vatoms
    this.vatoms = [];

    // Fetch and process cached vatoms
    var cachedVatomDescriptors = LocalStorage.get("vatom-cache-" + this.cacheName) || [];
    for (var i = 0 ; i < cachedVatomDescriptors.length ; i++)
        this.vatoms.push(new Vatom(cachedVatomDescriptors[i]));

}

/** @private Saves vatoms to the cache */
Inventory.prototype.saveCache = function() {

    // Check if we have a cache name
    if (!this.cacheName)
        return;

    // Create a list of all vatom descriptors
    var vatomDescriptors = [];
    for (var i = 0 ; i < this.vatoms.length ; i++)
        vatomDescriptors.push(this.vatoms[i]._descriptor);

    // Save
    LocalStorage.set("vatom-cache-" + this.cacheName, vatomDescriptors);

}

/** @private Called when the state of the inventory changes */
Inventory.prototype.onUpdated = function () {

    // Trigger events
    Events.callEvent("inventory.changed", {Inventory: this});
    this.triggerEvent("changed");

    // Save cache
    this.saveCache();

};

/** Refreshes the inventory contents */
Inventory.prototype.refresh = function () {

    // Check if we have a fetch function
    Log.debug("Inventory", "Starting refresh", this)
    if (!this.fetchVatoms)
        return;

    // Stop if already running
    if (this.isFetching) return;
    this.isFetching = true;

    // Create success callback
    var success = (data, pageToken) => {

        // Check for data
        this.isFetching = false;
        if (!data)
            return;

        // Store latest page token
        this.pageToken = pageToken;

        // Add new vatoms
        for (var i = 0 ; i < data.length ; i++)
            this.addVatom(data[i]);

        // If no page token this is a full refresh, remove vatoms that no longer exist
        if (!pageToken) {
            for (var i = 0 ; i < this.vatoms.length ; i++) {

                // Check if exists in new list
                var vatom = this.vatoms[i];
                var found = false;
                for (var x = 0 ; x < data.length ; x++) {
                    if (data[x].identifier == vatom.identifier) {
                        found = true;
                        break;
                    }
                }

                // If not found, remove it
                if (!found)
                    this.removeVatom(vatom);

            }
        }

        // Notify updated
        this.onUpdated();
        Events.callEvent("inventory.refresh-completed", {inventory: this});

    }

    // Create fail callback
    var fail = (error, errorText) => {

        // Notify failed
        this.isFetching = false;
        Events.callEvent("inventory.refresh-failed", {inventory: this});

    }

    // Execute our fetch function
    this.fetchVatoms(success, fail, this.pageToken, this.pageCount);

};

Inventory.prototype.addVatom = function (vatom) {
    var index = this.indexOf(vatom);
    if (index == -1) {
        Events.callEvent("inventory.vatom.added", {
            inventory: this,
            vatomId: vatom.identifier
        });
        this.vatoms.push(vatom);
    } else {

        Events.callEvent("inventory.vatom.updated", {
            inventory: this,
            vatomId: vatom.identifier
        });
        this.vatoms[index].updateVatom(vatom);
    }
};
Inventory.prototype.removeVatom = function (vatom) {
    var vid = vatom.identifier||vatom;
    for (var i = 0; i < this.vatoms.length; i++) {

        if (this.vatoms[i].identifier == vid) {

            //this.vatoms[i].free&&this.vatoms[i].free();
            this.vatoms.splice(i, 1);
            Events.callEvent("inventory.vatom.removed", {
                inventory: this,
                vatomId: vid
            });
            return;
        }
    }

};
Inventory.prototype.indexOf = function (vatom) {
    if(vatom) {
        var vid = vatom.identifier || vatom;
        for (var i = 0; i < this.vatoms.length; i++) {
            if (this.vatoms[i].identifier == vid) {
                return i;
            }
        }
    }
    return -1;
};


Inventory.prototype.contains = function (vatom) {
    return this.indexOf(vatom)>-1;
};

Inventory.prototype.getVatomsByTemplate = function(template)
{
    var out = [];
    for(var i = 0;i<this.vatoms;i++)
    {
        var vatom = this.vatoms[i];
        if(vatom.templateVariation==template)
        {
            out.push(vatom);
        }
    }
    return out;

};


/*Inventory.prototype.merge = function (inventory) {

    return new Inventory(this.vatoms.concat(inventory.vatoms));
};
*/

module.exports = Inventory;



