var EventListener = require("./EventListener");

/** @class Passes events between different components of the SDK */
var Events = {

    _listener:new EventListener(),
    listeners:[],
	/** Registers an event listener callback */
    registerEvent: function(name,callback) {
     if(Events._listener.registerEvent(name,callback))
        {
            return true;
        }
    },
    /** Registers an event listener callback that will only be fired once */
    registerSingleCallEvent: function(name,callback) {
        return Events._listener.registerSingleCallEvent(name,callback);
    },

	/** Triggers an event, and sends the event data to all registered listeners for this event */
    callEvent: function(name,data) {


        this.listeners.slice(0).forEach(function(listener){listener.callEvent(name,data)});

        Events._listener.callEvent(name,data);
    },

	/** Remove all listeners for a specified event */
    clearEvent: function(name) {
        Events._listener.callEvent(name);
    },

	/** Removes an event listener */
    unregisterEvent: function(name, callback) {
        if(Events._listener.unregisterEvent(name,callback))
        {
           // //console.error("removed",name);
            return true;
        }
    },
    addListener:function(listener)
    {
        if(listener instanceof EventListener)
        {   //maybe might want to check it is not already in?
            if(this.listeners.indexOf(listener)==-1) {
                this.listeners.push(listener);
            }
        }
    },
    removeListener:function(listener)
    {

        var idx = this.listeners.indexOf(listener);
        if(idx>-1) {
            this.listeners.slice(idx, 1);
        }
    }

};

// Add function aliases. TODO: Refactor this class, what is it doing?
Events.on                   = Events.registerEvent;
Events.addEventListener     = Events.registerEvent;
Events.off                  = Events.unregisterEvent;
Events.removeEventListener  = Events.unregisterEvent;
Events.emit                 = Events.callEvent;

module.exports = Events;



