var View = require('./View');
var WebView = function()
{

    View.call(this);

    this.div = document.createElement("DIV");//$("<div class = 'vatom-view vatom-webview' ></div>");//use div instead of iframe directly so you can still add children views
    this.frame =document.createElement("IFRAME");//
    this.frame.style.position='absolute';
    this.frame.style.width='100%';
    this.frame.style.height='100%';
    this.frame.style.border='0';// $("<iframe class='vatom-view' style ='position:absolute;width:100%;height:100%;border: 0;'></iframe>");
    this.div.appendChild(this.frame);

    this.src = '';
    var _this = this;
    // Init view
    View.prototype.init.call(this, this.div);

    //Sets webview src
    Object.defineProperty(this, 'url', {
        get: function() { return _this.src; },
        set: function(newValue) {
            _this.src = newValue;
            _this.frame.src= newValue;
        },
        enumerable: true,
        configurable: true
    });

}
// Subclass
WebView.prototype = Object.create(View.prototype);
WebView.prototype.constructor = WebView;

module.exports=WebView;



