var VatomImageResource = require('./VatomImageResource');
var VatomTextResource = require('./VatomTextResource');
var VatomVideoResource = require('./VatomVideoResource');
var VatomAudioResource = require('./VatomAudioResource');
var VatomResource = require("./VatomResource");
var VatomV3DResource = require("./VatomV3DResource");
var Events = require("../Events");
var LocalStorage = require("../LocalStorage")

import Log from '../Log'

// Constants
var MAX_SIMULTANEOUS_DOWNLOADS = 4

class VatomResourceManager {

    constructor() {

        // Properties
        this.currencyIcons = []
        this.queuedDownloads = []
        this.inProgressDownloads = []
        this.cachedResources = {}
        this.resourcePriorities = {
            Default: 0,
            Script: 10
        }

    }

    /** Gets the resource at the specified URL */
    getResource(url, type) {

        // Skip if no URL
        if (!url)
            return null

        // Check if got a cached resource object for this URL
        if (this.cachedResources[url])
            return this.cachedResources[url]

        // No cached object, we need to create one. Check resource type
        if (type.indexOf(":Image") != -1) {

            // Create image resource
            var resource = new VatomImageResource(this.transformURL(url))
            this.cachedResources[url] = resource
            return resource

        } else if (type.indexOf(":Text") != -1 || type.indexOf(":Javascript") != -1) {

            // Create text resource
            var resource = new VatomTextResource(this.transformURL(url))
            this.cachedResources[url] = resource
            return resource

        } else if (type.indexOf(":Audio") != -1) {

            // Create audio resource
            var resource = new VatomAudioResource(this.transformURL(url))
            this.cachedResources[url] = resource
            return resource

        } else if (type.indexOf(":Video") != -1) {

            // Create video resource
            var resource = new VatomVideoResource(this.transformURL(url))
            this.cachedResources[url] = resource
            return resource

        } else if (type.indexOf(":3D::Scene") != -1) {

            var resource = new VatomV3DResource(this.transformURL(url))
            this.cachedResources[url] = resource
            return resource

        } else {

            // Unknown resource type
            var resource = new VatomResource(this.transformURL(url));
            this.cachedResources[url] = resource;
            return resource;
        }

    }


    /** Clears cached resources */
    clearCachedResources() {
        this.cachedResources = {};
    }

    /** Adds a resource to the download queue. */
    downloadResource(res) {

        // Check if got a URL
        if (!res || !res.URL || res.data)
            return

        // Check if loaded already
        if (res.state.Loaded)
            return

        // Check if already in the queue
        if (this.queuedDownloads.indexOf(res) != -1 || this.inProgressDownloads.indexOf(res) != -1)
            return

        // Add to download queue
        res.state = VatomResource.states.Loading
        this.queuedDownloads.push(res)

        // Sort queue by priority, highest first
        this.queuedDownloads.sort((a, b) => b.priority - a.priority)

        // Start the next download
        this.startNextDownload();

    }

    /** @private Starts the next download in the queue */
    startNextDownload() {

        // Check if there are any more queued downloads
        if (this.queuedDownloads.length == 0)
            return

        // Check if the maximum number of items are downloading
        if (this.inProgressDownloads.length >= MAX_SIMULTANEOUS_DOWNLOADS)
            return

        // Select a resource to download
        var resource = this.queuedDownloads.shift()
        this.inProgressDownloads.push(resource)

        // Download it
        this.startDownload(resource);

        // Start next download
        this.startNextDownload();

    }

    /** @private Starts the download for the specified resource. TODO: What is going on in here? Fix this! */
    startDownload(res) {

        var downloadFinished = (data, err) => {

            //console.log("downloaded");
            //remove cancel
            if (res.cancelDownload) {
                res.cancelDownload = null;
            }

            // Remove from queue
            this.downloadComplete(res);

            // Check for data
            if (err != null || data == null) {
                return res.onLoadFailed(err);
            }

            // Store data
            res.memoryCachedData = data;


            // Notify complete
            res.onLoadedData()

        };
        var download = (param) => {
            var xhr = new XMLHttpRequest();
            var cancelDownload = () => {
                if (xhr) {
                    xhr.abort();
                }
                res.cancelDownload = null;
            };
            // Fetch the data

            try {


                //add cancel fucntion
                res.cancelDownload = cancelDownload;
                //Resource is an image
                if (res instanceof VatomImageResource || res instanceof VatomV3DResource) {

                    xhr.responseType = "blob";
                    xhr.open("GET", res.URL + (param||""));
                    xhr.withCredentials = true;

                    // Attach state change listener
                    xhr.onreadystatechange = () => {

                        // Check request state
                        if (xhr.readyState != 4) // DONE
                            return;

                        // Check response code
                        if (xhr.status == 200) {

                            var blob = new Blob([xhr.response]);
                            var data = URL.createObjectURL(blob);
                            downloadFinished(data);

                        }
                        else {
                            ///failed

                            if(!param)
                            {
                                //redownload with random param
                                //console.log("redownload");
                                download("?d="+Math.floor((Math.random() * 10000) + 1))
                            }
                            else
                            downloadFinished();
                        }
                    };

                    // Send request
                    xhr.send();
                }
                //Resources is text
                else if (res instanceof VatomTextResource) {

                    xhr.responseType = "text";
                    xhr.open("GET", res.URL);
                    xhr.withCredentials = true;

                    // Attach state change listener
                    xhr.onreadystatechange = () => {

                        // Check request state
                        if (xhr.readyState != 4) // DONE
                            return;

                        // Check response code
                        if (xhr.status == 200) {
                            downloadFinished(xhr.response);

                        }
                        else {
                            downloadFinished();
                        }
                    };

                    // Send request
                    xhr.send();
                }
                else {
                    downloadFinished(res.URL)
                }
            }
            catch (e) {
                //console.log(e);
            }
        }
        download();
    }

    /** @private Called when a download finishes, to remove it from the in-progress list */
    downloadComplete(res) {

        // Remove it from the in-progress list
        var idx = this.inProgressDownloads.indexOf(res)
        if (idx > -1)
            this.inProgressDownloads.splice(idx, 1)

        // Start another download
        this.startNextDownload();

    }

    /** @private Called to update our asset provider tokens */
    updateAssetProviders(providers) {

        // Make sure we have an array
        if (!providers || !providers.length)
            return

        // Store provider info
        for (var provider of providers)
            LocalStorage.set("assetprovider." + provider.name + "." + provider.type, provider.descriptor)

    }

    /** @private Add the appropriate Cloudfront fields to resource URLs */
    transformURL(url) {

        // Check if we are in the right domain
        if (!url.toLowerCase().startsWith("https://cdn.api.vatomic.net") && !url.toLowerCase().startsWith("https://cdn.vatomic.io"))
            return url

        // Add fields
        var descriptor = LocalStorage.get("assetprovider.vatomic.Cloudfront") || {}
        url += (url.indexOf("?") == -1 ? "?" : "&") + "Key-Pair-Id=" + encodeURIComponent(descriptor.CloudFrontKeyPairId)
        url += "&Signature=" + encodeURIComponent(descriptor.CloudFrontSignature || descriptor.CloudFronSignature)          // < NOTE: Server spelling mistake in Login payload
        url += "&Policy=" + encodeURIComponent(descriptor.CloudFrontPolicy)

        // Return it
        return url

    }

}

module.exports = new VatomResourceManager();



