//
// Tap gesture recognizer
var GestureRecognizer = require("./GestureRecognizer");
function TapGestureRecognizer() {
	
	// Init superclass
	GestureRecognizer.call(this);
	
	// Setup vars
	this.pointer = null;
	this.timeout = 0;
	
}

// Inherits from GestureRecognizer
TapGestureRecognizer.prototype = Object.create(GestureRecognizer.prototype);
TapGestureRecognizer.prototype.constructor = TapGestureRecognizer;

/** @private Called on pointer down. Return true if recognized. */
TapGestureRecognizer.prototype.onPointerDown = function(pointer) {
	
	// Check if got a pointer already
	//if (this.pointer)
	//	return false;
	
	// Store pointer
	this.pointer = pointer;

	this.lastX = this.pointer.x;
	this.lastY = this.pointer.y;
	
	// Change state
	this.setState(GestureRecognizer.GestureRecognizerStates.Possible);
	
	// Create timeout
	clearTimeout(this.timeout);
	var _this = this;
	this.timeout = setTimeout(function() {
	
		// Ignore if cancelled
		if (_this.state == GestureRecognizer.GestureRecognizerStates.Cancelled)
			return;
		
		// Event cancelled
		_this.pointer = null;
		_this.setState(GestureRecognizer.GestureRecognizerStates.None);
		
	}, 500);
	
}

/** @private Called when the pointer moves. Return true if recognized. */
TapGestureRecognizer.prototype.onPointerMove = function(pointer) {

	// Ignore if cancelled
	if (this.state == GestureRecognizer.GestureRecognizerStates.Cancelled)
		return;
	
	// Ignore if not same pointer
	if (this.pointer != pointer)
		return false;
	
	// Check if the move is too small to count
	//if (this.lastX - 5 > pointer.x && this.lastX + 5 < pointer.x && this.lastY - 5 > pointer.y && this.lastY + 5 < pointer.y)
	//	return;

	if(Math.sqrt(((this.lastX-pointer.x)*(this.lastX-pointer.x))+((this.lastY-pointer.y)*(this.lastY-pointer.y)))<6)
	return;

	// Tap gestures don't move
	this.pointer = null;
	this.setState(GestureRecognizer.GestureRecognizerStates.None);
	
	// Cancel timeout
	clearTimeout(this.timeout);
	
}

/** @private Called when the pointer goes up. Return true if recognized. */
TapGestureRecognizer.prototype.onPointerUp = function(pointer) {
	
	// Ignore if cancelled
	if (this.state == GestureRecognizer.GestureRecognizerStates.Cancelled)
		return;

	// Ignore if not same pointer
	if (this.pointer != pointer)
	{////console.log(this.pointer);
		////console.log(pointer);
		return false;}
	
	// State is now complete
	this.setState(GestureRecognizer.GestureRecognizerStates.Completed);
	
	// Cancel timeout
	clearTimeout(this.timeout);
	return true;
	
}

/** @private Called when the pointer event is cancelled. Return true if recognized. */
TapGestureRecognizer.prototype.onPointerCancel = function(pointer) {

	// Ignore if cancelled
	if (this.state == GestureRecognizer.GestureRecognizerStates.Cancelled)
		return;
	
	// Ignore if not same pointer
	if (this.pointer != pointer)
		return false;
	
	// State is now cancelled
	this.setState(GestureRecognizer.GestureRecognizerStates.Cancelled);
	
	// Cancel timeout
	clearTimeout(this.timeout);
	
}
module.exports=TapGestureRecognizer;


