var Network = {};

Network.fetch = function(url, settings, callback)
{

    var method = "GET";
    var payload = {};
    var headers = {};
    if(settings)
    {
        if(settings.method)
        {
            method=settings.method;
        }
        if(settings.body)
        {
            payload = settings.body;
        }
        if(settings.headers)
        {
            headers = settings.headers;
        }

    }


    if (!endpoint || endpoint.charAt(0) != '/' ) {
        // Fail
        onFail&&onFail("invalid_url", "The request URL provided was invalid.")
        return;
    }
    var url = Store.DEFAULTS.SERVER_ADDRESS + Request.API_PREFIX + endpoint;

    if (!(payload instanceof FormData))
    {
        payload = JSON.stringify(payload);
    }
    // Create request
    var req = new XMLHttpRequest();

    req.onreadystatechange = (function () {
        if (req.readyState != 4) return;

        try {
            if (req.status == 200) {

                onSuccess && onSuccess(JSON.parse(req.responseText).payload);
            }
            else {


                var response = JSON.parse(req.responseText);
                var err =Request.ERROR_CODES[response.error]||"Unknown code - "+response.error;
                var errText = Request.ERROR_MESSAGES[err]||response.message;

                onFail&&onFail(err, errText);

                if(err=="UNAUTHORIZED")
                {
                    lStorage.setItem("vatomic.isLoggedIn", false);
                }

            }
        }
        catch (e) {
            console.error(e);
            onFail&&onFail(req.status, "Unkown Error");
        }
    }).bind(this);

    req.onerror = function () {
        onFail&&onFail("network_error", "network status " + req.status);
    }.bind(this);

    req.open(method, url, true);

    // Add app headers
    req.setRequestHeader('X-Vatomic-App-Id', Store.DEFAULTS.APP_ID);
    req.withCredentials = true;
    contentType&&req.setRequestHeader("Content-type", contentType);

    // Create payload if needed
    if (method != "GET" && method != "DELETE") {
        req.send(payload);
    }
    else
        req.send();
    
    $.ajax({

        // POST options
        type: method,
        url: url,
        data: payload,
        headers: headers,
        success: function (response, status, xhr) {

            var out ={failed:false,text:xhr.responseText};
            callback(out);
        } 

    }).fail(function (xhr, err, errText) {

        var out ={failed:true,text: xhr.responseText};
        callback(out);
    });

};


Network.openURL = function(url)
{
    document.location = url;

};

module.exports=Network;


