var Face = require("./Face");
var VatomResource = require("../../Code/Resources/VatomResource");
var Events = require("../../Code/Events");
var VatomicWebSocketListener = require("../../Code/VatomicWebSocketListener");
var Store = require("../../Code/VatomicStore");

var SpriteSheetFace = function (vatomView, face) {
    Face.call(this, vatomView, face);

    if (!this.vatom || !this.face) {
        this.showError();
        return;
    }

    this.socketListener = new VatomicWebSocketListener();
    this.socketListener.pause();
    this.socketListener.onRPC = this.RPC.bind(this);
    this.socketListener.start();


    this.image = this.vatom.resources[this.face.properties.resources[0]];
    this.frame_width = this.vatom.properties.frame_width || 32;//	The width of a single frame, in pixels
    this.frame_height = this.vatom.properties.frame_height || 32;//	The height of a single frame, in pixels
    this.frame_spacing_x = this.vatom.properties.frame_spacing_x || 0;//	The amount of horizontal padding between frame columns in the sprite sheet
    this.frame_spacing_y = this.vatom.properties.frame_spacing_y || 0;//	The amount of vertical padding between frame rows in the sprite sheet
    this.frame_rate = this.vatom.properties.frame_rate || 30;//	The desired frame rate of the sprite animation in frames per second
    this.frame_count = this.vatom.properties.frame_count || 1;//	The number of frames in this animation
    this.sprite_states = this.vatom.properties.sprite_states;//An array of state objects, see table below
    this.canvas = document.createElement("canvas");
   // this.canvas.style.width = "100%";
   // this.canvas.style.height = "100%";
    this.canvasContext = this.canvas.getContext('2d');
    this.currentFrame = 0;
    //this.animation = //this.sprite_states[0];
    this.setAnimation(this.sprite_states[0],true);

    this.onResourceChanged = function (resource) {

        if (resource == this.image) {
            this.onLoad();
        }
    }.bind(this);
    Events.registerEvent("vatom.resource.changed", this.onResourceChanged);

};
// Subclass
SpriteSheetFace.prototype = Object.create(Face.prototype);
SpriteSheetFace.prototype.constructor = SpriteSheetFace;

SpriteSheetFace.prototype.onResize = function () {

    this.canvas.width = this.element.clientWidth;
    this.canvas.height = this.element.clientHeight;
};

SpriteSheetFace.prototype.onLoad = function () {
    Face.prototype.onLoad.call(this);

    if (this.image) {


            if (this.image.state == VatomResource.states.Loaded) {



                if(this.loaded)return;
                this.sprite = new Image;
                this.sprite.onload = function(){

                    this.show(this.canvas);
                    this.socketListener.resume();
                    this.loaded = true;
                    this.loadState(Store._states[this.vatom.identifier+"-"+this.face.id]);
                    this.interval = setInterval(function () {
                        this._loop();

                    }.bind(this), 1000 / this.frame_rate);


                }.bind(this);
                this.sprite.src = this.image.data;


                Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);


                this.onLoadComplete();

            }
            else if (this.image.state == VatomResource.states.Error) {
                Events.unregisterEvent("vatom.resource.changed", this.onResourceChanged);
                this.showError();
                this.onLoadError();

            }
            else {
                Events.registerEvent("vatom.resource.changed", this.onResourceChanged);
                this.showLoading();
            }

    }
    else {
        this.showError();
    }
};
SpriteSheetFace.prototype.onUnload = function () {
    if(this.interval)
    {
        clearInterval(this.interval);
        this.socketListener.stop();

    }
};
SpriteSheetFace.prototype.setAnimation = function (animation,dontSave) {

    if(animation) {
        this.animation = animation;

        if (animation.slide_in) {

            this.slide_in = true;
        }
        else {
            this.currentFrame = animation.frame_start;
        }


        !dontSave&&this.saveState({
            animation:this.animation.name,
            frame:animation.frame_start,
            slide_in:this.slide_in
        });
    }
};
SpriteSheetFace.prototype.getAnimationByName = function (name) {
    for (var i = 0; i < this.sprite_states.length; i++) {

        if(this.sprite_states[i].name == name)
        {
            return this.sprite_states[i];
        }
    }
    return null;
};
SpriteSheetFace.prototype.RPC = function (data) {


    console.log(data);
    if (data && (!data.template_variation|| data.template_variation == this.vatom.templateVariation) &&data.rpc) {

       var animation =  this.getAnimationByName(data.rpc);
        //console.log(animation);
        if(animation!=null)
        {
            this.setAnimation(animation);
        }

    }
};

SpriteSheetFace.prototype._loop = function () {

   // //console.log(this.animation);
    if (this.animation) {

        //clear
        this.canvasContext.clearRect(0, 0, this.canvas.width, this.canvas.height);


        var startx =  (this.frame_width+this.frame_spacing_x)*this.currentFrame;

        var row = Math.floor(startx/this.image.width);
        startx -= row*this.image.width;
        var starty = (this.frame_height+this.frame_spacing_y)*row;


        var width = this.canvas.width;//Math.min(this.canvas.width,this.canvas.height);

        var height = this.canvas.height;

        if(this.frame_width>this.frame_height)
        {

            width = this.canvas.width;
            height=width*(this.frame_height/this.frame_width);
            if(height>this.canvas.height)
            {
                height = this.canvas.height;
                width = height*(this.frame_width/this.frame_height);
            }

        }
        else
        {
            height = this.canvas.height;
            width= height*(this.frame_width/this.frame_height);
            if(width>this.canvas.width)
            {
                width = this.canvas.width;
                height=width*(this.frame_height/this.frame_width);
            }
        }


        var ox = Math.floor((this.canvas.width-width)/2);
        var oy = Math.floor((this.canvas.height-height)/2);

        this.canvasContext.drawImage(this.sprite,startx,starty,this.frame_width,this.frame_height,ox,oy,width, height)//,this.element.clientWidth,this.element.clientHeight);//ox,oy, Math.floor(width), Math.floor(height));

        if (!this.animation.backwards) {
            if (this.animation.frame_end||this.animation.frame_start == this.currentFrame) {

                this.slide_in = false;
                if (this.animation.loop) {
                    this.currentFrame = this.animation.frame_start;
                }
                else if (this.animation.next_state&&this.animation.next_state.length>0) {
                    this.setAnimation(getAnimationByName(this.animation.next_state));
                  /*  if (this.animation.slide_in) {
                        this.currentFrame++;
                    }*/
                }
            }
            else
            if (this.slide_in&&this.animation.frame_start < this.currentFrame)
            {
                this.currentFrame--;
            }
            else
            {   this.slide_in = false;
                this.currentFrame++;
            }
        }
        else {
            if (this.animation.frame_start||0 == this.currentFrame) {

                this.slide_in = false;
                if (this.animation.loop) {
                    this.currentFrame = this.animation.frame_end;
                }
                else if (this.animation.next_state&&this.animation.next_state.length>0) {
                    this.setAnimation(getAnimationByName(this.animation.next_state));
                   /* if (this.animation.slide_in) {
                        this.currentFrame--;
                    }*/
                }
            }
            else
            if (this.slide_in&&(this.animation.frame_end||this.animation.frame_start) < this.currentFrame)
            {
                this.currentFrame++;
            }
            else {
                this.slide_in = false;
                this.currentFrame--;
            }
        }


    }

};



SpriteSheetFace.prototype.loadState = function (state) {

    if(!state)return;
    try {
        console.log("load state",state);
        if(state.animation!=this.animation) {
            this.setAnimation(this.getAnimationByName(state.animation), true);
            this.currentFrame = state.frame;
            this.slide_in = state.slide_in;
        }
    }
    catch(e)
    {
        console.log(e);
    }
};
SpriteSheetFace.prototype.onUnload =function()
{
    clearInterval(this.interval);
    this.socketListener.stop();
};

SpriteSheetFace.prototype.free = function () {
    this.onUnload();

};

module.exports = SpriteSheetFace;



