var VatomResource = require('./VatomResource');
var Events = require("../Events");
/** @class Represents an image resource attached to a vatom */
var VatomImageResource = function VatomImageResource(url) {
    VatomResource.call(this, url);
    //get size

    var _this = this;

    _this._width = 0;
    _this._height = 0;
};

// Subclass
VatomImageResource.prototype = Object.create(VatomResource.prototype);
VatomImageResource.prototype.constructor = VatomImageResource;

/** The image width */
Object.defineProperty(VatomImageResource.prototype, 'width', {
    get: function () {
        return this._width || 0;
    }
});

/** The image height */
Object.defineProperty(VatomImageResource.prototype, 'height', {
    get: function () {
        return this._height || 0;
    }
});

VatomImageResource.prototype.onLoadedData = function () {

    // Check if got image
    //console.log("Vatomic SDK: Image data loaded");
    if (this.data == null) {

        // Failed
        //console.error("Unable to decode image data for " + this.URL);

        // Notify change
        this.state = VatomResource.Error;
        return;

    }

    var _this = this;
    var temp = new Image();
    var wait = setInterval(function () {
        var w = temp.naturalWidth,
            h = temp.naturalHeight;
        if (w && h) {
            clearInterval(wait);
            _this._width = w;
            _this._height = h;
            temp.src = "";
            temp = null;
            // Events.callEvent("resourceLoaded",_this);
            _this.state = VatomResource.states.Loaded;
            //console.log("Image loaded...");

        }
    }, 30);
    temp.src = this.data;

};

VatomImageResource.prototype.getBlob = function (callback) {
    if (!callback && !this.state == VatomResource.states.Loaded)
        return false;

    var xhr = new XMLHttpRequest();
    xhr.open('GET', this.data, true);
    xhr.responseType = 'blob';
    xhr.onload = function (e) {
        if (this.status == 200) {

            callback(this.response);
            // myBlob is now the blob that the object URL pointed to.
        }
    };
    xhr.send();
    return true;


};
module.exports = VatomImageResource;



