/** Main starting point for using the Vatomic SDK. Here you can log in, etc. */

// Includes
var Events = require('./Events.js');
var VatomUser = require('./Users/VatomUser.js');
var VatomCurrentUser = require('./Users/VatomCurrentUser.js');
var Vatom = require('./Vatom.js');
var LocalStorage = require("./LocalStorage");
var VatomDiscoverFilter = require("./VatomDiscoverFilter.js");
var RequestHelper = require("./RequestHelper");
var Request = require("./Request");
var VatomResourceManager = require("./Resources/VatomResourceManager");
var VatomicInventory = require("./VatomicInventory");
var VatomicWebSocket = require("./VatomicWebSocket");
var VatomicStore = require("./VatomicStore");
var VatomView = require("../UI/VatomView");
var UUID =require("../Lib/UUID");
var jwtDecode = require("jwt-decode")

import Log from './Log'

/** @private Validates the stored JWT token and returns true if it's valid */
function validateJWT(token) {

	// Check if we have one
	if (!token)
		return

	// Remove "Bearer ". If it doesn't exist, the server has changed it's Authentication header format.
	if (token.substring(0, 7) == "Bearer ")
		token = token.substring(7)
	else
		return false

	// Decode token
	var info = jwtDecode(token)
	if (!info || !info.exp || !info.user_id)
		return false

	// Check if expired
	if (Date.now() > info.exp * 1000)
		return false

	// Our token is valid
	return true

}

/** @private Reads a property from the JWT token */
function getJWTProperty(token, propertyName) {

	// Check if we have one
	if (!token)
		return

	// Remove "Bearer ". If it doesn't exist, the server has changed it's Authentication header format.
	if (token.substring(0, 7) == "Bearer ")
		token = token.substring(7)
	else
		return null

	// Decode token
	var info = jwtDecode(token)
	return info && info[propertyName]

}

//var VatomicConsole = require("./VatomicConsole");

/** Vatomic singleton class. This is the first entry point into the Vatomic SDK.
 *
 * Events emitted:
 * - `vatom.received` : Emitted when the user receives a new vatom.
 *
 */
class Vatomic {

    // Initializer. You must call this on startup of your app.
    static init(config) {

        // Store app ID
        //console.log(config)
        VatomicStore.DEFAULTS.APP_ID = config.appID

        // Store configuration options
        if (config.serverAddress)           VatomicStore.DEFAULTS.SERVER_ADDRESS = config.serverAddress
        if (config.websocketAddress)        VatomicStore.DEFAULTS.WEBSOCKET_ADDRESS = config.websocketAddress
        //console.log(VatomicStore)

        // Validate our current token
		var token = LocalStorage.get("vatomic.token")
        if (validateJWT(token)) {

			// Token is valid, do login stuff
            Log.info("User", "JWT token is still valid, we are still logged in")
            Vatomic.onLogin()

		} else {

			// Token invalid, remove it
            Log.info("User", "JWT token has expired, removing it")
			LocalStorage.remove("vatomic.token")

		}

        // Add unauthorized listener
        Events.registerEvent("user.unauthorized", Vatomic.onLogout);

    }

    // Check if logged in
    static isLoggedIn() {
        return !!LocalStorage.get("vatomic.token");
    }

    // Check if logged in
    static get loggedIn() {
        return !!LocalStorage.get("vatomic.token");
    }

	/** Get current access token */
	static get accessToken() {
		return LocalStorage.get("vatomic.token") || ""
	}

	/** Get current user ID, pulled directly from the JWT token. */
	static get userID() {
		return getJWTProperty(LocalStorage.get("vatomic.token"), "user_id")
	}

	/** Switch accounts. Sets the specified access token as the current one. You can use `Vatomic.accessToken` to get the current token after logging in and store it, then use this to switch back to that account
	 *	without needing to get the user's credentials again. @returns True if token is valid. */
	static setAccessToken(token) {

		// Validate token
		if (!validateJWT(token))
			return false

		// Do logout stuff
		Vatomic.onLogout()

		// Set access token
		LocalStorage.set("vatomic.token", token)

		// Do login stuff
		Vatomic.onLogin()
		return true

	}

    /** Do guest login, using the device's Guest ID or a user-supplied one. @returns Promise */
    static loginGuest(guestID, onSuccess, onFail) {

        // Create payload
        var payload = {
            guest_id: guestID || Vatomic.guestID
        }

        // Send request
        return Request.post("/user/vatomic/login", payload).then(data => {

            // Success, guest ID exists and we logged in
            Vatomic.onLogin()
            VatomResourceManager.updateAssetProviders(data.asset_provider)
            onSuccess && onSuccess(data)
			return LocalStorage.get("vatomic.token")

        }).catch(err => {

            // Failed, check error
            console.log(err)
            if (err.code != "ACCOUNT_NOT_FOUND")
                throw err

            // Guest account didn't exist, register it
            return Request.post("/user/register", payload).then(data => {

                // Success, account registered
                Vatomic.onLogin()
                VatomResourceManager.updateAssetProviders(data.asset_provider)
                onSuccess && onSuccess(data)

            })

        }).catch(err => {

            // Failed
            onFail && onFail(err.code, err.message)
            throw err

        })

    }

    // Do login via username and password. The username can be an email or a phone number.
    static login(username, password, onSuccess, onFail) {

        // Create payload
        var payload = {
            password: password
        }

        // Check if email or phone was passed in
        if (username.indexOf("@") != -1)
            payload.email = username
        else
            payload.phone_number = username

        // Send request
        return Request.send("POST", "/user/vatomic/login", null, payload, onSuccess, onFail).then(data => {

            // Logged in successfully
            VatomResourceManager.updateAssetProviders(data.asset_provider)
            Vatomic.onLogin()
			return LocalStorage.get("vatomic.token")

        })

    }

    // Do login via OAuth token from a service
    static loginOauth(service, oauthToken, onSuccess, onFail) {

        // Create payload
        var payload = {
            oauth_provider: service,
            token: oauthToken
        }

        // Send request
        return Request.send("POST", "/user/oauth/login", null, payload, onSuccess, onFail).then(data => {

            // Logged in successfully
            VatomResourceManager.updateAssetProviders(data.asset_provider)
            Vatomic.onLogin()
			return LocalStorage.get("vatomic.token")

        })

    }

    /** @private Called on login */
    static onLogin() {

        // Open websocket connection
        VatomicWebSocket.open()

        // Create current user
        VatomicStore['user'] = new VatomCurrentUser()

        // Create user's inventory
        VatomicStore['inventory'] = new VatomicInventory()

        Vatomic.inventory.refresh()


        // Send notification
        Events.callEvent("vatomic.login-complete");
        Events.callEvent("vatomic.reset.all");


    }

    // TODO: Why is this here?
    static getVatomInventory(vatom)
    {
       return new VatomicInventory(function (onSuccess, onFail, token, pageCount) {

            RequestHelper.getInventory(vatom?vatom.identifier:'', token, pageCount, function (data) {

                var vatoms = [];
                for (var i = 0; i < data.length; i++) {
                    var vatom = new Vatom(data[i]);
                    if (vatom.faces.length > 0)//filter out vatoms with no faces e.g coin wallet
                    {
                        vatoms.push(vatom);
                    }
                }
                onSuccess(vatoms);

            }, onFail);

        }.bind(this));
    }

    /** @private Called on logout */
    static onLogout() {

        // Store logged out status
        LocalStorage.remove("vatomic.token");

        // Close the web socket connection
        VatomicWebSocket.close();

        // Remove current user
        VatomicStore['user'] = null;

        // Remove inventory
        VatomicStore['inventory'] = null;

        VatomResourceManager.clearCachedResources();

    }

    // Do logout
    static logout(onSuccess, onFail) {

        // Check if logged out already
        /* if (Vatomic.isLoggedIn) {
         return;
         }*/
        RequestHelper.logout(onSuccess, onFail);
        // Send request
        Vatomic.onLogout();
        // Send notification
        Events.callEvent("vatomic.logout-complete");
    }

    /** Register a new account. @returns Promise. */
    static register(info, onSuccess, onFail) {

        // Send request
        return Request.send("POST", "/user/register", null, info, onSuccess, onFail)

    }

    // Verify user account
    static verifyPhone(phone, token, onSuccess, onFail) {

		// Create payload
        var payload = {
            phone_number: phone,
            token: token
        }

        // Send request
        return Request.send("POST", "/user/verify", null, payload, onSuccess, onFail)

    }

    // Verify user account
    static verifyEmail(email, token, onSuccess, onFail) {

		// Create payload
        var payload = {
            email: email,
            token: token
        }

        // Send request
        return Request.send("POST", "/user/verify", null, payload, onSuccess, onFail)

    }

	/** Resend the verification code. @returns Promise. */
	static resendVerifyCode(emailOrPhone) {

		// Create payload
        var payload = {}

        // Check if email or phone was passed in
        if (emailOrPhone.indexOf("@") != -1)
            payload.email = emailOrPhone
        else
            payload.phone_number = emailOrPhone

        // Send request
        return Request.post("/user/verify/resend", payload)

	}

	/** Sends a new password to the user's address. @returns Promise. */
	static resetPassword(emailOrPhone) {

		// Create payload
        var payload = {}

        // Check if email or phone was passed in
        if (emailOrPhone.indexOf("@") != -1)
            payload.email = emailOrPhone
        else
            payload.phone_number = emailOrPhone

        // Send request
        return Request.post("/user/resetpwd", payload)

	}

    // @deprecated
    static updateProfile(payload, onSuccess, onFail) {
        console.warn("DEPRECATED: Use the promise-based Vatomic.user.updateProfile instead");

        if (Vatomic.user) {
            Vatomic.user.updateProfile(payload).then(onSuccess).catch(onFail);
        }
        else {
            if (onFail) {
                onFail();
            }
        }
    }

    // TODO: This should be in Vatomic.user.*
    static updateProfileAvatar(form, onSuccess, onFail) {

        if (Vatomic.user) {
            Vatomic.user.updateAvatar(form, onSuccess, onFail);
        }
        else {
            if (onFail) {
                onFail();
            }
        }
    }

    // TODO: Check if anything uses this and REMOVE it. This should be vatom.send() instead
    static tranfserVatom(vatom, to, onSuccess, onFail) {
        var vatomid, actions=[{action:"Transfer"}]
        if (vatom instanceof Vatom) {
            vatomid = vatom.identifier;
            actions = vatom.transferActions||actions;
        }
        else
            vatomid = vatom;



       // Events.callEvent("vatom.transfer-start", {identifier: vatomid});
        RequestHelper.transferVatomActions(vatomid, to,actions, function () {

           // Events.callEvent("vatom.transfer-complete", {identifier: vatomid});
            if (onSuccess) {
                onSuccess();
            }
        }, function (code, message) {
           // Events.callEvent("vatom.transfer-failed", {identifier: vatomid});
            if (onFail) {
                onFail(code, message);
            }
        });
    }

    // TODO: Use vatom.send() instead. Remove this.
    static tranfserVatomEmail(vatom, email) {

        var to = {email: email};
        Vatomic.tranfserVatom(vatom, to);
    }

	/** Registers a new native face with the SDK. The handler is a function which takes a VatomView instance, a face properties object, and a callback. The callback should be called with the new Face object instance.
	 *  The Face object should conform to or extend the Face class. */
	static registerFace(faceURL, handler) {
		VatomView.NativeFaces[faceURL] = handler;
	}

    /** Fetches a unique identifier for the current user or device. */
    static get guestID() {

        var guestId = LocalStorage.get("guestId");

        if (!guestId) {
            guestId = UUID.generate();
            LocalStorage.set("guestId", guestId);
        }

        return guestId;

    }

    /** If the user is logged in, gets their main inventory as a VatomInventory object. */
    static get inventory() {
        return VatomicStore['inventory'];
    }

    /** If the user is logged in, this will contain an instance of VatomCurrentUser. */
    static get user() {
        return VatomicStore['user'];
    }

};

// Register built-in native faces
Vatomic.registerFace("native://image", 					(vatomView, faceProps, cb) => require(["../UI/NativeFaces/ImageFace"], 			Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://coin-renderer", 			(vatomView, faceProps, cb) => require(["../UI/NativeFaces/CoinFace"], 			Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://progress-image-overlay", (vatomView, faceProps, cb) => require(["../UI/NativeFaces/ProgressImageFace"], 	Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://layered-image", 			(vatomView, faceProps, cb) => require(["../UI/NativeFaces/LayeredImageFace"], 	Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://folder", 				(vatomView, faceProps, cb) => require(["../UI/NativeFaces/FolderFace"], 		Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://generic-3d-folder", 		(vatomView, faceProps, cb) => require(["../UI/NativeFaces/V3DFolderFace"], 		Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://generic-3d", 			(vatomView, faceProps, cb) => require(["../UI/NativeFaces/V3DFace"], 			Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://sprite-sheet", 			(vatomView, faceProps, cb) => require(["../UI/NativeFaces/SpriteSheetFace"], 	Face => cb(new Face(vatomView, faceProps))));
Vatomic.registerFace("native://script-renderer", 		(vatomView, faceProps, cb) => require(["../UI/NativeFaces/ScriptFace"],			Face => cb(new Face(vatomView, faceProps))));

module.exports = Vatomic;



