// Includes
var VatomUser = require("./VatomUser.js");
var Request = require("../Request");
var VatomicStore = require("../VatomicStore");

/** @class VatomCurrentUser defines the currently logged-in user. */
var VatomCurrentUser = function() {
	VatomUser.call(this);

	// Setup vars
	this.isCurrentUser = true;

	// Fetch profile as soon as we are created
	this.fetchProfile();

};

// Subclass
VatomCurrentUser.prototype = Object.create(VatomUser.prototype);
VatomCurrentUser.prototype.constructor = VatomCurrentUser;

/** @private Updates the current user's profile data. Returns a Promise. */
VatomCurrentUser.prototype.updateProfile = function(payload) {

	// Send request
	return Request.patch("/currentuser", payload).then(data => {

		// Trigger another profile fetch, just in case the data ended up being different
		this.fetchProfile();

	});

};

/** Update the current user's avatar image. Returns a promise. */
VatomCurrentUser.prototype.updateAvatar = function(img) {

    // Check object type
    var form = null;
    if (img instanceof FormData) {

        // Already in form format
        form = img;

    } else if (img instanceof File || img instanceof Blob) {

        // Can pass as a file to the form
        form = new FormData();
        form.append("avatar", img);

    } else if (img instanceof ArrayBuffer) {

        // Convert to Blob and add to FormData
        form = new FormData();
        form.append("avatar", new Blob([img], {type: "image/jpeg"}));

    } else {

        // Unknown format
        return Promise.reject({ code: "unknown_object_type", message: "An unknown object type was passed to updateAvatar. Please pass a File, Blob or FormData." });

    }

	return Request.post('/currentuser/avatar', form).then(data => {

    	// Trigger another profile fetch, just in case the data ended up being different
    	this.fetchProfile();

    });

};

/** Update the user's name. Returns a Promise. */
VatomCurrentUser.prototype.updateName = function(firstName, lastName) {

	// Store old values
	var oldFirstName = this.firstName;
	var oldLastName = this.lastName;

	// Update to new values
	this.firstName = firstName;
	this.lastName = lastName;

	// Create payload
	var payload = {
		first_name: firstName,
		last_name: lastName
	};

	// Send update
	return this.updateProfile(payload).catch(err => {

		// Failed, put old values back
		this.firstName = oldFirstName;
		this.lastName = oldLastName;

        // Re-throw error
        throw err;

	});

}

/** Update the user's phone number. Returns a Promise. */
VatomCurrentUser.prototype.updatePhoneNumber = function(phoneNumber) {

	// Store old values
	var oldPhoneNumber = this.phoneNumber;

	// Update to new values
	this.phoneNumber = phoneNumber;

	// Create payload
	var payload = {
		phone_number: phoneNumber
	}

	// Send update
	return this.updateProfile(payload).catch(err => {

		// Failed, put old values back
		this.phoneNumber = oldPhoneNumber;

        // Re-throw error
        throw err;

	});

}

/** Update the user's email address. Returns a Promise. */
VatomCurrentUser.prototype.updateEmail = function(email) {

	// Store old values
	var oldEmail = this.email;

	// Update to new values
	this.email = email;

	// Create payload
	var payload = {
		email: email
	}

	// Send update
	return this.updateProfile(payload).catch(err => {

		// Failed, put old values back
		this.email = oldEmail;

        // Re-throw error
        throw err;

	});

}

/** Update the user's password. Returns a Promise. */
VatomCurrentUser.prototype.updatePassword = function(password) {

	// Create payload
	var payload = {
		password: password
	}

	// Send update
	return this.updateProfile(payload);

}

/** Link the specified Facebook ID to the user's profile. Returns a Promise. */
VatomCurrentUser.prototype.linkFacebook = function(token) {

	// Store old values
	var oldFacebookLinked = this.isFacebookLinked;

	// Update to new values
	this.isFacebookLinked = !!token;

	// Create payload
	var payload = {
		oauth: {
			provider: "Facebook",
			id: token || "."
		}
	}

	// Send update
	return this.updateProfile(payload).catch(err => {

		// Failed, put old values back
		this.isFacebookLinked = oldFacebookLinked;

        // Re-throw error
        throw err;

	});

};

/** Unlinks Facebook from the user's profile. Returns a Promise. */
VatomCurrentUser.prototype.unlinkFacebook = function() {

	// Link to null token
	return this.linkFacebook(null);

}

/** @private On update, store our userID globally */
VatomCurrentUser.prototype.onUpdated = function() {
	VatomUser.prototype.onUpdated.call(this);

	// Store our ID
	if (this.userID)
		VatomicStore.currentUserID = this.userID;

}

module.exports = VatomCurrentUser;



